import { formatDate } from '../utils/date'

export const getTestingStatusClassByStatus = (testingStatus) => {
    switch (testingStatus) {
        case "Created":
            return "testings__item--disable"
        case "Finished":
            return "testings__item--finished"
        case "Checked":
            return "testings__item--passed"
        default:
            return " "
    }
}

export const getTestingFormatDate = (testingDate) => {
    let res = ''
    let dateNow = new Date()
    let dateTimeTesting = new Date(testingDate)

    if (dateNow.getDate() == dateTimeTesting.getDate() && dateNow.getMonth() == dateTimeTesting.getMonth()) {
        let minutes = dateTimeTesting.getMinutes()
        res = `Сегодня в ${dateTimeTesting.getHours()}:${minutes > 9 ? minutes : "0" + minutes}`
    } else {
        dateNow.setDate(dateNow.getDate() + 1)
        if (dateNow.getDate() == dateTimeTesting.getDate() && dateNow.getMonth() == dateTimeTesting.getMonth()) {
            let minutes = dateTimeTesting.getMinutes()
            res = `Завтра в ${dateTimeTesting.getHours()}:${minutes > 9 ? minutes : "0" + minutes}`
        } else {
            res = formatDate(new Date(testingDate))
        }
    }

    return res;
}