import { useEffect, useState } from "react"
import Button from "../Button/Button"
import clsx from "clsx"
import "./RadioGroup.css"


const RadioGroup = ({ name, variants, onChange, value = null, showIsTrueAnswer = false, isClearable = true }) => {
  const [checkedValue, setCheckedValue] = useState(value)
  const [isMounted, setIsMounted] = useState(true)

  useEffect(() => {
    if (!isMounted) {
      onChange && onChange(checkedValue)
    }
    setIsMounted(false)
  }, [checkedValue])

  return (
    <>
      <div className="radio">
        <ul className="radio__list">
          {
            variants?.map((variantItem, index) => (
              <li className={clsx("radio__item", "radio-item", {
                _checked: checkedValue?.textAnswerVariant == variantItem.textAnswerVariant,
                _success: showIsTrueAnswer && (checkedValue?.textAnswerVariant == variantItem.textAnswerVariant && variantItem.isTrueVariant == true),
                _failed: showIsTrueAnswer && (checkedValue?.textAnswerVariant == variantItem.textAnswerVariant && variantItem.isTrueVariant == false)
              })} key={`${name}-${index+1}`}>
                <input className="radio-item__input" type="radio" name={name} id={`${name}-${index+1}`} value={variantItem.textAnswerVariant} onClick={() => setCheckedValue(variantItem)} />
                <label className="radio-item__label" htmlFor={`${name}-${index+1}`}>
                  <span className="radio-item__text" dangerouslySetInnerHTML={{ __html: variantItem.textAnswerVariant}}></span>
                  {showIsTrueAnswer && <div className="radio-item__icon"></div>}
                </label>
              </li>
            ))
          }

        </ul>
      </div>
      {isClearable && <Button className="radio__clear-btn" onClick={() => {
        const selectedRadio = document.querySelector(`input[name="${name}"]:checked`);
        if (selectedRadio) selectedRadio.checked = false;
        setCheckedValue(null);
      }}>Очистить мой выбор</Button>}
    </>
  )
}

export default RadioGroup