import { Case, Switch } from '../SwitchCase'
import { getTestingFormatDate, getTestingStatusClassByStatus } from '../../utils/testing'
import clsx from 'clsx'
import { Link } from 'react-router-dom'

const TestingItemForTeacher = ({ testing }) => {

    return (
        <li className={clsx("testings__item", "testings-item", getTestingStatusClassByStatus(testing.testingStatus))}>
            <span className='testings-item__date'>{getTestingFormatDate(testing.testingDate)}</span>
            <div className='testings-item__data data'>
                <span className='data__stage'>{`${testing.course} курс${testing.nGroup ? ` ${testing.nGroup} группа` : ""}`}</span>
                <span className='data__department'>{testing.department}</span>
            </div>
            <Switch param={getTestingStatusClassByStatus(testing.testingStatus)}>
                <Case value="testings__item--disable">
                    <div className='discipline-btn'>{testing.disciplineName}</div>
                </Case>
                <Case value={[" ", "testings__item--finished"]}>
                    <Link to={`/teacher/testing/${testing.testingId}`} state={{ course: testing.course, group: testing.nGroup, deptName: testing.department, disciplineName: testing.disciplineName }} className='discipline-btn'>{testing.disciplineName}</Link>
                </Case>
                <Case value="testings__item--passed">
                    <Link to={`/testing-results/${testing.testingId}`} state={{ course: testing.course, group: testing.nGroup, deptName: testing.department, disciplineName: testing.disciplineName }} className='discipline-btn'>{testing.disciplineName}</Link>
                </Case>
            </Switch>
        </li>
    )
}

export default TestingItemForTeacher