import { useEffect, useState, useRef } from 'react';
import "./TextArea.css"

const TextArea = ({ className, heightByContent = false, onChange, value, ...props}) => {
    const textAreaRef = useRef(null)
    const [text, setText] = useState(value)
    const [isMounted, setIsMounted] = useState(true)

    useEffect(() => {
        if (!isMounted) {         
            onChange(text || null)
        } else {
            if (heightByContent) {
                textAreaRef.current.style.height = textAreaRef.current.scrollHeight + "px"
            }
        }
        setIsMounted(false)
    }, [text])

    const defaultClassName = 'textarea'
    const classNames = className ? className + ` ${defaultClassName}` : defaultClassName

    return (
        <textarea value={text} ref={textAreaRef} className={classNames} onChange={e => setText(e.target.value)} {...props} /> 
    );
};

export default TextArea;
