import clsx from "clsx";
import "./Checkbox.css";

const Checkbox = ({ id, className, name, label, value, onChange, children, checked, ...props }) => {
  return (
    <div
      className={clsx(className, "checkbox", {
        _custom: !!children,
      })}
    >
      <input
        checked={checked}
        name={name}
        onChange={onChange}
        type="checkbox"
        value={value}
        className="checkbox__input"
        id={id}
        {...props}
      />
      {(label || children) && (
        <label htmlFor={id} className="checkbox__label">
          {label || children}
        </label>
      )}
    </div>
  );
};

export default Checkbox;
