import { useContext, useState, useEffect } from 'react'
import RadioGroup from '../../components/ui/RadioGroup/RadioGroup'
import { Switch, Case } from '../../components/SwitchCase'
import TextArea from '../../components/ui/TextArea/TextArea'
import { SortableList } from '../../components/ui/SortableList/SortableList'
import Input from '../../components/ui/Input/Input'
import CheckBoxGroup from '../../components/ui/CheckboxGroup/CheckBoxGroup'
import { AppContext } from '../../context'
import Accordance from '../../components/ui/Accordance/Accordance'
import Button from '../../components/ui/Button/Button'
import Countdown from '../../components/ui/Countdown/Countdown'
import { useLocation, useNavigate } from 'react-router-dom'
import AnswerBlankService from '../../api/AnswerBlankService'
import { useFetching } from "../../hooks/useFetching"
import Loader from '../../components/ui/Loader/Loader'
import AnchorLink from 'react-anchor-link-smooth-scroll'
import Popup from '../../components/ui/Popup/Popup'
import Layout from '../../components/Layout'

const Testing = () => {
  const location = useLocation()
  const { answerBlankId, disciplineName } = location.state

  const redirect = useNavigate()

  const { showToast } = useContext(AppContext)

  const [testingData, setTestingData] = useState(null)
  const [answers, setAnswers] = useState([])
  const [timeToEndInSeconds, setTimeToEndInSeconds] = useState(3600)
  const [modalEndActive, setModalEndActive] = useState(false)

  const [getAnswerBlankByIdForStudent, isAnswerBlankLoading] = useFetching(async (id) => {
    const response = await AnswerBlankService.getAnswerBlankByIdForStudent(id)
    if (response.status == 200) {
      setTestingData(response.data)
      setAnswers(response.data.answers)
      setTimeToEndInSeconds(response.data.timeToEndInSeconds)
    }
  })

  const [saveAnswers, isSaveLoading] = useFetching(async (answerBlankId, updateAnswers) => {
    const response = await AnswerBlankService.updateAnswerBlank(answerBlankId, updateAnswers)
    if (response.status == 200) {
      showToast("success", "Ответы сохранены!")
      getAnswerBlankByIdForStudent(testingData?.id)
    }
  })

  const [endTesting, isEndLoading] = useFetching(async (answerBlankId, updateAnswers) => {
    const response = await AnswerBlankService.endTestingForStudent(answerBlankId, updateAnswers)
    if (response.status == 200) {
      showToast("success", "Ответы сохранены!")
      showToast("success", "Тестирование успешно завершено!")
      redirect(`/testings/${testingData?.studentId}`)
    } else {
      showToast("error", `Статус ${response.status}`, "Ошибка при завершении тестирования!")
    }
  })

  const onChangeAnswerText = (answerText, questionId) => {
    const answerByQuestionId = answers?.find(x => x.questionId == questionId)

    if (answerByQuestionId) {
      setAnswers(prevState =>
        prevState.map(item =>
          item.questionId === questionId
            ? { ...item, text: answerText }
            : item
        )
      )

      setAnswers(prevState =>
        prevState.filter(item => item.text != null || item.studentAnswerJustification != null || item.studentAnswerVariants.length > 0)
      )
    } else {
      setAnswers([...answers, {
        text: answerText,
        studentAnswerJustification: null,
        answerBlankId: testingData?.id,
        questionId,
        studentAnswerVariants: [],
      }])
    }
  }

  const onChangeJustification = (justification, questionId) => {
    const answerByQuestionId = answers.find(x => x.questionId == questionId)

    if (answerByQuestionId) {
      setAnswers(prevState =>
        prevState.map(item =>
          item.questionId === questionId
            ? { ...item, studentAnswerJustification: justification }
            : item
        )
      )

      setAnswers(prevState =>
        prevState.filter(item => item.text != null || item.studentAnswerJustification != null || item.studentAnswerVariants.length > 0)
      )
    } else {
      setAnswers([...answers, {
        text: null,
        studentAnswerJustification: justification,
        answerBlankId: testingData?.id,
        questionId,
        studentAnswerVariants: [],
      }])
    }
  }

  const onChangeAnswerVariants = (answerVariants, questionId) => {
    const answerByQuestionId = answers.find(x => x.questionId == questionId)

    if (answerByQuestionId) {
      setAnswers(prevState =>
        prevState.map(item =>
          item.questionId === questionId
            ? { ...item, studentAnswerVariants: answerVariants }
            : item
        )
      )

      setAnswers(prevState =>
        prevState.filter(item => item.text != null || item.studentAnswerJustification != null || item.studentAnswerVariants.length > 0)
      )
    } else {
      setAnswers([...answers, {
        text: null,
        studentAnswerJustification: null,
        answerBlankId: testingData?.id,
        questionId,
        studentAnswerVariants: answerVariants,
      }])
    }
  }

  const getDefaultVariants = (questionId) => {
    const qVariants = testingData?.ticket.questions.find(x => x.id == questionId).answerVariants
    const answerVariants = answers?.find(x => x.questionId == questionId)?.studentAnswerVariants
    const res = []

    if (answerVariants) {
      for (let i = 0; i < answerVariants.length; i++) {
        let qVar = { ...qVariants.find(x => x.id == answerVariants[i].answerVariantId) }
        qVar.numberAnswerVariant = answerVariants[i].numberAnswerVariant ? answerVariants[i].numberAnswerVariant : 0
        res.push(qVar)
      }
    }

    return res
  }

  const getSortableListData = (initialData, choosenVariants) => {
    const res = []

    if (choosenVariants) {
      for (let i = 0; i < choosenVariants.length; i++) {
        const variantFromInitialData = initialData.find(x => x.id == choosenVariants[i].answerVariantId)
        res.push(variantFromInitialData)
      }
      return res
    } else {
      return initialData
    }
  }

  const handleChangeRadioGroup = (checkedValue, questionId) => {
    const res = []

    if (checkedValue) {
      const answerEntity = answers?.find(x => x.questionId == questionId)
      const newItem = {
        answerVariantId: checkedValue?.id,
      }
      if (answerEntity && answerEntity.studentAnswerVariants.length > 0 && answerEntity.studentAnswerVariants[0]?.id) {
        newItem.id = answerEntity.studentAnswerVariants[0]?.id
        newItem.answerId = answerEntity.studentAnswerVariants[0]?.answerId
      } else {
        newItem.id = "00000000-0000-0000-0000-000000000000"
        newItem.answerId = "00000000-0000-0000-0000-000000000000"
      }
      res.push(newItem)
    }
    onChangeAnswerVariants(res, questionId)
  }

  const handleChangeCheckboxGroup = (checkedValues, questionId) => {
    const res = []

    const answerEntity = answers?.find(x => x.questionId == questionId)
    for (let i = 0; i < checkedValues.length; i++) {
      const newItem = {
        answerVariantId: checkedValues[i].id,
      }
      if (answerEntity) {
        if (answerEntity.studentAnswerVariants.length > 0 && answerEntity.studentAnswerVariants[i]?.id) {
          newItem.id = answerEntity.studentAnswerVariants[i]?.id
          newItem.answerId = answerEntity.studentAnswerVariants[i]?.answerId
        } else {
          newItem.id = "00000000-0000-0000-0000-000000000000"
          newItem.answerId = "00000000-0000-0000-0000-000000000000"
        }
      } else {
        newItem.id = "00000000-0000-0000-0000-000000000000"
        newItem.answerId = "00000000-0000-0000-0000-000000000000"
      }
      res.push(newItem)
    }
    onChangeAnswerVariants(res, questionId)
  }

  const handleChangeSortableList = (updatedList, questionId) => {
    const res = []

    const answerEntity = answers?.find(x => x.questionId == questionId)
    for (let i = 0; i < updatedList.length; i++) {
      const newItem = {
        answerVariantId: updatedList[i].id,
        numberAnswerVariant: i + 1
      }
      if (answerEntity) {
        newItem.id = "00000000-0000-0000-0000-000000000000"
        if (answerEntity.studentAnswerVariants && answerEntity.studentAnswerVariants[i]?.id) {
          newItem.id = answerEntity.studentAnswerVariants[i]?.id
        }
        newItem.answerId = answerEntity.id || "00000000-0000-0000-0000-000000000000"
      }
      res.push(newItem)
    }

    onChangeAnswerVariants(res, questionId)
  }

  const handleChangeAccordance = (updatedList, questionId) => {
    const res = []

    const answerEntity = answers?.find(x => x.questionId == questionId)
    if (updatedList) {
      for (let i = 0; i < updatedList.length; i++) {
        const newItem = {
          answerVariantId: updatedList[i].id,
          numberAnswerVariant: updatedList[i].numberAnswerVariant
        }
        if (answerEntity) {
          newItem.id = "00000000-0000-0000-0000-000000000000"
          if (answerEntity.studentAnswerVariants && answerEntity.studentAnswerVariants[i]?.id) {
            newItem.id = answerEntity.studentAnswerVariants[i]?.id
          }
          newItem.answerId = answerEntity.id || "00000000-0000-0000-0000-000000000000"
        }
        res.push(newItem)
      }
    }

    onChangeAnswerVariants(res, questionId)
  }

  const getQuestionsByBlockNumber = (blockNumber) => {
    const questionTypeNamesForBlockOne = ["№вопрос2", "№вопрос3", "№вопрос4"]
    const questionTypeNamesForBlockTwo = ["№вопрос1", "№вопрос5"]
    const questionTypeNamesForBlockThree = ["№вопроскейс"]

    let res = []

    if (blockNumber == 1) {
      for (let questionTypeName of questionTypeNamesForBlockOne) {
        const questionsByType = testingData?.ticket.questions.filter(x => x.questionType.questionTypeName == questionTypeName)
        if (questionsByType) {
          res = [...res, ...questionsByType]
        }
      }
    } else if (blockNumber == 2) {
      for (let questionTypeName of questionTypeNamesForBlockTwo) {
        const questionsByType = testingData?.ticket.questions.filter(x => x.questionType.questionTypeName == questionTypeName)
        if (questionsByType) {
          res = [...res, ...questionsByType]
        }
      }
    } else {
      for (let questionTypeName of questionTypeNamesForBlockThree) {
        const questionsByType = testingData?.ticket.questions.filter(x => x.questionType.questionTypeName == questionTypeName)
        if (questionsByType) {
          res = [...res, ...questionsByType]
        }
      }
    }

    return res
  }

  useEffect(() => {
    getAnswerBlankByIdForStudent(answerBlankId)
  }, [])

  return (
    <Layout>
      <div className="testing">
        <div className='testing__container container'>
          {
            isAnswerBlankLoading
              ? <Loader text="Загрузка" />
              :
              <>
                <div className="testing__head">
                  <h1 className="testing__title title">{disciplineName}</h1>
                </div>
                <div className="testing__wrapper">
                  <div className="testing__questions questions">
                    <div className="questions__wrapper">
                      <h2 className="questions__wrapper-title title">Блок 1</h2>
                      <ul className='questions__list'>
                        {
                          getQuestionsByBlockNumber(1)
                            .map((question, index) => (
                              <li key={index} className='questions__item questions-item' id={`question-1-${index + 1}`}>
                                <div className='questions-item__info'>
                                  <span className='questions-item__number'>Вопрос №{index + 1}</span>
                                  <span className='questions-item__score'>Макс. балл: {question.numberOfPoints}</span>
                                </div>
                                <div className="questions-item__body">
                                  <div className="questions-item__text" dangerouslySetInnerHTML={{ __html: question.questionText.replace("align=left", "") }}></div>
                                  <span className='questions-item__descr'>{question.questionType.description}</span>
                                  <div className="questions-item__answer-variants answer-variants">
                                    <Switch param={question.questionType.questionTypeName}>
                                      <Case value="№вопрос2">
                                        <CheckBoxGroup value={getDefaultVariants(question.id)} onChange={(checkedValues) => handleChangeCheckboxGroup(checkedValues, question.id)} name={`question-1-${index + 1}`} variants={question.answerVariants} />
                                      </Case>
                                      <Case value="№вопрос3">
                                        <SortableList
                                          items={getSortableListData(question.answerVariants, answers?.find(x => x.questionId == question.id)?.studentAnswerVariants)}
                                          onChange={(updatedList) => handleChangeSortableList(updatedList, question.id)}
                                          renderItem={(variant) => (
                                            <SortableList.Item id={variant.id}>
                                              <SortableList.DragHandle />
                                              <div dangerouslySetInnerHTML={{ __html: variant.textAnswerVariant }}></div>
                                            </SortableList.Item>
                                          )} />
                                      </Case>
                                      <Case value="№вопрос4">
                                        <Accordance defaultValues={getDefaultVariants(question.id)} answers={question.answerVariants} onChange={(data) => handleChangeAccordance(data, question.id)} />
                                      </Case>
                                    </Switch>
                                  </div>
                                </div>
                              </li>
                            ))
                        }
                      </ul>
                    </div>
                    <div className="questions__wrapper">
                      <h2 className="questions__wrapper-title title">Блок 2</h2>
                      <ul className='questions__list'>
                        {
                          getQuestionsByBlockNumber(2)
                            .map((question, index) => (
                              <li key={index} className='questions__item questions-item' id={`question-2-${index + 1}`}>
                                <div className='questions-item__info'>
                                  <span className='questions-item__number'>Вопрос №{index + 1}</span>
                                  <span className='questions-item__score'>Макс. балл: {question.numberOfPoints}</span>
                                </div>
                                <div className="questions-item__body">
                                  <div className="questions-item__text" dangerouslySetInnerHTML={{ __html: question.questionText.replace("align=left", "") }}></div>
                                  <span className='questions-item__descr'>{question.questionType.description}</span>
                                  <div className="questions-item__answer-variants answer-variants">
                                    <Switch param={question.questionType.questionTypeName}>
                                      <Case value="№вопрос1">
                                        <RadioGroup value={getDefaultVariants(question.id)[0]} onChange={(checkedValue) => handleChangeRadioGroup(checkedValue, question.id)} name={`question-2-${index + 1}`} variants={question.answerVariants} />
                                        <TextArea value={answers?.find(x => x.questionId == question.id)?.studentAnswerJustification} onPaste={(evt) => evt.preventDefault()} onChange={(justification) => onChangeJustification(justification, question.id)} className='answer-variants__justification' />
                                      </Case>
                                      <Case value="№вопрос5">
                                        Ответ: <Input value={answers?.find(x => x.questionId == question.id)?.text} onPaste={(evt) => evt.preventDefault()} onChange={(answer) => onChangeAnswerText(answer, question.id)} />
                                        Обоснование: <TextArea value={answers?.find(x => x.questionId == question.id)?.studentAnswerJustification} onPaste={(evt) => evt.preventDefault()} onChange={(justification) => onChangeJustification(justification, question.id)} className='answer-variants__justification' />
                                      </Case>
                                    </Switch>
                                  </div>
                                </div>
                              </li>
                            ))
                        }
                      </ul>
                    </div>
                    <div className="questions__wrapper">
                      <h2 className="questions__wrapper-title title">Блок 3</h2>
                      <ul className='questions__list'>
                        {
                        getQuestionsByBlockNumber(3).map((question, index) => (
                            <li key={index} className='questions__item questions-item' id={`question-3-${index + 1}`}>
                              <div className='questions-item__info'>
                                <span className='questions-item__number'>Вопрос №{index + 1}</span>
                                <span className='questions-item__score'>Макс. балл: {question.numberOfPoints}</span>
                              </div>
                              <div className="questions-item__body">
                                <div className="questions-item__text" dangerouslySetInnerHTML={{ __html: question.questionText.replace("align=left", "") }}></div>
                                <span className='questions-item__descr'>{question.questionType.description}</span>
                                <div className="questions-item__answer-variants answer-variants">
                                  <TextArea value={answers?.find(x => x.questionId == question.id)?.text} onPaste={(evt) => evt.preventDefault()} onChange={(text) => onChangeAnswerText(text, question.id)} />
                                </div>
                              </div>
                            </li>
                          ))
                        }
                      </ul>
                    </div>
                  </div>
                  <div className='testing__navigation navigation'>
                    <div className="navigation__body">
                      <h2 className='navigation__title'>Навигация по тесту</h2>
                      <div className='navigation__blocks'>
                        <div className="navigation__block">
                          <h3 className="navigation__block-title">Блок 1</h3>
                          <ul className='navigation__list'>
                            {
                              getQuestionsByBlockNumber(1).map((question, index) => (
                                <li key={question.id} className='navigation__item'>
                                  <AnchorLink href={`#question-1-${index + 1}`} className='navigation__link'>
                                    {index + 1}
                                  </AnchorLink>
                                </li>
                              ))
                            }
                          </ul>
                        </div>
                        <div className="navigation__block">
                          <h3 className="navigation__block-title">Блок 2</h3>
                          <ul className='navigation__list'>
                            {
                              getQuestionsByBlockNumber(2).map((question, index) => (
                                <li key={question.id} className='navigation__item'>
                                  <AnchorLink href={`#question-2-${index + 1}`} className='navigation__link'>
                                    {index + 1}
                                  </AnchorLink>
                                </li>
                              ))
                            }
                          </ul>
                        </div>
                        <div className="navigation__block">
                          <h3 className="navigation__block-title">Блок 3</h3>
                          <ul className='navigation__list'>
                            {
                              getQuestionsByBlockNumber(3).map((question, index) => (
                                <li key={question.id} className='navigation__item'>
                                  <AnchorLink href={`#question-3-${index + 1}`} className='navigation__link'>
                                    {index + 1}
                                  </AnchorLink>
                                </li>
                              ))
                            }
                          </ul>
                        </div>
                      </div>
                      <Countdown onTimeOver={() => { showToast("warning", "Время экзамена истекло!"); endTesting(testingData?.id, answers) }} seconds={timeToEndInSeconds} />
                      <Button isDisabled={isSaveLoading} className='testing__btn' onClick={() => saveAnswers(testingData?.id, answers)}>
                        {isSaveLoading ? <Loader text="Сохранение" /> : "Сохранить ответы"}
                      </Button>
                    </div>
                  </div>
                </div>
                <div className="testing__bottom">
                  <Button className='testing__btn' onClick={() => setModalEndActive(true)}>Завершить экзамен</Button>
                </div>
                <Popup isOpen={modalEndActive} onClose={() => setModalEndActive(false)}>
                  <h2 className="popup__title title">Вы действительно хотите завершить тестирование?</h2>
                  <div className="confirm-buttons">
                    <Button isDisabled={isEndLoading} onClick={() => endTesting(testingData?.id, answers)}>
                      {isEndLoading ? <Loader /> : "Да"}
                    </Button>
                    <Button onClick={() => setModalEndActive(false)}>Нет</Button>
                  </div>
                </Popup>
              </>
          }
        </div>
      </div>
    </Layout>
  )
}

export default Testing