import { useEffect, useState } from "react"
import RoleService from "../../api/RoleService"
import { useFetching } from "../../hooks/useFetching"
import { useForm, Controller } from "react-hook-form"
import Input from "../ui/Input/Input"
import Select from "../ui/Select/Select"
import DekanatService from "../../api/DekanatService"
import Button from "../ui/Button/Button"
import Loader from "../ui/Loader/Loader"

const UpdateUserForm = ({ defaultValues, onSubmit, isLoading }) => {
    const { control, handleSubmit, setValue } = useForm({
        mode: "onSubmit",
        defaultValues
    })

    const [isSelectUkoRole, setIsSelectUkoRole] = useState(false)

    const [roles, setRoles] = useState([])
    const [getRoles, isRolesLoading] = useFetching(async () => {
        const response = await RoleService.getRoles()
        if (response.status == 200) {
            const data = response.data.filter(x => x.name != "student").map(role => ({
                value: role.id,
                label: role.name
            }))
            setRoles(data)
            setIsSelectUkoRole(defaultValues.role == "uko")
        }
    })

    const [filials, setFilials] = useState([])
    const [getFilials, isFilialsLoading] = useFetching(async () => {
        const response = await DekanatService.getFilials()
        const data = response.data.map(dataItem => ({
            value: dataItem.filId,
            label: dataItem.filial
        }))
        setFilials(data)
    })

    const handleChangeRole = (onChange, newValue) => {
        onChange(newValue?.value)
        
        const roleName = newValue?.label
        if (roleName == "uko") {
            setIsSelectUkoRole(true)
            filials.length == 0 && getFilials()
        } else {
            setIsSelectUkoRole(false)
            setValue("filialId", undefined)
        }
    }

    useEffect(() => {
        getRoles()
    }, [])

    return (
        <form onSubmit={handleSubmit(onSubmit)} className="form">
            <label className='form__label'>
                <span className='form__text'>Логин</span>
                <Controller
                    control={control}
                    name='login'
                    rules={{
                        required: true
                    }}
                    render={({ field: { value, onChange }, fieldState: { error } }) => (
                        <Input
                            value={value}
                            className="form__input"
                            onChange={onChange}
                            isErrorStatus={!!error}
                        />
                    )}
                />
            </label>
            <label className='form__label'>
                <span className='form__text'>Пароль (необязательно)</span>
                <Controller
                    control={control}
                    name='password'
                    render={({ field: { onChange }, fieldState: { error } }) => (
                        <Input
                            type="password"
                            className="form__input"
                            onChange={onChange}
                            isErrorStatus={!!error}
                        />
                    )}
                />
            </label>
            <label className='form__label'>
                <span className='form__text'>Роль</span>
                <Controller
                    control={control}
                    name='roleId'
                    rules={{
                        required: true
                    }}
                    render={({ field: { value, onChange }, fieldState: { error } }) => (
                        <Select
                            options={roles}
                            value={roles.find(x => x.value == value)}
                            onChange={(newValue) => handleChangeRole(onChange, newValue)}
                            isLoading={isRolesLoading}
                            isDisabled={isRolesLoading}
                            placeholder='Выберите роль'
                            isErrorStatus={!!error}
                        />
                    )}
                />
            </label>
            {
                isSelectUkoRole
                &&
                <label className='form__label'>
                    <span className='form__text'>Филиал</span>
                    <Controller
                        control={control}
                        name='filialId'
                        rules={{
                            required: true
                        }}
                        render={({ field: { value, onChange }, fieldState: { error } }) => (
                            <Select
                                value={filials.find(x => x.value == value)}
                                onChange={(newValue) => onChange(newValue?.value)}
                                placeholder='Выберите филиал'
                                options={filials}
                                isLoading={isFilialsLoading}
                                isDisabled={isFilialsLoading}
                                isErrorStatus={!!error}
                            />
                        )}
                    />
                </label>
            }
            <Button className="form__btn" disabled={isLoading}>
                {isLoading ? <Loader text="Изменение" /> : "Изменить"}
            </Button>
        </form>
    )
}

export default UpdateUserForm