import { useState, useEffect } from 'react';
import {AppContext} from "./context";
import { BrowserRouter } from 'react-router-dom';
import AppRouter from './components/AppRouter';
import toast, { Toaster } from 'react-hot-toast';


const App = () => {
  const [isAuthStudent, setIsAuthStudent] = useState(false);
  const [isAuthAdmin, setIsAuthAdmin] = useState(false);
  const [userName, setUserName] = useState('');
  const [employeeId, setEmployeeId] = useState(null)
  const [userFilialId, setUserFilialId] = useState(null)
  const [studentId, setStudentId] = useState(null)
  const [isLoading, setLoading] = useState(true)
  const [accessToken, setAccessToken] = useState(null)
  const [roleName, setRoleName] = useState(null)

  const successNotify = (summary, detail = "") => toast.success(`${summary}\r\n${detail}`, {
    duration: 5000
  })

  const errorNotify = (summary, detail = "") => toast.error(`${summary}\r\n${detail}`, {
    duration: 5000
  })

  const warningNotify = (summary, detail = "") => toast(`${summary}\r\n${detail}`, {
    icon: "⚠️",
    duration: 5000
  })

  const showToast = (type, summary, detail) => {
    if (type == "success") {
      successNotify(summary, detail)
    } else if (type == "error") {
      errorNotify(summary, detail)
    } else if (type == "warning") {
      warningNotify(summary, detail)
    }
  };

  useEffect(() => {
    document.title = "Сетевое тестирование ДГУ"
    if (localStorage.getItem('isAuthStudent')) {
      setIsAuthStudent(true)
      setUserName(localStorage.getItem('userName'))
      setStudentId(localStorage.getItem('studentId'))
    } else if (localStorage.getItem('isAuthAdmin')) {
      setIsAuthAdmin(true)
      setUserName(localStorage.getItem('userName'))
      setEmployeeId(localStorage.getItem("employeeId"))
      setAccessToken(localStorage.getItem("access_token"))
      setRoleName(localStorage.getItem("roleName"))
      setUserFilialId(localStorage.getItem("filialId"))
    }
    setLoading(false);
  }, [])

  return (
    !isLoading
      &&
      <AppContext.Provider value={{
        isAuthStudent,
        setIsAuthStudent,
        isAuthAdmin,
        setIsAuthAdmin,
        userName,
        setUserName,
        isLoading,
        studentId,
        setStudentId,
        employeeId,
        setEmployeeId,
        userFilialId,
        setUserFilialId,
        accessToken,
        setAccessToken,
        roleName,
        setRoleName,
        showToast
      }}>
        <BrowserRouter>
          <Toaster position="top-center" />
          <AppRouter />
        </BrowserRouter>
      </AppContext.Provider>
  )
}

export default App