import { useContext, useEffect, useState } from "react"
import Layout from "../../components/Layout"
import { useFetching } from "../../hooks/useFetching"
import UserService from "../../api/UserService"
import Loader from "../../components/ui/Loader/Loader"
import Button from "../../components/ui/Button/Button"
import Popup from "../../components/ui/Popup/Popup"
import CreateUserForm from "../../components/admin/CreateUserForm"
import { AppContext } from "../../context"
import UpdateUserForm from "../../components/admin/UpdateUserForm"

const Users = () => {
  const { showToast } = useContext(AppContext)

  const [selectedUserId, setSelectedUserId] = useState(null)
  const [users, setUsers] = useState([])
  const [getUsers, isUsersLoading] = useFetching(async () => {
    const response = await UserService.getEmployees()
    if (response.status == 200) {
      setUsers(response.data)
    }
  })

  const [createUser, isCreateLoading] = useFetching(async (createData) => {
    const response = await UserService.createUser(createData)
    if (response.status == 200) {
      setModalCreateActive(false)
      showToast("success", "Создание пользователя прошло успешно!")
      getUsers()
    }
  })

  const [updateUser, isUpdateLoading] = useFetching(async (updateData) => {
    const response = await UserService.updateUser(updateData)
    if (response.status == 200) {
      exitModalUpdate()
      showToast("success", "Изменение пользователя прошло успешно!")
      getUsers()
    }
  })

  const [removeUser, isRemoveLoading] = useFetching(async (userId) => {
    const response = await UserService.removeUser(userId)
    if (response.status == 200) {
      exitModalDelete()
      showToast("success", "Удаление пользователя прошло успешно!")
      getUsers()
    }
  })

  const [recoveryUser, isRecoveryLoading] = useFetching(async (userId) => {
    const response = await UserService.recoveryUser(userId)
    if (response.status == 200) {
      exitModalRecovery()
      showToast("success", "Восстановление пользователя прошло успешно!")
      getUsers()
    }
  })

  const [modalCreateActive, setModalCreateActive] = useState(false)
  const [modalUpdateActive, setModalUpdateActive] = useState(false)
  const [modalDeleteActive, setModalDeleteActive] = useState(false)
  const [modalRecoveryActive, setModalRecoveryActive] = useState(false)

  const exitModalUpdate = () => {
    setSelectedUserId(null)
    setModalUpdateActive(false)
  }

  const exitModalDelete = () => {
    setSelectedUserId(null)
    setModalDeleteActive(false)
  }

  const exitModalRecovery = () => {
    setSelectedUserId(null)
    setModalRecoveryActive(false)
  }

  useEffect(() => {
    getUsers()
  }, [])

  return (
    <Layout headerNavLinks={[
      {
        href: "/admin",
        text: "Тестирования"
      },
      {
        href: "/admin/users",
        text: "Пользователи"
      }
    ]}>
      <div className="users">
        <div className="container">
          <h1 className="users__title title">Пользователи</h1>
          <div className="users__action">
            <Button onClick={() => setModalCreateActive(true)}>Создать</Button>
          </div>
          <div className="users__table">
            {
              isUsersLoading
                ? <Loader text="Идет загрузка тестирований" />
                :
                <table>
                  <thead>
                    <tr>
                      <th>Логин</th>
                      <th>Роль</th>
                      <th>Филиал</th>
                      <th>Изменить</th>
                      <th>Удалить</th>
                      <th>Восстановить</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      users.map(user => (
                        <tr key={user.id}>
                          <td>{user.login}</td>
                          <td>{user.role}</td>
                          <td>{user.filial}</td>
                          <td>
                            {
                              !user.isDeleted
                              &&
                              <Button className="btn_edit" onClick={() => {
                                setSelectedUserId(user.id)
                                setModalUpdateActive(true)
                              }} />
                            }
                          </td>
                          <td>
                            {
                              !user.isDeleted
                              &&
                              <Button className="btn_remove" onClick={() => {
                                setSelectedUserId(user.id)
                                setModalDeleteActive(true)
                              }} />
                            }
                          </td>
                          <td>
                            {
                              user.isDeleted
                              &&
                              <Button className="btn_recovery" onClick={() => {
                                setSelectedUserId(user.id)
                                setModalRecoveryActive(true)
                              }} />
                            }
                          </td>
                        </tr>
                      ))
                    }
                  </tbody>
                </table>
            }
          </div>
        </div>
      </div>
      <Popup isOpen={modalCreateActive} onClose={() => setModalCreateActive(false)}>
        <h1 className="popup__title title">Создание пользователя</h1>
        <CreateUserForm onSubmit={createUser} isLoading={isCreateLoading} />
      </Popup>
      <Popup isOpen={modalUpdateActive} onClose={exitModalUpdate}>
        <h1 className="popup__title title">Изменение пользователя</h1>
        <UpdateUserForm defaultValues={users.find(x => x.id == selectedUserId)} onSubmit={updateUser} isLoading={isUpdateLoading} />
      </Popup>
      <Popup isOpen={modalDeleteActive} onClose={exitModalDelete}>
        <h2 className="popup__title title">Вы действительно хотите удалить пользователя?</h2>
        <div className="confirm-buttons">
          <Button disabled={isRemoveLoading} onClick={() => removeUser(selectedUserId)}>
            {isRemoveLoading ? <Loader /> : "Да"}
          </Button>
          <Button onClick={exitModalDelete}>Нет</Button>
        </div>
      </Popup>
      <Popup isOpen={modalRecoveryActive} onClose={exitModalRecovery}>
        <h2 className="popup__title title">Вы действительно хотите восстановить пользователя?</h2>
        <div className="confirm-buttons">
          <Button disabled={isRecoveryLoading} onClick={() => recoveryUser(selectedUserId)}>
            {isRecoveryLoading ? <Loader /> : "Да"}
          </Button>
          <Button onClick={exitModalRecovery}>Нет</Button>
        </div>
      </Popup>
    </Layout>
  )
}

export default Users