import { roundNumber } from "./roundNumber"

export const getTotalScoreByAnswers = (studentAnswers) => {
    let totalScore = 0
    
    if (studentAnswers) {
        for (let answer of studentAnswers) {
            if (answer.numberOfPoints) {
                totalScore += answer.numberOfPoints
            }
        }
    }

    totalScore = totalScore > 100 ? 100 : roundNumber(totalScore) 
    return totalScore
}