import { useState, useEffect, useContext } from 'react'
import Input from '../../components/ui/Input/Input'
import UserService from '../../api/UserService'
import { useFetching } from '../../hooks/useFetching'
import { Link, useLocation, useNavigate } from 'react-router-dom'
import Select from '../../components/ui/Select/Select'
import Button from '../../components/ui/Button/Button'
import DatePicker from '../../components/ui/DatePicker/DatePicker'
import { AppContext } from "../../context"
import TestingService from '../../api/TestingService'
import Loader from '../../components/ui/Loader/Loader'
import QuestionBlockForTestingService from '../../api/QuestionBlockForTestingService'
import { useForm, Controller } from 'react-hook-form'
import DekanatService from '../../api/DekanatService'
import Layout from '../../components/Layout'

const UpdateTestingSecondStep = () => {
    const { showToast, userFilialId } = useContext(AppContext)

    const redirect = useNavigate()

    const location = useLocation()
    const { testing, questions: updateQuestions, successCreateRedirectUrl } = location.state

    const { control, handleSubmit, setValue, resetField, trigger, getValues, formState: { isSubmitted } } = useForm({
        mode: "onSubmit",
        defaultValues: {
            testing: testing.timeTableSessionId ? {             
                testDurationOnMinutes: testing.testDurationOnMinutes, 
                testingDate: new Date(testing.testingDate)
            } : {
                disciplineName: testing.disciplineName,
                testDurationOnMinutes: testing.testDurationOnMinutes, 
                testingDate: new Date(testing.testingDate)
            },
            inspectors: testing.testingInspectorsForEmployee.map(x => x.personId || x.employeeId)
        }
    })

    const [inspectors, setInspectors] = useState([])
    const [getInspectors, isInspectorsLoading] = useFetching(async () => {
        const response = await UserService.getInspectors()
        if (response.status == 200) {
            const data = response.data.map(dataItem => ({
                value: dataItem.id || dataItem.personId,
                label: dataItem.login
            }))
            setInspectors(data)
        }
    })

    const [getQuestionBlockForTestingByTestingId, isQuestionBlockLoading] = useFetching(async (testingId) => {
        const response = await QuestionBlockForTestingService.getQuestionBlockForTestingByTestingId(testingId)
        if (response.status == 200) {
            const res = [
                {
                    id: response.data[0].id,
                    testingId: response.data[0].testingId,
                    questionBlock: 1,
                    updateQuestionTypeForTestings: [
                        {
                            id: response.data[0].questionTypeForTestings[0].id,
                            questionTypeId: response.data[0].questionTypeForTestings[0].questionTypeId,
                            questionBlockForTestingId: response.data[0].id,
                            questionCount: response.data[0].questionTypeForTestings[0].questionCount
                        },
                        {
                            id: response.data[0].questionTypeForTestings[1].id,
                            questionTypeId: response.data[0].questionTypeForTestings[1].questionTypeId,
                            questionBlockForTestingId: response.data[0].id,
                            questionCount: response.data[0].questionTypeForTestings[1].questionCount
                        },
                        {
                            id: response.data[0].questionTypeForTestings[2].id,
                            questionTypeId: response.data[0].questionTypeForTestings[2].questionTypeId,
                            questionBlockForTestingId: response.data[0].id,
                            questionCount: response.data[0].questionTypeForTestings[2].questionCount
                        }
                    ],
                    scores: response.data[0].scores
                },
                {
                    id: response.data[1].id,
                    testingId: response.data[0].testingId,
                    questionBlock: 2,
                    updateQuestionTypeForTestings: [
                        {
                            id: response.data[1].questionTypeForTestings[0].id,
                            questionTypeId: response.data[1].questionTypeForTestings[0].questionTypeId,
                            questionBlockForTestingId: response.data[1].id,
                            questionCount: response.data[1].questionTypeForTestings[0].questionCount
                        },
                        {
                            id: response.data[1].questionTypeForTestings[1].id,
                            questionTypeId: response.data[1].questionTypeForTestings[1].questionTypeId,
                            questionBlockForTestingId: response.data[1].id,
                            questionCount: response.data[1].questionTypeForTestings[1].questionCount
                        }
                    ],
                    scores: response.data[1].scores
                },
                {
                    id: response.data[2].id,
                    testingId: response.data[0].testingId,
                    questionBlock: 3,
                    updateQuestionTypeForTestings: [
                        {
                            id: response.data[2].questionTypeForTestings[0].id,
                            questionTypeId: response.data[2].questionTypeForTestings[0].questionTypeId,
                            questionBlockForTestingId: response.data[2].id,
                            questionCount: response.data[2].questionTypeForTestings[0].questionCount
                        }
                    ],
                    scores: response.data[2].scores
                }
            ]

            setValue("updateQuestionBlockForTestings", res)
        }
    })

    const [faculties, setFaculties] = useState([])
    const [getFaculties, isFacultiesLoading] = useFetching(async () => {
        const response = await DekanatService.getFaculties()
        const data = response.data.map(dataItem => ({
            value: dataItem.facId,
            label: dataItem.facName
        }))
        setFaculties(data)
    })

    const [departments, setDepartments] = useState([])
    const [getDepartments, isDepartmentsLoading] = useFetching(async (id) => {
        const response = await DekanatService.getDepartmentsByFacultyId(id)
        const data = response.data.map(dataItem => ({
            value: dataItem.departmentId,
            label: dataItem.deptName
        }))
        setDepartments(data)
    })

    const [courses, setCourses] = useState([])
    const [getCourses, isCoursesLoading] = useFetching(async (id, filialId) => {
        const response = await DekanatService.getCoursesByDepartmentId(id, filialId)
        const data = response.data.sort((a, b) => a - b).map(dataItem => ({
            value: dataItem,
            label: dataItem
        }))
        setCourses(data)
    })

    const [groups, setGroups] = useState([])
    const [getGroups, isGroupsLoading] = useFetching(async (id, nCourse, filialId) => {
        const response = await DekanatService.getGroupsByDepartmentIdAndCourse(id, nCourse, filialId)
        const data = response.data.map(dataItem => ({
            value: dataItem,
            label: dataItem
        }))

        setGroups(data)
    })

    const [edukinds, setEdukinds] = useState([])
    const [getEdukinds, isEdukindsLoading] = useFetching(async () => {
        const response = await DekanatService.getEdukinds()
        const data = response.data.map(dataItem => ({
            value: dataItem.edukindId,
            label: dataItem.edukind
        }))
        setEdukinds(data)
    })

    const [retakes, setRetakes] = useState([])
    const [getRetakes, isRetakesLoading] = useFetching(async () => {
        const response = await TestingService.getRetakes()
        const data = response.data.map(dataItem => ({
            value: dataItem.retake,
            label: dataItem.description
        }))
        setRetakes(data)
        setValue("testing.retake", data.find(x => x.label == testing.retake).value)
    })

    const [updateTesting, isUpdateLoading] = useFetching(async (updateTestingData) => {
        const response = await TestingService.updateTesting(updateTestingData)
        if (response.status == 200) {
            showToast("success", "Тестирование успешно обновлено!")
            redirect(successCreateRedirectUrl)
        }
    })

    useEffect(() => {
        if (!testing.timeTableSessionId) {
            setValue("testing.filialId",  testing.filialId)
            setValue("testing.facultyId",  testing.facultyId)
            setValue("testing.departmentId",  testing.departmentId)
            setValue("testing.course",  testing.course)
            setValue("testing.nGroup",  testing.nGroup)
            setValue("testing.edukindId",  testing.edukindId)

            getFaculties(userFilialId)
                .then(_ => getDepartments(testing.facultyId))
                .then(_ => getCourses(testing.departmentId, userFilialId))
                .then(_ => getGroups(testing.departmentId, testing.course, userFilialId))
                
            getEdukinds()
            getRetakes()
        }
        getInspectors()
        getQuestionBlockForTestingByTestingId(updateQuestions[0].testingId)
    }, [])

    const handleChangeFaculty = (onChange, newValue) => {
        onChange(newValue?.value);
        resetField("testing.departmentId");
        resetField("testing.course");
        resetField("testing.nGroup");
        isSubmitted && trigger(["testing.departmentId", "testing.course", "testing.nGroup"])
        getDepartments(newValue?.value)
    }

    const handleChangeDepartment = (onChange, newValue) => {
        onChange(newValue?.value)
        resetField("testing.course");
        resetField("testing.nGroup");
        isSubmitted && trigger(["testing.course", "testing.nGroup"])
        getCourses(newValue?.value, userFilialId)
    }

    const handleChangeCourse = (onChange, newValue) => {
        onChange(newValue?.value)
        resetField("testing.nGroup");
        isSubmitted && trigger("testing.nGroup")

        const departmentId = getValues("testing.departmentId")
        getGroups(departmentId, newValue?.value, userFilialId)
    }

    const handleUpdateTesting = (data) => {
        const totalScore = data.updateQuestionBlockForTestings.reduce((acc, curr) => acc + parseInt(curr.scores), 0)
        if (totalScore > 100) {
            showToast("error", "Общее кол-во баллов за все блоки превышает лимит в 100 баллов!")
            return;
        }

        const RETAKES_NUMBER = {
            "FirstRetake": 1,
            "Первая сдача": 1,
            "SecondRetake": 2,
            "Пересдача": 2,
            "ThirdRetake": 3,
            "Комиссия": 3
        }

        for (let questionItem of updateQuestions) {
            questionItem.updateAnswerVariants = questionItem.answerVariants
            delete questionItem["answerVariants"]
        }

        const updateTestingData = {
            id: testing.testingId,
            timetableSessionId: testing.timeTableSessionId,
            testDurationOnMinutes: data.testing.testDurationOnMinutes,
            testingDate: new Date(new Date(data.testing.testingDate).setHours(new Date(data.testing.testingDate).getHours() + 3)),
            retake: RETAKES_NUMBER[testing.retake],
            disciplineId: testing.disciplineId,
            disciplineName: testing.disciplineName,
            testingCreatorId: testing.testingCreatorId,
            testingInspectorsId: data.inspectors.filter(x => typeof x == "string"),
            personsId: data.inspectors.filter(x => typeof x == "number"),
            updateQuestionBlockForTestings: data.updateQuestionBlockForTestings,
            updateQuestions
        }

        if (!testing.timeTableSessionId) {
            updateTestingData.filialId = userFilialId
            updateTestingData.facultyId = data.testing.facultyId
            updateTestingData.departmentId = data.testing.departmentId
            updateTestingData.course = data.testing.course
            updateTestingData.nGroup = data.testing.nGroup
            updateTestingData.edukindId = data.testing.edukindId
            updateTestingData.retake = RETAKES_NUMBER[data.testing.retake]
            updateTestingData.disciplineName = data.testing.disciplineName
        }

        updateTesting(updateTestingData)
    }

    const handleError = () => {
        showToast("error", "Не все поля заполнены!")
    }

    return (
        <Layout>
            <div className='update-testing update-testing_second'>
                <div className="update-testing__container container">
                    <h1 className='update-testing__title title'>Шаг 2. Заполните недостающие данные</h1>
                    <form className='form' onSubmit={handleSubmit(handleUpdateTesting, handleError)}>
                        <>
                            <div className='form__group'>
                                {
                                    !testing.timeTableSessionId
                                    &&
                                    <>
                                        <div className='form__label'>
                                            <span className='form__text'>Факультет</span>
                                            <Controller
                                                control={control}
                                                name='testing.facultyId'
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                    <Select
                                                        value={faculties.find(x => x.value == value)}
                                                        onChange={(newValue) => handleChangeFaculty(onChange, newValue)}
                                                        placeholder='Выберите факультет'
                                                        options={faculties}
                                                        isLoading={isFacultiesLoading}
                                                        isDisabled={isFacultiesLoading}
                                                        isErrorStatus={!!error}
                                                    />
                                                )}
                                            />
                                        </div>
                                        <div className='form__label'>
                                            <span className='form__text'>Направление</span>
                                            <Controller
                                                control={control}
                                                name='testing.departmentId'
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                    <Select
                                                        value={departments.find(x => x.value == value) || null}
                                                        onChange={(newValue) => handleChangeDepartment(onChange, newValue)}
                                                        placeholder='Выберите направление'
                                                        options={departments}
                                                        isLoading={isDepartmentsLoading}
                                                        isDisabled={isDepartmentsLoading}
                                                        isErrorStatus={!!error}
                                                    />
                                                )}
                                            />
                                        </div>
                                        <div className='form__label'>
                                            <span className='form__text'>Курс</span>
                                            <Controller
                                                control={control}
                                                name='testing.course'
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                    <Select
                                                        value={courses.find(x => x.value == value) || null}
                                                        onChange={(newValue) => handleChangeCourse(onChange, newValue)}
                                                        placeholder="Выберите курс"
                                                        options={courses}
                                                        isLoading={isCoursesLoading}
                                                        isDisabled={isCoursesLoading}
                                                        isErrorStatus={!!error}
                                                    />
                                                )}
                                            />
                                        </div>
                                        <div className='form__label'>
                                            <span className='form__text'>Группа</span>
                                            <Controller
                                                control={control}
                                                name='testing.nGroup'
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                    <Select
                                                        value={groups.find(x => x.value == value) || null}
                                                        onChange={(newValue) => onChange(newValue?.value)}
                                                        placeholder="Выберите группу"
                                                        options={groups}
                                                        isLoading={isGroupsLoading}
                                                        isDisabled={isGroupsLoading}
                                                        isErrorStatus={!!error}
                                                    />
                                                )}
                                            />
                                        </div>
                                        <div className='form__label'>
                                            <span className='form__text'>Форма обучения</span>
                                            <Controller
                                                control={control}
                                                name='testing.edukindId'
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                    <Select
                                                        value={edukinds.find(x => x.value == value)}
                                                        placeholder="Выберите форму обучения"
                                                        onChange={(newValue) => onChange(newValue?.value)}
                                                        options={edukinds}
                                                        isLoading={isEdukindsLoading}
                                                        isDisabled={isEdukindsLoading}
                                                        isErrorStatus={!!error}
                                                    />
                                                )}
                                            />
                                        </div>
                                        <div className='form__label'>
                                            <span className='form__text'>Тип сдачи</span>
                                            <Controller
                                                control={control}
                                                name='testing.retake'
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                    <Select
                                                        value={retakes.find(x => x.value == value)}
                                                        placeholder="Выберите тип сдачи"
                                                        onChange={(newValue) => onChange(newValue?.value)}
                                                        options={retakes}
                                                        isLoading={isRetakesLoading}
                                                        isDisabled={isRetakesLoading}
                                                        isErrorStatus={!!error}
                                                    />
                                                )}
                                            />
                                        </div>
                                        <div className='form__label'>
                                            <span className='form__text'>Название дисциплины</span>
                                            <Controller
                                                control={control}
                                                name='testing.disciplineName'
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                    <Input
                                                        value={value}
                                                        isErrorStatus={!!error}
                                                        className="form__input"
                                                        onChange={onChange}
                                                    />
                                                )}
                                            />
                                        </div>
                                    </>
                                }
                                <div className='form__label'>
                                    <span className='form__text'>Дата проведения</span>
                                    <Controller
                                        control={control}
                                        name='testing.testingDate'
                                        rules={{
                                            required: true
                                        }}
                                        render={({ field: { value, onChange }, fieldState: { error } }) => (
                                            <DatePicker
                                                value={value}
                                                onChange={onChange}
                                                isErrorStatus={!!error}
                                                timeIntervals={5}
                                            />
                                        )}
                                    />
                                </div>
                                <div className='form__label'>
                                    <span className='form__text'>Длительность</span>
                                    <Controller
                                        control={control}
                                        name='testing.testDurationOnMinutes'
                                        rules={{
                                            required: true
                                        }}
                                        render={({ field: { value, onChange }, fieldState: { error } }) => (
                                            <Input
                                                value={value}
                                                onChange={onChange}
                                                min={1}
                                                onWheel={() => document.activeElement.blur()}
                                                className="form__input"
                                                isSmall={true}
                                                isErrorStatus={!!error}
                                                type="number"
                                            />
                                        )}
                                    />
                                </div>
                                <div className='form__label'>
                                    <span className='form__text'>Сотрудник(и) для проверки ответов тестирования</span>
                                    <Controller
                                        control={control}
                                        name='inspectors'
                                        rules={{
                                            required: true
                                        }}
                                        render={({ field: { value, onChange }, fieldState: { error } }) => (
                                            <Select
                                                isMulti={true}
                                                value={value.map(x => inspectors?.find(insp => insp.value == x))}
                                                onChange={(newValue) => { onChange(newValue.map(x => x.value)) }}
                                                placeholder='Выберите сотрудника(ов)'
                                                options={inspectors}
                                                isLoading={isInspectorsLoading}
                                                isDisabled={isInspectorsLoading}
                                                closeMenuOnSelect={false}
                                                blurInputOnSelect={false}
                                                isErrorStatus={!!error}
                                            />
                                        )}
                                    />
                                </div>
                            </div>
                            {
                                isQuestionBlockLoading
                                    ? <Loader text="Загрузка" />
                                    :
                                    <div className='form__table'>
                                        <table>
                                            <thead>
                                                <tr>
                                                    <th>Тип вопроса</th>
                                                    <th>Количество ТЗ по формам</th>
                                                    <th>Количество баллов за блок</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td colSpan={3}>Блок 1</td>
                                                </tr>
                                                <tr>
                                                    <td>№вопрос2</td>
                                                    <td>
                                                        <Controller
                                                            control={control}
                                                            name={`updateQuestionBlockForTestings.0.updateQuestionTypeForTestings.0.questionCount`}
                                                            rules={{
                                                                required: true
                                                            }}
                                                            render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                                <Input value={value} min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} isSmall={true} className="form__input" onInput={e => {
                                                                    if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                                                }} type="number" />
                                                            )}
                                                        />
                                                    </td>
                                                    <td rowSpan={3}>
                                                        <Controller
                                                            control={control}
                                                            name={`updateQuestionBlockForTestings.0.scores`}
                                                            rules={{
                                                                required: true
                                                            }}
                                                            render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                                <Input value={value} min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                                    if (e.target.value > 100) { e.target.value = 100 }
                                                                }} isSmall={true} className="form__input" type="number" />
                                                            )}
                                                        />

                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>№вопрос3</td>
                                                    <td>
                                                        <Controller
                                                            control={control}
                                                            name={`updateQuestionBlockForTestings.0.updateQuestionTypeForTestings.1.questionCount`}
                                                            rules={{
                                                                required: true
                                                            }}
                                                            render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                                <Input value={value} min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} isSmall={true} className="form__input" onInput={e => {
                                                                    if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                                                }} type="number" />
                                                            )}
                                                        />
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>№вопрос4</td>
                                                    <td>
                                                        <Controller
                                                            control={control}
                                                            name={`updateQuestionBlockForTestings.0.updateQuestionTypeForTestings.2.questionCount`}
                                                            rules={{
                                                                required: true
                                                            }}
                                                            render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                                <Input value={value} min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} isSmall={true} className="form__input" onInput={e => {
                                                                    if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                                                }} type="number" />
                                                            )}
                                                        />
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td colSpan={3}>Блок 2</td>
                                                </tr>
                                                <tr>
                                                    <td>№вопрос1</td>
                                                    <td>
                                                        <Controller
                                                            control={control}
                                                            name={`updateQuestionBlockForTestings.1.updateQuestionTypeForTestings.0.questionCount`}
                                                            rules={{
                                                                required: true
                                                            }}
                                                            render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                                <Input value={value} min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} isSmall={true} className="form__input" onInput={e => {
                                                                    if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                                                }} type="number" />
                                                            )}
                                                        />
                                                    </td>
                                                    <td rowSpan={2}>
                                                        <Controller
                                                            control={control}
                                                            name={`updateQuestionBlockForTestings.1.scores`}
                                                            rules={{
                                                                required: true
                                                            }}
                                                            render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                                <Input value={value} min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                                    if (e.target.value > 100) { e.target.value = 100 }
                                                                }} isSmall={true} className="form__input" type="number" />
                                                            )}
                                                        />
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td>№вопрос5</td>
                                                    <td>
                                                        <Controller
                                                            control={control}
                                                            name={`updateQuestionBlockForTestings.1.updateQuestionTypeForTestings.1.questionCount`}
                                                            rules={{
                                                                required: true
                                                            }}
                                                            render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                                <Input value={value} min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} isSmall={true} className="form__input" onInput={e => {
                                                                    if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                                                }} type="number" />
                                                            )}
                                                        />
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td colSpan={3}>Блок 3</td>
                                                </tr>
                                                <tr>
                                                    <td>№вопрокейс</td>
                                                    <td>
                                                        <Controller
                                                            control={control}
                                                            name={`updateQuestionBlockForTestings.2.updateQuestionTypeForTestings.0.questionCount`}
                                                            rules={{
                                                                required: true
                                                            }}
                                                            render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                                <Input value={value} min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} isSmall={true} className="form__input" onInput={e => {
                                                                    if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                                                }} type="number" />
                                                            )}
                                                        />
                                                    </td>
                                                    <td>
                                                        <Controller
                                                            control={control}
                                                            name={`updateQuestionBlockForTestings.2.scores`}
                                                            rules={{
                                                                required: true
                                                            }}
                                                            render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                                <Input value={value} min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                                    if (e.target.value > 100) { e.target.value = 100 }
                                                                }} isSmall={true} className="form__input" type="number" />
                                                            )}
                                                        />
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                            }
                        </>

                        <div className='update-testing__btns'>
                            <Button className="update-testing__save" disabled={isUpdateLoading}>
                                {isUpdateLoading ? <Loader text="Обновить тест" /> : "Обновить тест"}
                            </Button>
                            <Link className='update-testing__back btn btn_secondary' to={-1}>Назад</Link>
                        </div>
                    </form>
                </div>
            </div>
        </Layout>
    )
}

export default UpdateTestingSecondStep