import { useState, useEffect, useContext } from 'react'
import Input from '../../components/ui/Input/Input'
import { useFetching } from '../../hooks/useFetching'
import { Link, useLocation, useNavigate } from 'react-router-dom'
import Select from '../../components/ui/Select/Select'
import Button from '../../components/ui/Button/Button'
import { AppContext } from "../../context"
import TestingService from '../../api/TestingService'
import Loader from '../../components/ui/Loader/Loader'
import UserService from '../../api/UserService'
import DatePicker from '../../components/ui/DatePicker/DatePicker'
import { useForm, Controller } from 'react-hook-form'
import { getQuestionTypeIdByName } from '../../utils/questionType'
import clsx from 'clsx'
import Layout from '../../components/Layout'

const CreateTestingSecondStep = () => {
    const { showToast, employeeId } = useContext(AppContext)

    const redirect = useNavigate()

    const location = useLocation()
    const { dataForCreate, questions: createQuestions, questionTypes, successCreateRedirectUrl } = location.state

    const { control, handleSubmit } = useForm({
        mode: "onSubmit",
        defaultValues: {
            testings: Array.isArray(dataForCreate)
                ? dataForCreate.map(item => ({
                    testDurationOnMinutes: item.duration,
                    testingDate: item.startEvent ? new Date(item.startEvent) : null,
                    nGroup: item.group
                }))
                : {
                    testDurationOnMinutes: dataForCreate.duration,
                    testingDate: dataForCreate.startEvent ? new Date(dataForCreate.startEvent) : null
                },
            createQuestionBlockForTesting: [
                {
                    questionBlock: 1,
                    questionTypeForTestings: [
                        {
                            questionTypeId: getQuestionTypeIdByName(questionTypes, "№вопрос2"),
                            questionCount: null
                        },
                        {
                            questionTypeId: getQuestionTypeIdByName(questionTypes, "№вопрос3"),
                            questionCount: null
                        },
                        {
                            questionTypeId: getQuestionTypeIdByName(questionTypes, "№вопрос4"),
                            questionCount: null
                        }
                    ],
                    scores: null
                },
                {
                    questionBlock: 2,
                    questionTypeForTestings: [
                        {
                            questionTypeId: getQuestionTypeIdByName(questionTypes, "№вопрос1"),
                            questionCount: null
                        },
                        {
                            questionTypeId: getQuestionTypeIdByName(questionTypes, "№вопрос5"),
                            questionCount: null
                        }
                    ],
                    scores: null
                },
                {
                    questionBlock: 3,
                    questionTypeForTestings: [
                        {
                            questionTypeId: getQuestionTypeIdByName(questionTypes, "№вопроскейс"),
                            questionCount: null
                        }
                    ],
                    scores: null
                }
            ]
        }
    })

    const [inspectors, setInspectors] = useState([])
    const [getInspectors, isInspectorsLoading] = useFetching(async () => {
        const response = await UserService.getInspectors()
        if (response.status == 200) {
            const data = response.data.map(dataItem => ({
                value: dataItem.id || dataItem.personId,
                label: dataItem.login
            }))
            setInspectors(data)
        }
    })

    const [createTesting, isCreateLoading] = useFetching(async (createTestingData) => {
        const response = await TestingService.createTesting(createTestingData)
        if (response.status == 200) {
            showToast("success", "Тестирование успешно создано!")
            redirect(successCreateRedirectUrl)
        }
    })

    useEffect(() => {
        getInspectors()
    }, [])

    const handleError = () => {
        showToast("error", "Не все поля заполнены!")
    }

    const handleCreateTesting = (data) => {
        const totalScore = data.createQuestionBlockForTesting.reduce((acc, curr) => acc + parseInt(curr.scores), 0)
        if (totalScore > 100) {
            showToast("error", "Общее кол-во баллов за все блоки превышает лимит в 100 баллов!")
            return;
        }

        const RETAKES_NUMBER = {
            "First": 1,
            "FirstRetake": 1,
            "Second": 2,
            "SecondRetake": 2,
            "Comission": 3,
            "ThirdRetake": 3
        }

        const dataForCreateIterable = Array.isArray(dataForCreate) ? dataForCreate : [dataForCreate]
        const createTestingData = [];
        for (let dataForCreateItem of dataForCreateIterable) {
            const testingDate = Array.isArray(data.testings)
                ? data.testings.find(x => x.nGroup == dataForCreateItem.group).testingDate
                : data.testings.testingDate

            const createTestingDataElem = {
                timetableSessionId: dataForCreateItem.id || null,
                testDurationOnMinutes: Array.isArray(data.testings)
                    ? data.testings.find(x => x.nGroup == dataForCreateItem.group).testDurationOnMinutes
                    : data.testings.testDurationOnMinutes,
                testingDate: new Date(new Date(testingDate).setHours(new Date(testingDate).getHours() + 3)),
                retake: RETAKES_NUMBER[dataForCreateItem.retake],
                disciplineId: dataForCreateItem.subjectId || null,
                disciplineName: dataForCreateItem.subjectName,
                testingCreatorId: employeeId,
                testingInspectorsId: data.inspectors.filter(x => typeof x == "string"),
                personsId: data.inspectors.filter(x => typeof x == "number"),
                createQuestionBlockForTesting: data.createQuestionBlockForTesting,
                createQuestions
            }

            if (!dataForCreateItem.id) {
                createTestingDataElem.filialId = dataForCreateItem.filialId
                createTestingDataElem.facultyId = dataForCreateItem.facultyId
                createTestingDataElem.departmentId = dataForCreateItem.departmentId
                createTestingDataElem.course = dataForCreateItem.course
                createTestingDataElem.nGroup = dataForCreateItem.group
                createTestingDataElem.edukindId = dataForCreateItem.edukindId
            }
            createTestingData.push(createTestingDataElem)
        }

        createTesting(createTestingData)
    }

    return (
        <Layout>
            <div className='create-testing create-testing_second'>
                <div className="create-testing__container container">
                    <h1 className='create-testing__title title'>Шаг 2. Заполните недостающие данные</h1>
                    <form className={clsx('form', { form_multiple: Array.isArray(dataForCreate) })} onSubmit={handleSubmit(handleCreateTesting, handleError)}>
                        {
                            Array.isArray(dataForCreate)
                                ?
                                <div className='form__table'>
                                    <table>
                                        <thead>
                                            <tr>
                                                <th>Группа</th>
                                                <th>Дата проведения</th>
                                                <th>Длительность</th>
                                                <th>Сотрудник(и) для проверки ответов тестирования</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                dataForCreate.map((item, index) => (
                                                    <tr key={index}>
                                                        <td>{item.group}</td>
                                                        <td>
                                                            <Controller
                                                                control={control}
                                                                name={`testings.${index}.testingDate`}
                                                                rules={{
                                                                    required: true
                                                                }}
                                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                                    <DatePicker
                                                                        value={value}
                                                                        onChange={(newValue) => { onChange(newValue) }}
                                                                        isErrorStatus={!!error}
                                                                        timeIntervals={5}
                                                                    />
                                                                )}
                                                            />
                                                        </td>
                                                        <td>
                                                            <Controller
                                                                control={control}
                                                                name={`testings.${index}.testDurationOnMinutes`}
                                                                rules={{
                                                                    required: true
                                                                }}
                                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                                    <Input
                                                                        value={value}
                                                                        onChange={(newValue) => onChange(newValue)}
                                                                        min={1}
                                                                        onWheel={() => document.activeElement.blur()}
                                                                        className="form__input"
                                                                        isSmall={true}
                                                                        isErrorStatus={!!error}
                                                                        type="number"
                                                                    />
                                                                )}
                                                            />
                                                        </td>
                                                        {
                                                            index == 0
                                                            &&
                                                            <td rowSpan={dataForCreate.length}>
                                                                <Controller
                                                                    control={control}
                                                                    name='inspectors'
                                                                    rules={{
                                                                        required: true
                                                                    }}
                                                                    render={({ field: { onChange }, fieldState: { error } }) => (
                                                                        <Select
                                                                            isMulti={true}
                                                                            onChange={(newValue) => { onChange(newValue.map(x => x.value)) }}
                                                                            placeholder='Выберите сотрудника(ов)'
                                                                            options={inspectors}
                                                                            isLoading={isInspectorsLoading}
                                                                            isDisabled={isInspectorsLoading}
                                                                            closeMenuOnSelect={false}
                                                                            blurInputOnSelect={false}
                                                                            isErrorStatus={!!error}
                                                                        />
                                                                    )}
                                                                />
                                                            </td>
                                                        }

                                                    </tr>
                                                ))
                                            }
                                        </tbody>
                                    </table>
                                </div>
                                :
                                <>
                                    <div className='form__group'>
                                        <div className='form__label'>
                                            <span className='form__text'>Дата проведения</span>
                                            <Controller
                                                control={control}
                                                name='testings.testingDate'
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                    <DatePicker
                                                        value={value}
                                                        onChange={(newValue) => { onChange(newValue) }}
                                                        isErrorStatus={!!error}
                                                        timeIntervals={5}
                                                    />
                                                )}
                                            />
                                        </div>
                                        <div className='form__label'>
                                            <span className='form__text'>Длительность</span>
                                            <Controller
                                                control={control}
                                                name='testings.testDurationOnMinutes'
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { value, onChange }, fieldState: { error } }) => (
                                                    <Input
                                                        value={value}
                                                        onChange={(newValue) => onChange(newValue)}
                                                        min={1}
                                                        onWheel={() => document.activeElement.blur()}
                                                        className="form__input"
                                                        isSmall={true}
                                                        isErrorStatus={!!error}
                                                        type="number"
                                                    />
                                                )}
                                            />
                                        </div>
                                        <div className='form__label'>
                                            <span className='form__text'>Сотрудник(и) для проверки ответов тестирования</span>
                                            <Controller
                                                control={control}
                                                name='inspectors'
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { onChange }, fieldState: { error } }) => (
                                                    <Select
                                                        isMulti={true}
                                                        onChange={(newValue) => { onChange(newValue.map(x => x.value)) }}
                                                        placeholder='Выберите сотрудника(ов)'
                                                        options={inspectors}
                                                        isLoading={isInspectorsLoading}
                                                        isDisabled={isInspectorsLoading}
                                                        closeMenuOnSelect={false}
                                                        blurInputOnSelect={false}
                                                        isErrorStatus={!!error}
                                                    />
                                                )}
                                            />
                                        </div>
                                    </div>
                                </>
                        }
                        <div className='form__table'>
                            <table>
                                <thead>
                                    <tr>
                                        <th>Тип вопроса</th>
                                        <th>Количество ТЗ по формам</th>
                                        <th>Количество баллов за блок</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td colSpan={3}>Блок 1</td>
                                    </tr>
                                    <tr>
                                        <td>№вопрос2</td>
                                        <td>
                                            <Controller
                                                control={control}
                                                name={`createQuestionBlockForTesting.0.questionTypeForTestings.0.questionCount`}
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { onChange }, fieldState: { error } }) => (
                                                    <Input min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} isSmall={true} className="form__input" onInput={e => {
                                                        if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                                    }} type="number" />
                                                )}
                                            />
                                        </td>
                                        <td rowSpan={3}>
                                            <Controller
                                                control={control}
                                                name={`createQuestionBlockForTesting.0.scores`}
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { onChange }, fieldState: { error } }) => (
                                                    <Input min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                        if (e.target.value > 100) { e.target.value = 100 }
                                                    }} isSmall={true} className="form__input" type="number" />
                                                )}
                                            />

                                        </td>
                                    </tr>
                                    <tr>
                                        <td>№вопрос3</td>
                                        <td>
                                            <Controller
                                                control={control}
                                                name={`createQuestionBlockForTesting.0.questionTypeForTestings.1.questionCount`}
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { onChange }, fieldState: { error } }) => (
                                                    <Input min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} isSmall={true} className="form__input" onInput={e => {
                                                        if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                                    }} type="number" />
                                                )}
                                            />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>№вопрос4</td>
                                        <td>
                                            <Controller
                                                control={control}
                                                name={`createQuestionBlockForTesting.0.questionTypeForTestings.2.questionCount`}
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { onChange }, fieldState: { error } }) => (
                                                    <Input min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} isSmall={true} className="form__input" onInput={e => {
                                                        if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                                    }} type="number" />
                                                )}
                                            />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td colSpan={3}>Блок 2</td>
                                    </tr>
                                    <tr>
                                        <td>№вопрос1</td>
                                        <td>
                                            <Controller
                                                control={control}
                                                name={`createQuestionBlockForTesting.1.questionTypeForTestings.0.questionCount`}
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { onChange }, fieldState: { error } }) => (
                                                    <Input min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} isSmall={true} className="form__input" onInput={e => {
                                                        if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                                    }} type="number" />
                                                )}
                                            />
                                        </td>
                                        <td rowSpan={2}>
                                            <Controller
                                                control={control}
                                                name={`createQuestionBlockForTesting.1.scores`}
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { onChange }, fieldState: { error } }) => (
                                                    <Input min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                        if (e.target.value > 100) { e.target.value = 100 }
                                                    }} isSmall={true} className="form__input" type="number" />
                                                )}
                                            />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>№вопрос5</td>
                                        <td>
                                            <Controller
                                                control={control}
                                                name={`createQuestionBlockForTesting.1.questionTypeForTestings.1.questionCount`}
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { onChange }, fieldState: { error } }) => (
                                                    <Input min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} isSmall={true} className="form__input" onInput={e => {
                                                        if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                                    }} type="number" />
                                                )}
                                            />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td colSpan={3}>Блок 3</td>
                                    </tr>
                                    <tr>
                                        <td>№вопрокейс</td>
                                        <td>
                                            <Controller
                                                control={control}
                                                name={`createQuestionBlockForTesting.2.questionTypeForTestings.0.questionCount`}
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { onChange }, fieldState: { error } }) => (
                                                    <Input min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} isSmall={true} className="form__input" onInput={e => {
                                                        if (e.target.value.length > 4) { e.target.value = e.target.value.slice(0, 4) }
                                                    }} type="number" />
                                                )}
                                            />
                                        </td>
                                        <td>
                                            <Controller
                                                control={control}
                                                name={`createQuestionBlockForTesting.2.scores`}
                                                rules={{
                                                    required: true
                                                }}
                                                render={({ field: { onChange }, fieldState: { error } }) => (
                                                    <Input min={1} isErrorStatus={!!error} onChange={(newValue) => onChange(newValue)} onWheel={() => document.activeElement.blur()} onInput={e => {
                                                        if (e.target.value > 100) { e.target.value = 100 }
                                                    }} isSmall={true} className="form__input" type="number" />
                                                )}
                                            />
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <div className='create-testing__btns'>
                            <Button className="create-testing__save" disabled={isCreateLoading}>
                                {isCreateLoading ? <Loader text="Создать тест" /> : "Создать тест"}
                            </Button>
                            <Link className='create-testing__back btn btn_secondary' to={-1}>Назад</Link>
                        </div>
                    </form>
                </div>
            </div>
        </Layout>
    )
}

export default CreateTestingSecondStep