import axios from "axios";
import { API_URL, getToken } from "./config";

export default class TestingService {
    static async getListTestsToCreate(filId = null, facId = null, edukindId = null, deptId = null, course = null) {
        const filterData = {
            filId,
            facId,
            edukindId,
            deptId,
            course
        }

        const response = await axios.post(`${API_URL}/Testing/GetListTestsToCreate`, filterData, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async parseFile(formFile) {
        const response = await axios.post(`${API_URL}/Testing/ParseFile`, formFile, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async createTesting(createTestingsDto) {
        const response = await axios.post(`${API_URL}/Testing/CreateTesting`, createTestingsDto, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async updateTesting(updateTestingsDto) {
        const response = await axios.post(`${API_URL}/Testing/UpdateTesting`, updateTestingsDto, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async updateTestingStatusToActive(testingId) {
        const response = await axios.post(`${API_URL}/Testing/UpdateTestingStatusToStarted`, {}, {
            params: {
                testingId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async removeTesting(testingId) {
        const response = await axios.post(`${API_URL}/Testing/RemoveTesting`, {}, {
            params: {
                testingId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async cancelingRemovedTesting(testingId) {
        const response = await axios.post(`${API_URL}/Testing/CancelingRemovedTesting`, {}, {
            params: {
                testingId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async getAllTestings() {
        const response = await axios.get(`${API_URL}/Testing/GetAllTestings`, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async getTestingsByEmployeeId(employeeId) {
        const response = await axios.get(`${API_URL}/Testing/GetTestingsByEmployeeId`, {
            params: {
                employeeId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async getTestingsByPersonId(personId) {
        const response = await axios.get(`${API_URL}/Testing/GetTestingsByPersonId`, {
            params: {
                personId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async getFutureTestingsByStudentId(id) {
        const response = await axios.get(`${API_URL}/Testing/GetFutureTestingsByStudentId`, {
            params: {
                studentId: id
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        }).catch((error) => {
            return error.response;
        })
        return response;
    }

    static async getStartedTestingsByStudentId(id) {
        const response = await axios.get(`${API_URL}/Testing/GetStartedTestingsByStudentId`, {
            params: {
                studentId: id
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        }).catch((error) => {
            return error.response;
        })
        return response;
    }

    static async getFinishedTestingsByStudentId(id) {
        const response = await axios.get(`${API_URL}/Testing/GetFinishedTestingsByStudentId`, {
            params: {
                studentId: id
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        }).catch((error) => {
            return error.response;
        })
        return response;
    }

    static async getCheckedTestingsByStudentId(id) {
        const response = await axios.get(`${API_URL}/Testing/GetCheckedTestingsByStudentId`, {
            params: {
                studentId: id
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        }).catch((error) => {
            return error.response;
        })
        return response;
    }

    static async endTestingForEmployee(testingId) {
        const response = await axios.post(`${API_URL}/Testing/EndTestingForEmployee`, {}, {
            params: {
                testingId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async getRetakes() {
        const response = await axios.get(`${API_URL}/Testing/GetRetakes`, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response
    }
}