import SessionItemForAdmin from '../admin/SessionItemForAdmin'
import { useFetching } from '../../hooks/useFetching'
import TestingService from '../../api/TestingService'
import { useNavigate } from 'react-router-dom'
import { Tab, TabGroup, TabList, TabPanel, TabPanels } from '@headlessui/react'
import { useContext, useState } from 'react'
import TestingsItemForAdmin from './TestingsItemForAdmin'
import { AppContext, AdminContext } from '../../context'
import Popup from '../ui/Popup/Popup'
import Button from '../ui/Button/Button'
import Loader from '../ui/Loader/Loader'
import UnblockTestingForm from './UnblockTestingForm'
import AnswerBlankService from '../../api/AnswerBlankService'

const TestingsListForAdmin = ({ ttSessions, testings, onDelete, onCancelDelete, onResetAnswerBlank, onUpdateTestingStatus }) => {
  const { showToast } = useContext(AppContext)

  const redirect = useNavigate()

  const [selectedSessionsIds, setSelectedSessionsIds] = useState([])
  const [selectedCurrentSessionId, setSelectedCurrentSessionId] = useState(null)

  const [selectedCurrentTestingId, setSelectedCurrentTestingId] = useState(null)
  const [modalDeleteActive, setModalDeleteActive] = useState(null)
  const [modalCancelDeleteActive, setModalCancelDeleteActive] = useState(null)
  const [modalUnlockEnterActive, setModalUnlockEnterActive] = useState(null)
  const [modalUpdateStatusActive, setModalUpdateStatusActive] = useState(null)

  const [loadTest, isParsingLoading] = useFetching(async (file, dataForCreate) => {
    const response = await TestingService.parseFile(file)
    if (response.status == 200) {
      redirect("/create/1", {
        state: {
          questions: response.data,
          dataForCreate,
          successCreateRedirectUrl: `/admin`
        }
      })
    }
  })

  const [removeTesting, isRemoveLoading] = useFetching(async (testingId) => {
    const response = await TestingService.removeTesting(testingId)
    if (response.status == 200) {
      exitModalDelete()
      onDelete()
      showToast("success", "Удаление тестирования прошло успешно!")
    }
  })

  const [recoveryTesting, isRecoveryLoading] = useFetching(async (testingId) => {
    const response = await TestingService.cancelingRemovedTesting(testingId)
    if (response.status == 200) {
      exitModalCancelDelete()
      onCancelDelete()
      showToast("success", "Восстановление тестирования прошло успешно!")
    }
  })

  const [resetAnswerBlank, isResetAnswerBlankLoading] = useFetching(async (resetData) => {
    const response = await AnswerBlankService.resetAnswerBlank(resetData)
    if (response.status == 200) {
      exitModalResetAnswerBlank()
      showToast("success", "Сброс начала прохождения тестирования студенту прошло успешно")
      onResetAnswerBlank()
    }
  })

  const [updateTestingStatusToActive, isUpdateStatusLoading] = useFetching(async (testingId) => {
    const response = await TestingService.updateTestingStatusToActive(testingId)
    if (response.status == 200) {
      exitModalUpdateStatus()
      showToast("success", "Отмена завершения тестирования прошла успешно")
      onUpdateTestingStatus()
    }
  })

  const exitModalDelete = () => {
    setSelectedCurrentTestingId(null)
    setModalDeleteActive(false)
  }

  const exitModalCancelDelete = () => {
    setSelectedCurrentTestingId(null)
    setModalCancelDeleteActive(false)
  }
  
  const exitModalResetAnswerBlank = () => {
    setSelectedCurrentTestingId(null)
    setModalUnlockEnterActive(false)
  }

  const exitModalUpdateStatus = () => {
    setSelectedCurrentSessionId(null)
    setModalUpdateStatusActive(false)
  }

  const onLoadTest = (file, ttSession) => {
    const formData = new FormData()
    formData.append("formFile", file)

    loadTest(formData, ttSession)
  }

  const onLoadTestMultiple = (file, ttSessionIds) => {
    const formData = new FormData()
    formData.append("formFile", file)

    const ttSessionsForCreateTests = ttSessions.filter(t => ttSessionIds.includes(t.id))

    loadTest(formData, ttSessionsForCreateTests)
  }

  const ttSessionsFirst = ttSessions.filter(x => x.retake == "First").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))
  const ttSessionsSecond = ttSessions.filter(x => x.retake == "Second").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))
  const ttSessionsComission = ttSessions.filter(x => x.retake == "Comission").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))

  const testingsFutureFirst = testings.filter(x => x.testingStatus == "Created" && x.retake == "Первая сдача").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))
  const testingsFutureSecond = testings.filter(x => x.testingStatus == "Created" && x.retake == "Пересдача").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))
  const testingsFutureComission = testings.filter(x => x.testingStatus == "Created" && x.retake == "Комиссия").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))

  const testingsActiveFirst = testings.filter(x => ["Started", "Finished"].includes(x.testingStatus) && x.retake == "Первая сдача").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))
  const testingsActiveSecond = testings.filter(x => ["Started", "Finished"].includes(x.testingStatus) && x.retake == "Пересдача").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))
  const testingsActiveComission = testings.filter(x => ["Started", "Finished"].includes(x.testingStatus) && x.retake == "Комиссия").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))

  const testingsCheckedFirst = testings.filter(x => x.testingStatus == "Checked" && x.retake == "Первая сдача").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))
  const testingsCheckedSecond = testings.filter(x => x.testingStatus == "Checked" && x.retake == "Пересдача").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))
  const testingsCheckedComission = testings.filter(x => x.testingStatus == "Checked" && x.retake == "Комиссия").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))

  const testingsArchivedFirst = testings.filter(x => x.testingStatus == "Archived" && x.retake == "Первая сдача").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))
  const testingsArchivedSecond = testings.filter(x => x.testingStatus == "Archived" && x.retake == "Пересдача").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))
  const testingsArchivedComission = testings.filter(x => x.testingStatus == "Archived" && x.retake == "Комиссия").sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate))

  return (
    <>
      <AdminContext.Provider value={{
        selectedSessionsIds,
        setSelectedSessionsIds,
        selectedCurrentSessionId,
        setSelectedCurrentSessionId,
        isParsingLoading,
        selectedCurrentTestingId,
        setSelectedCurrentTestingId,
        setModalDeleteActive,
        setModalCancelDeleteActive,
        setModalUnlockEnterActive,
        setModalUpdateStatusActive
      }}>
        <TabGroup className="testing__tab-group tab-group">
          <TabList className="tab-group__list">
            <Tab className="tab-group__tab" key={1}>
              Не созданные
            </Tab>
            <Tab className="tab-group__tab" key={2}>
              Будущие
            </Tab>
            <Tab className="tab-group__tab" key={3}>
              Текущие
            </Tab>
            <Tab className="tab-group__tab" key={4}>
              Прошедшие
            </Tab>
            <Tab className="tab-group__tab" key={5}>
              Архив
            </Tab>
          </TabList>
          <TabPanels className="tab-group__panels">
            <TabPanel className="tab-group__panel" key={1}>
              <TabGroup className="testing__tab-group tab-group tab-group_sub">
                <TabList className="tab-group__list">
                  <Tab className="tab-group__tab" key={6}>
                    Первая сдача
                  </Tab>
                  <Tab className="tab-group__tab" key={7}>
                    Пересдача
                  </Tab>
                  <Tab className="tab-group__tab" key={8}>
                    Комиссионная пересдача
                  </Tab>
                </TabList>
                <TabPanels className="tab-group__panels">
                  <TabPanel className="tab-group__panel" key={6}>
                    <ul className='testings__list'>
                      {
                        ttSessionsFirst.map(session =>
                          <SessionItemForAdmin key={session.id} session={session} onLoadTestMultiple={onLoadTestMultiple} onLoadTest={onLoadTest} />
                        )
                      }
                    </ul>
                  </TabPanel>
                  <TabPanel className="tab-group__panel" key={7}>
                    <ul className='testings__list'>
                      {
                        ttSessionsSecond.map(session =>
                          <SessionItemForAdmin key={session.id} session={session} onLoadTestMultiple={onLoadTestMultiple} onLoadTest={onLoadTest} />
                        )
                      }
                    </ul>
                  </TabPanel>
                  <TabPanel className="tab-group__panel" key={8}>
                    <ul className='testings__list'>
                      {
                        ttSessionsComission.map(session =>
                          <SessionItemForAdmin key={session.id} session={session} onLoadTestMultiple={onLoadTestMultiple} onLoadTest={onLoadTest} />
                        )
                      }
                    </ul>
                  </TabPanel>
                </TabPanels>
              </TabGroup>
            </TabPanel>
            <TabPanel className="tab-group__panel" key={2}>
              <TabGroup className="testing__tab-group tab-group tab-group_sub">
                <TabList className="tab-group__list">
                  <Tab className="tab-group__tab" key={9}>
                    Первая сдача
                  </Tab>
                  <Tab className="tab-group__tab" key={10}>
                    Пересдача
                  </Tab>
                  <Tab className="tab-group__tab" key={11}>
                    Комиссионная пересдача
                  </Tab>
                </TabList>
                <TabPanels className="tab-group__panels">
                  <TabPanel className="tab-group__panel" key={9}>
                    <ul className='testings__list'>
                      {
                        testingsFutureFirst.map(testing =>
                          <TestingsItemForAdmin key={testing.testingId} testing={testing} />
                        )
                      }
                    </ul>
                  </TabPanel>
                  <TabPanel className="tab-group__panel" key={10}>
                    <ul className='testings__list'>
                      {
                        testingsFutureSecond.map(testing =>
                          <TestingsItemForAdmin key={testing.testingId} testing={testing} />
                        )
                      }
                    </ul>
                  </TabPanel>
                  <TabPanel className="tab-group__panel" key={11}>
                    <ul className='testings__list'>
                      {
                        testingsFutureComission.map(testing =>
                          <TestingsItemForAdmin key={testing.testingId} testing={testing} />
                        )
                      }
                    </ul>
                  </TabPanel>
                </TabPanels>
              </TabGroup>
            </TabPanel>
            <TabPanel className="tab-group__panel" key={3}>
              <TabGroup className="testing__tab-group tab-group tab-group_sub">
                <TabList className="tab-group__list">
                  <Tab className="tab-group__tab" key={12}>
                    Первая сдача
                  </Tab>
                  <Tab className="tab-group__tab" key={13}>
                    Пересдача
                  </Tab>
                  <Tab className="tab-group__tab" key={14}>
                    Комиссионная пересдача
                  </Tab>
                </TabList>
                <TabPanels className="tab-group__panels">
                  <TabPanel className="tab-group__panel" key={12}>
                    <ul className='testings__list'>
                      {
                        testingsActiveFirst.map(testing =>
                          <TestingsItemForAdmin key={testing.testingId} testing={testing} />
                        )
                      }
                    </ul>
                  </TabPanel>
                  <TabPanel className="tab-group__panel" key={13}>
                    <ul className='testings__list'>
                      {
                        testingsActiveSecond.map(testing =>
                          <TestingsItemForAdmin key={testing.testingId} testing={testing} />
                        )
                      }
                    </ul>
                  </TabPanel>
                  <TabPanel className="tab-group__panel" key={14}>
                    <ul className='testings__list'>
                      {
                        testingsActiveComission.map(testing =>
                          <TestingsItemForAdmin key={testing.testingId} testing={testing} />
                        )
                      }
                    </ul>
                  </TabPanel>
                </TabPanels>
              </TabGroup>
            </TabPanel>
            <TabPanel className="tab-group__panel" key={4}>
              <TabGroup className="testing__tab-group tab-group tab-group_sub">
                <TabList className="tab-group__list">
                  <Tab className="tab-group__tab" key={15}>
                    Первая сдача
                  </Tab>
                  <Tab className="tab-group__tab" key={16}>
                    Пересдача
                  </Tab>
                  <Tab className="tab-group__tab" key={17}>
                    Комиссионная пересдача
                  </Tab>
                </TabList>
                <TabPanels className="tab-group__panels">
                  <TabPanel className="tab-group__panel" key={15}>
                    <ul className='testings__list'>
                      {
                        testingsCheckedFirst.map(testing =>
                          <TestingsItemForAdmin key={testing.testingId} testing={testing} />
                        )
                      }
                    </ul>
                  </TabPanel>
                  <TabPanel className="tab-group__panel" key={16}>
                    <ul className='testings__list'>
                      {
                        testingsCheckedSecond.map(testing =>
                          <TestingsItemForAdmin key={testing.testingId} testing={testing} />
                        )
                      }
                    </ul>
                  </TabPanel>
                  <TabPanel className="tab-group__panel" key={17}>
                    <ul className='testings__list'>
                      {
                        testingsCheckedComission.map(testing =>
                          <TestingsItemForAdmin key={testing.testingId} testing={testing} />
                        )
                      }
                    </ul>
                  </TabPanel>
                </TabPanels>
              </TabGroup>
            </TabPanel>
            <TabPanel className="tab-group__panel" key={5}>
              <TabGroup className="testing__tab-group tab-group tab-group_sub">
                <TabList className="tab-group__list">
                  <Tab className="tab-group__tab" key={18}>
                    Первая сдача
                  </Tab>
                  <Tab className="tab-group__tab" key={19}>
                    Пересдача
                  </Tab>
                  <Tab className="tab-group__tab" key={20}>
                    Комиссионная пересдача
                  </Tab>
                </TabList>
                <TabPanels className="tab-group__panels">
                  <TabPanel className="tab-group__panel" key={18}>
                    <ul className='testings__list'>
                      {
                        testingsArchivedFirst.map(testing =>
                          <TestingsItemForAdmin key={testing.testingId} testing={testing} />
                        )
                      }
                    </ul>
                  </TabPanel>
                  <TabPanel className="tab-group__panel" key={19}>
                    <ul className='testings__list'>
                      {
                        testingsArchivedSecond.map(testing =>
                          <TestingsItemForAdmin key={testing.testingId} testing={testing} />
                        )
                      }
                    </ul>
                  </TabPanel>
                  <TabPanel className="tab-group__panel" key={20}>
                    <ul className='testings__list'>
                      {
                        testingsArchivedComission.map(testing =>
                          <TestingsItemForAdmin key={testing.testingId} testing={testing} />
                        )
                      }
                    </ul>
                  </TabPanel>
                </TabPanels>
              </TabGroup>
            </TabPanel>
          </TabPanels>
        </TabGroup>
        <Popup isOpen={modalDeleteActive} onClose={exitModalDelete}>
          <h2 className="popup__title title">Вы действительно хотите удалить тестирование?</h2>
          <div className="confirm-buttons">
            <Button disabled={isRemoveLoading} onClick={() => removeTesting(selectedCurrentTestingId)}>
              {isRemoveLoading ? <Loader /> : "Да"}
            </Button>
            <Button onClick={exitModalDelete}>Нет</Button>
          </div>
        </Popup>
        <Popup isOpen={modalCancelDeleteActive} onClose={exitModalCancelDelete}>
          <h2 className="popup__title title">Вы действительно хотите восстановить тестирование?</h2>
          <div className="confirm-buttons">
            <Button disabled={isRecoveryLoading} onClick={() => recoveryTesting(selectedCurrentTestingId)}>
              {isRecoveryLoading ? <Loader /> : "Да"}
            </Button>
            <Button onClick={exitModalCancelDelete}>Нет</Button>
          </div>
        </Popup>
        <Popup isOpen={modalUpdateStatusActive} onClose={exitModalUpdateStatus}>
          <h2 className="popup__title title">Вы действительно хотите отменить завершение тестирования?</h2>
          <div className="confirm-buttons">
            <Button disabled={isUpdateStatusLoading} onClick={() => updateTestingStatusToActive(selectedCurrentTestingId)}>
              {isUpdateStatusLoading ? <Loader /> : "Да"}
            </Button>
            <Button onClick={exitModalUpdateStatus}>Нет</Button>
          </div>
        </Popup>
        <Popup isOpen={modalUnlockEnterActive} onClose={() => setModalUnlockEnterActive(false)}>
          <h2 className="popup__title title">Сброс начала прохождения тестирования студенту</h2>
          <UnblockTestingForm onSubmit={resetAnswerBlank} isLoading={isResetAnswerBlankLoading} />
        </Popup>
      </AdminContext.Provider>
    </>
  )
}

export default TestingsListForAdmin