import { useState, useEffect, useContext } from 'react';
import { useFetching } from '../../hooks/useFetching';
import Select from '../ui/Select/Select';
import { useForm, Controller } from 'react-hook-form'
import DekanatService from '../../api/DekanatService';
import { AppContext } from '../../context';
import TestingService from '../../api/TestingService';
import Input from '../ui/Input/Input'
import FileButton from '../ui/FileButton/FileButton';
import Button from '../ui/Button/Button';
import Loader from '../ui/Loader/Loader';

const CreateTestingForm = ({ hasFilial = false, onSubmit, isLoading }) => {
    const { userFilialId } = useContext(AppContext)

    const [filials, setFilials] = useState([])
    const [getFilials, isFilialsLoading] = useFetching(async () => {
        const response = await DekanatService.getFilials()
        const dataArr = []
        response.data.forEach(dataItem => {
            dataArr.push({
                value: dataItem.filId,
                label: dataItem.filial
            })
        })

        setFilials(dataArr)
    })

    const [faculties, setFaculties] = useState([])
    const [getFaculties, isFacultiesLoading] = useFetching(async () => {
        const response = await DekanatService.getFaculties()
        const data = response.data.map(dataItem => ({
            value: dataItem.facId,
            label: dataItem.facName
        }))
        setFaculties(data)
    })

    const [departments, setDepartments] = useState([])
    const [getDepartments, isDepartmentsLoading] = useFetching(async (id) => {
        const response = await DekanatService.getDepartmentsByFacultyId(id)
        const data = response.data.map(dataItem => ({
            value: dataItem.departmentId,
            label: dataItem.deptName
        }))
        setDepartments(data)
    })

    const [courses, setCourses] = useState([])
    const [getCourses, isCoursesLoading] = useFetching(async (id, filialId) => {
        const response = await DekanatService.getCoursesByDepartmentId(id, filialId)
        const data = response.data.sort((a, b) => a - b).map(dataItem => ({
            value: dataItem,
            label: dataItem
        }))
        setCourses(data)
    })

    const [groups, setGroups] = useState([])
    const [getGroups, isGroupsLoading] = useFetching(async (id, nCourse, filialId) => {
        const response = await DekanatService.getGroupsByDepartmentIdAndCourse(id, nCourse, filialId)
        const data = response.data.map(dataItem => ({
            value: dataItem,
            label: dataItem
        }))

        setGroups(data)
    })

    const [edukinds, setEdukinds] = useState([])
    const [getEdukinds, isEdukindsLoading] = useFetching(async () => {
        const response = await DekanatService.getEdukinds()
        const data = response.data.map(dataItem => ({
            value: dataItem.edukindId,
            label: dataItem.edukind
        }))
        setEdukinds(data)
    })

    const [retakes, setRetakes] = useState([])
    const [getRetakes, isRetakesLoading] = useFetching(async () => {
        const response = await TestingService.getRetakes()
        const data = response.data.map(dataItem => ({
            value: dataItem.retake,
            label: dataItem.description
        }))
        setRetakes(data)
    })

    useEffect(() => {
        hasFilial && getFilials()
        getFaculties()
        getEdukinds()
        getRetakes()
    }, [])

    const { control, handleSubmit, resetField, getValues, trigger, formState: { isSubmitted } } = useForm({
        mode: "onSubmit",
    })

    const handleChangeFilial = (onChange, newValue) => {
        onChange(newValue?.value);
        resetField("facultyId");
        resetField("departmentId");
        resetField("course");
        resetField("group");
        isSubmitted && trigger(["facultyId", "departmentId", "course", "group"])
    }

    const handleChangeFaculty = (onChange, newValue) => {
        onChange(newValue?.value);
        resetField("departmentId");
        resetField("course");
        resetField("group");
        isSubmitted && trigger(["departmentId", "course", "group"])
        getDepartments(newValue?.value)
    }

    const handleChangeDepartment = (onChange, newValue) => {
        onChange(newValue?.value)
        resetField("course");
        resetField("group");
        isSubmitted && trigger(["course", "group"])

        const filialId = hasFilial ? getValues("filialId") : userFilialId
        getCourses(newValue?.value, filialId)
    }

    const handleChangeCourse = (onChange, newValue) => {
        onChange(newValue?.value)
        resetField("group");
        isSubmitted && trigger("group")

        const filialId = hasFilial ? getValues("filialId") : userFilialId
        const departmentId = getValues("departmentId")
        getGroups(departmentId, newValue?.value, filialId)
    }

    return (
        <form className='form' onSubmit={handleSubmit(onSubmit)}>
            {
                hasFilial
                &&
                    <label className='form__label'>
                        <span className='form__text'>Филиал</span>
                        <Controller
                            control={control}
                            name='filialId'
                            rules={{
                                required: true
                            }}
                            render={({ field: { value, onChange } }) => (
                                <Select
                                    value={filials.find(x => x.value == value) || null}
                                    onChange={(newValue) => handleChangeFilial(onChange, newValue)}
                                    placeholder='Выберите филиал'
                                    options={filials}
                                    isLoading={isFilialsLoading}
                                    isDisabled={isFilialsLoading}
                                />
                            )}
                        />
                    </label>
            }
            <label className='form__label'>
                <span className='form__text'>Факультет</span>
                <Controller
                    control={control}
                    name='facultyId'
                    rules={{
                        required: true
                    }}
                    render={({ field: { value, onChange }, fieldState: { error } }) => (
                        <Select
                            value={faculties.find(x => x.value == value) || null}
                            onChange={(newValue) => handleChangeFaculty(onChange, newValue)}
                            placeholder='Выберите факультет'
                            options={faculties}
                            isLoading={isFacultiesLoading}
                            isDisabled={isFacultiesLoading}
                            isErrorStatus={!!error}
                        />
                    )}
                />
            </label>
            <label className='form__label'>
                <span className='form__text'>Направление</span>
                <Controller
                    control={control}
                    name='departmentId'
                    rules={{
                        required: true
                    }}
                    render={({ field: { value, onChange }, fieldState: { error } }) => (
                        <Select
                            value={departments.find(x => x.value == value) || null}
                            onChange={(newValue) => handleChangeDepartment(onChange, newValue)}
                            placeholder='Выберите направление'
                            options={departments}
                            isLoading={isDepartmentsLoading}
                            isDisabled={isDepartmentsLoading}
                            isErrorStatus={!!error}
                        />
                    )}
                />
            </label>
            <label className='form__label'>
                <span className='form__text'>Курс</span>
                <Controller
                    control={control}
                    name='course'
                    rules={{
                        required: true
                    }}
                    render={({ field: { value, onChange }, fieldState: { error } }) => (
                        <Select
                            value={courses.find(x => x.value == value) || null}
                            onChange={(newValue) => handleChangeCourse(onChange, newValue)}
                            placeholder="Выберите курс"
                            options={courses}
                            isLoading={isCoursesLoading}
                            isDisabled={isCoursesLoading}
                            isErrorStatus={!!error}
                        />
                    )}
                />
            </label>
            <label className='form__label'>
                <span className='form__text'>Группа</span>
                <Controller
                    control={control}
                    name='group'
                    rules={{
                        required: true
                    }}
                    render={({ field: { value, onChange }, fieldState: { error } }) => (
                        <Select
                            isMulti={true}
                            value={groups.find(x => x.value == value) || null}
                            onChange={(newValue) => onChange(newValue?.map((x) => x.value))}
                            placeholder="Выберите группу"
                            options={groups}
                            isLoading={isGroupsLoading}
                            isDisabled={isGroupsLoading}
                            isErrorStatus={!!error}
                        />
                    )}
                />
            </label>
            <label className='form__label'>
                <span className='form__text'>Форма обучения</span>
                <Controller
                    control={control}
                    name='edukindId'
                    rules={{
                        required: true
                    }}
                    render={({ field: { onChange }, fieldState: { error } }) => (
                        <Select
                            placeholder="Выберите форму обучения"
                            onChange={(newValue) => onChange(newValue?.value)}
                            options={edukinds}
                            isLoading={isEdukindsLoading}
                            isDisabled={isEdukindsLoading}
                            isErrorStatus={!!error}
                        />
                    )}
                />
            </label>
            <label className='form__label'>
                <span className='form__text'>Тип сдачи</span>
                <Controller
                    control={control}
                    name='retake'
                    rules={{
                        required: true
                    }}
                    render={({ field: { onChange }, fieldState: { error } }) => (
                        <Select
                            placeholder="Выберите тип сдачи"
                            onChange={(newValue) => onChange(newValue?.value)}
                            options={retakes}
                            isLoading={isRetakesLoading}
                            isDisabled={isRetakesLoading}
                            isErrorStatus={!!error}
                        />
                    )}
                />
            </label>
            <label className='form__label'>
                <span className='form__text'>Название дисциплины</span>
                <Controller
                    control={control}
                    name='subjectName'
                    rules={{
                        required: true
                    }}
                    render={({ field: { onChange }, fieldState: { error } }) => (
                        <Input
                            isErrorStatus={!!error}
                            className="form__input"
                            onChange={onChange}
                        />
                    )}
                />
            </label>
            <label className='form__label'>
                <span className='form__text'>Банк тестов</span>
                <Controller
                    control={control}
                    name='questionsBankFile'
                    rules={{
                        required: "Банк тестов не загружен!"
                    }}
                    render={({ field: { value: file, onChange }, fieldState: { error } }) => (
                        <div className='form__file-control'>
                            <FileButton className="form__file-btn" accept=".doc, .docx" text="Загрузить" onSelected={(e) => onChange(e.target.files[0])} />
                            {file && <div className='form__file-name'>{file.name}</div>}
                            {error && <div className='form__file-error'>{error.message}</div>}
                        </div>
                    )}
                />
            </label>
            <Button disabled={isLoading} className='form__btn'>
                {isLoading ? <Loader text="Продолжить" /> : "Продолжить"}
            </Button>
        </form>
    )
}

export default CreateTestingForm