import LoginStudent from "../pages/LoginStudent";
import LoginAdmin from "../pages/LoginAdmin";
import TestingForTeacher from "../pages/teacher/TestingForTeacher";
import TestingResults from '../pages/TestingResults'
import CheckingAnswers from "../pages/teacher/CheckingAnswers";
import UkoPage from "../pages/uko/UkoPage";
import AdminPage from "../pages/admin/AdminPage";
import Testing from "../pages/student/Testing";
import CreateTestingFirstStep from "../pages/common/CreateTestingFirstStep";
import CreateTestingSecondStep from "../pages/common/CreateTestingSecondStep";
import UpdateTestingFirstStep from "../pages/common/UpdateTestingFirstStep";
import UpdateTestingSecondStep from "../pages/common/UpdateTestingSecondStep";
import TestingPreview from "../pages/common/TestingPreview";
import Testings from "../pages/student/Testings";
import TestingsForTeacher from "../pages/teacher/TestingsForTeacher";
import TestingStudentAnswers from "../pages/TestingStudentAnswers";
import Users from "../pages/admin/Users";

export const privateStudentRoutes = [
    {path: '/testings/:id', element: <Testings />, exact: true},
    {path: '/testing/:id', element: <Testing />, exact: true}, 
    {path: '/testing-answers/:id', element: <TestingStudentAnswers />, exact: true},
]

export const privateTeacherRoutes = [
    {path: '/teacher/:id', element: <TestingsForTeacher />, exact: true},
    {path: '/teacher/testing/:id', element: <TestingForTeacher />, exact: true},
    {path: '/teacher/answers-check', element: <CheckingAnswers />, exact: true},
    {path: '/testing-results/:id', element: <TestingResults />, exact: true},
    {path: '/testing-answers/:id', element: <TestingStudentAnswers />, exact: true},
]

export const privateUkoRoutes = [
    {path: '/uko/:id', element: <UkoPage />, exact: true},
    {path: '/create/1', element: <CreateTestingFirstStep />, exact: true},
    {path: '/create/2', element: <CreateTestingSecondStep />, exact: true},
    {path: '/update/1', element: <UpdateTestingFirstStep />, exact: true},
    {path: '/update/2', element: <UpdateTestingSecondStep />, exact: true},
    {path: '/testing-results/:id', element: <TestingResults />, exact: true},
    {path: '/preview', element: <TestingPreview />, exact: true},
    {path: '/testing-answers/:id', element: <TestingStudentAnswers />, exact: true},
]

export const privateAdminRoutes = [
    {path: '/admin', element: <AdminPage />, exact: true},
    {path: '/admin/users', element: <Users />, exact: true},
    {path: '/create/1', element: <CreateTestingFirstStep />, exact: true},
    {path: '/create/2', element: <CreateTestingSecondStep />, exact: true},
    {path: '/update/1', element: <UpdateTestingFirstStep />, exact: true},
    {path: '/update/2', element: <UpdateTestingSecondStep />, exact: true},
    {path: '/testing-results/:id', element: <TestingResults />, exact: true},
    {path: '/preview', element: <TestingPreview />, exact: true},
    {path: '/testing-answers/:id', element: <TestingStudentAnswers />, exact: true},
]

export const publicRoutes = [
    {path: '/', element: <LoginStudent />, exact: true},
    {path: '/login', element: <LoginAdmin />, exact: true}
]