import { useEffect, useState } from "react"
import "./CheckBoxGroup.css"
import clsx from "clsx"

const CheckBoxGroup = ({ name, variants, onChange, value = [], showIsTrueAnswers = false }) => {
  const [checkedValues, setCheckedValues] = useState(value)
  const [isMounted, setIsMounted] = useState(true)

  useEffect(() => {
    if (!isMounted) {
      onChange && onChange(checkedValues)
    }
    setIsMounted(false)
  }, [checkedValues])

  const onClick = (evt) => {
    evt.target.closest(".checkbox-item").classList.toggle("_checked")

    const choosenVariant = checkedValues?.find(x => x.textAnswerVariant == evt.target.value)
    if (choosenVariant) {
      const newCheckedValues = checkedValues?.filter(x => x.textAnswerVariant != evt.target.value)
      setCheckedValues(newCheckedValues)
    } else {
      setCheckedValues([...checkedValues, variants.find(x => x.textAnswerVariant == evt.target.value)])
    }
  }

  return (
    <>
      <div className="checkbox-group">
        <ul className="checkbox-group__list">
          {
            variants?.map((variantItem, index) => (
              <li className={clsx("checkbox-group__item", "checkbox-item", {
                _checked: checkedValues?.find(x => x.textAnswerVariant == variantItem.textAnswerVariant),
                _success: showIsTrueAnswers && (checkedValues?.find(x => x.textAnswerVariant == variantItem.textAnswerVariant) && variantItem.isTrueVariant),
                _failed: showIsTrueAnswers && (checkedValues?.find(x => x.textAnswerVariant == variantItem.textAnswerVariant) && !variantItem.isTrueVariant)
              })} key={`${name}-${index + 1}`}>
                <input className="checkbox-item__input" type="checkbox" name={`${name}-${index + 1}`} id={`${name}-${index + 1}`} value={variantItem.textAnswerVariant} onClick={onClick} />
                <label className="checkbox-item__label" htmlFor={`${name}-${index + 1}`}>
                  <span className="checkbox-item__text" dangerouslySetInnerHTML={{ __html: variantItem.textAnswerVariant }}></span>
                  {showIsTrueAnswers && <div className="checkbox-item__icon"></div>}
                </label>
              </li>
            ))
          }
        </ul>
      </div>
    </>
  )
}

export default CheckBoxGroup