import { Switch, Case } from '../../components/SwitchCase'
import { SortableList } from '../../components/ui/SortableList/SortableList'
import CheckBoxGroup from '../ui/CheckboxGroup/CheckBoxGroup'
import Input from '../../components/ui/Input/Input'
import RadioGroup from '../../components/ui/RadioGroup/RadioGroup'
import Accordance from '../../components/ui/Accordance/Accordance'


const CreateQuestionsPreview = ({ questions, questionTypes, onChange }) => {
    const getQuestionTypeNameById = (questionTypeId) => {
        const qType = questionTypes.find(x => x.id == questionTypeId)
        return qType?.questionTypeName
    }

    const handleChangeCheckboxGroup = (checkedValues, questionNumber) => {
        const questionEntity = questions[questionNumber]
        for (let i = 0; i < questionEntity.createAnswerVariants.length; i++) {
            const createAnswerEntity = checkedValues?.find(x => x.textAnswerVariant == questionEntity.createAnswerVariants[i].textAnswerVariant)
            if (createAnswerEntity) {
                questionEntity.createAnswerVariants[i].isTrueVariant = true
            } else {
                questionEntity.createAnswerVariants[i].isTrueVariant = false
            }
        }

        const res = [...questions]
        res[questionNumber] = questionEntity
        onChange && onChange(res)
    }

    const handleChangeRadioGroup = (checkedValue, questionNumber) => {
        const questionEntity = questions[questionNumber]
        for (let i = 0; i < questionEntity.createAnswerVariants.length; i++) {
            const createAnswerEntity = checkedValue.textAnswerVariant == questionEntity.createAnswerVariants[i].textAnswerVariant
            if (createAnswerEntity) {
                questionEntity.createAnswerVariants[i].isTrueVariant = true
            } else {
                questionEntity.createAnswerVariants[i].isTrueVariant = false
            }
        }

        const res = [...questions]
        res[questionNumber] = questionEntity
        onChange && onChange(res)
    }

    const getSortableListItems = (items) => {
        const newItems = JSON.parse(JSON.stringify(items))
        for (let i = 0; i < newItems.length; i++) {
            newItems[i].id = i + 1
        }
        return newItems
    }

    const handleChangeSortableList = (updatedList, questionNumber) => {
        const questionEntity = questions[questionNumber]
        for (let i = 0; i < updatedList.length; i++) {
            delete updatedList[i].id
            updatedList[i].numberAnswerVariant = i + 1
        }
        questionEntity.createAnswerVariants = updatedList

        const res = [...questions]
        res[questionNumber] = questionEntity
        onChange && onChange(res)
    }

    const handleChangeAccordance = (updatedList, questionNumber) => {
        const questionEntity = questions[questionNumber]
        const fixedVariants = questionEntity.createAnswerVariants.filter(x => x.isFixedElement == true)
        questionEntity.createAnswerVariants = [...fixedVariants, ...updatedList]

        const res = [...questions]
        res[questionNumber] = questionEntity
        onChange && onChange(res)
    }

    const handleChangeTextAnswerVariant = (textAnswerVariant, questionNumber) => {
        const questionEntity = questions[questionNumber] 
        questionEntity.createAnswerVariants[0].textAnswerVariant = textAnswerVariant

        const res = [...questions]
        res[questionNumber] = questionEntity
        onChange && onChange(res)
    }


    return (
        <div className="questions questions_create">
            <ul className='questions__list'>
                {
                    questions.map((question, index) => (
                        <li key={index} className='questions__item questions-item' id={`question-${index + 1}`}>
                            <div className='questions-item__info'>
                                <span className='questions-item__number'>Вопрос №{index + 1}</span>
                            </div>
                            <div className="questions-item__body">
                                <p dangerouslySetInnerHTML={{ __html: question.questionText.replace("align=left", "") }} className="questions-item__text" onCopy={(evt) => evt.preventDefault()}></p>
                                {getQuestionTypeNameById(question.questionTypeId) == "№вопрос3" && <span className='questions-item__descr'>Расставьте в правильном порядке</span>}
                                <div className="questions-item__answer-variants answer-variants">
                                    <Switch param={getQuestionTypeNameById(question.questionTypeId)}>
                                        <Case value="№вопроскейс">
                                            <span className='answer-variants__label'>Ключ:</span>
                                            <div dangerouslySetInnerHTML={{ __html: question.justification}} className='answer-variants__justification'></div>
                                        </Case>
                                        <Case value="№вопрос1">
                                            <RadioGroup isClearable={false} onChange={(data) => handleChangeRadioGroup(data, index)} name={`question-${index + 1}`} value={question.createAnswerVariants.find(x => x.isTrueVariant == true)} variants={question.createAnswerVariants} />
                                            <span className='answer-variants__label'>Ключ:</span>
                                            <div dangerouslySetInnerHTML={{ __html: question.justification}} className='answer-variants__justification'></div>
                                        </Case>
                                        <Case value="№вопрос2">
                                            <CheckBoxGroup onChange={(data) => handleChangeCheckboxGroup(data, index)} name={`question-${index + 1}`} value={question.createAnswerVariants.filter(x => x.isTrueVariant == true)} variants={question.createAnswerVariants} />
                                        </Case>
                                        <Case value="№вопрос3">
                                            <SortableList
                                                items={getSortableListItems(question.createAnswerVariants)}
                                                onChange={(updatedList) => handleChangeSortableList(updatedList, index)}
                                                renderItem={(variant) => (
                                                    <SortableList.Item id={variant.id}>
                                                        <SortableList.DragHandle />
                                                        <div dangerouslySetInnerHTML={{ __html: variant.textAnswerVariant }}></div>
                                                    </SortableList.Item>
                                                )} />
                                        </Case>
                                        <Case value="№вопрос4">
                                            <Accordance defaultValues={question.createAnswerVariants.filter(x => x.isFixedElement == false)} answers={question.createAnswerVariants} onChange={(data) => handleChangeAccordance(data, index)} />
                                        </Case>
                                        <Case value="№вопрос5">
                                            <span className='answer-variants__label'>Ответ:</span> <Input onChange={(textAnswerVariant) => handleChangeTextAnswerVariant(textAnswerVariant, index)} value={question.createAnswerVariants[0]?.textAnswerVariant} onPaste={(evt) => evt.preventDefault()} />
                                            <span className='answer-variants__label'>Ключ:</span> <div dangerouslySetInnerHTML={{ __html: question.justification}} className='answer-variants__justification'></div>
                                        </Case>
                                    </Switch>
                                </div>
                            </div>
                        </li>
                    ))
                }
            </ul>
        </div>
    )
}

export default CreateQuestionsPreview