import clsx from 'clsx';
import { useEffect, useState, forwardRef } from 'react';
import "./Input.css"

const Input = forwardRef(({ className, onChange, isSmall, isErrorStatus, value, ...props}, ref) => {
    const [text, setText] = useState(value)
    const [isMounted, setIsMounted] = useState(true)

    useEffect(() => {
        if (!isMounted) {
            onChange && onChange(text || null)
        }
        setIsMounted(false)
    }, [text])

    return (
        <input value={text} className={clsx(className, "input", {
            input_sm: isSmall,
            _error: isErrorStatus
        })} onChange={e => setText(e.target.value)} ref={ref} {...props}/>
    );
});

export default Input;
