import Input from "../ui/Input/Input"

const QuestionBlocksPreview = ({ inspectors, questionBlocks }) => {
    return (
        <>
            <div className='testing-preview__teacher'>
                Сотрудник для проверки тестирования: <span>{inspectors.map(x => x.login).join(", ")}</span>
            </div>
            <div className='testing-preview__table'>
                <table>
                    <thead>
                        <tr>
                            <th>Тип вопроса</th>
                            <th>Количество ТЗ по формам</th>
                            <th>Количество баллов за блок</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td colSpan={3}>Блок 1</td>
                        </tr>
                        <tr>
                            <td>№вопрос2</td>
                            <td>
                                <Input isSmall={true} className="testing-preview__input" value={questionBlocks[0]?.questionTypeForTestings[0]?.questionCount} />
                            </td>
                            <td rowSpan={3}>
                                <Input isSmall={true} className="testing-preview__input" value={questionBlocks[0]?.scores} />
                            </td>
                        </tr>
                        <tr>
                            <td>№вопрос3</td>
                            <td>
                                <Input isSmall={true} className="testing-preview__input" value={questionBlocks[0]?.questionTypeForTestings[1]?.questionCount} />
                            </td>
                        </tr>
                        <tr>
                            <td>№вопрос4</td>
                            <td>
                                <Input isSmall={true} className="testing-preview__input" value={questionBlocks[0]?.questionTypeForTestings[2]?.questionCount} />
                            </td>
                        </tr>
                        <tr>
                            <td colSpan={3}>Блок 2</td>
                        </tr>
                        <tr>
                            <td>№вопрос1</td>
                            <td>
                                <Input isSmall={true} className="testing-preview__input" value={questionBlocks[1]?.questionTypeForTestings[0]?.questionCount} />
                            </td>
                            <td rowSpan={2}>
                                <Input isSmall={true} className="testing-preview__input" value={questionBlocks[1]?.scores} />
                            </td>
                        </tr>
                        <tr>
                            <td>№вопрос5</td>
                            <td>
                                <Input isSmall={true} className="testing-preview__input" value={questionBlocks[1]?.questionTypeForTestings[1]?.questionCount} />
                            </td>
                        </tr>
                        <tr>
                            <td colSpan={3}>Блок 3</td>
                        </tr>
                        <tr>
                            <td>№вопрокейс</td>
                            <td>
                                <Input isSmall={true} className="testing-preview__input" value={questionBlocks[2]?.questionTypeForTestings[0]?.questionCount} />
                            </td>
                            <td>
                                <Input isSmall={true} className="testing-preview__input" value={questionBlocks[2]?.scores} />
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </>

    )
}

export default QuestionBlocksPreview