import { useContext, useEffect, useState } from 'react'
import Popup from '../ui/Popup/Popup'
import { useNavigate } from 'react-router-dom'
import { useFetching } from '../../hooks/useFetching'
import { Tab, TabGroup, TabList, TabPanel, TabPanels } from '@headlessui/react'
import { AppContext } from '../../context'
import TestingService from '../../api/TestingService'
import TestingItemForStudent from './TestingItemForStudent'
import Loader from '../ui/Loader/Loader'
import AnswerBlankService from '../../api/AnswerBlankService'
import Button from '../ui/Button/Button'

const getTestingStatusByActiveTabIndex = (tabIndex) => {
  const testingStatusByIndex = {
    0: "Created",
    1: "StartedAndFinished",
    2: "Checked"
  }

  return testingStatusByIndex[tabIndex]
}

const TestingListForStudent = ({ studentId }) => {
  const { showToast } = useContext(AppContext)

  const [currentTestingTabIndex, setCurrentTestingTabIndex] = useState(1)
  const [modalStartActive, setModalStartActive] = useState(false)

  const [testingId, setTestingId] = useState(null)
  const [testings, setTestings] = useState([])

  const testingsFirst = testings.filter(x => x.retake == "Первая сдача")
  const testingsSecond = testings.filter(x => x.retake == "Пересдача")
  const testingsComission = testings.filter(x => x.retake == "Комиссия")

  const redirect = useNavigate()

  const [getFutureTestings, isFutureLoading] = useFetching(async () => {
    const response = await TestingService.getFutureTestingsByStudentId(studentId)
    if (response.status == 200) {
      setTestings(response.data.sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate)))
    } else {
      showToast("error", response.data)
    }
  })

  const [getStartedAndFinishedTestings, isStartedAndFinishedLoading] = useFetching(async () => {
    let startedAndFinishedTestings = []

    const responseStarted = await TestingService.getStartedTestingsByStudentId(studentId)
    if (responseStarted.status == 200) {
      startedAndFinishedTestings = responseStarted.data
    } else {
      showToast("error", "Ошибка при получении начатых тестирований!", responseStarted.data)
    }

    const responseFinished = await TestingService.getFinishedTestingsByStudentId(studentId)
    if (responseFinished.status == 200) {
      startedAndFinishedTestings = [...startedAndFinishedTestings, ...responseFinished.data]
    } else {
      showToast("error", "Ошибка при получении проверяемых тестирований!", responseFinished.data)
    }

    setTestings(startedAndFinishedTestings.sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate)))
  })

  const [getCheckedTestings, isCheckedLoading] = useFetching(async () => {
    const response = await TestingService.getCheckedTestingsByStudentId(studentId)
    if (response.status == 200) {
      setTestings(response.data.sort((a, b) => new Date(b.testingDate) - new Date(a.testingDate)))
    } else {
      showToast("error", response.data)
    }
  })

  const [startTesting, isStartLoading] = useFetching(async (studentId, testingId) => {
    const response = await AnswerBlankService.studentStartTesting(studentId, testingId)

    if (response.status == 200) {
      const {id: answerBlankId} = response.data
      let disciplineName = testings.find(e => e.id == testingId)?.disciplineName

      redirect(`/testing/${testingId}`, {
        state: { answerBlankId, disciplineName }
      })
    } else {
      showToast("error", `Статус ${response.status}`, response.data)
    }
  })

  const exitModalStart = () => {
    setTestingId(null)
    setModalStartActive(false)
  }

  useEffect(() => {
    const currentTestingStatus = getTestingStatusByActiveTabIndex(currentTestingTabIndex)
    if (currentTestingStatus == "Created") {
      getFutureTestings()
    } else if (currentTestingStatus == "StartedAndFinished") {
      getStartedAndFinishedTestings()
    } else if (currentTestingStatus == "Checked") {
      getCheckedTestings()
    }
  }, [currentTestingTabIndex])

  return (
    <>
      <TabGroup className="testing__tab-group tab-group" onChange={(index) => setCurrentTestingTabIndex(index)} selectedIndex={currentTestingTabIndex}>
        <TabList className="tab-group__list">
          <Tab className="tab-group__tab" key="Future">
            Будущие
          </Tab>
          <Tab className="tab-group__tab" key="StartedAndFinished">
            Текущие
          </Tab>
          <Tab className="tab-group__tab" key="Checked">
            Прошедшие
          </Tab>
        </TabList>
        <TabPanels className="tab-group__panels">
          <TabPanel className="tab-group__panel" key="Future">
            {
              (isFutureLoading && currentTestingTabIndex == 0)
                ? <Loader text="Идет загрузка тестирований" />
                :
                <TabGroup className="testing__tab-group tab-group tab-group_sub">
                  <TabList className="tab-group__list">
                    <Tab className="tab-group__tab" key={4}>
                      Первая сдача
                    </Tab>
                    <Tab className="tab-group__tab" key={5}>
                      Пересдача
                    </Tab>
                    <Tab className="tab-group__tab" key={6}>
                      Комиссионная пересдача
                    </Tab>
                  </TabList>
                  <TabPanels className="tab-group__panels">
                    <TabPanel className="tab-group__panel" key={4}>
                      <ul className='testings__list'>
                        {
                          testingsFirst.map(testing =>
                            <TestingItemForStudent key={testing.id} onClick={() => {setTestingId(testing.id); setModalStartActive(true)}} testing={testing} />
                          )
                        }
                      </ul>
                    </TabPanel>
                    <TabPanel className="tab-group__panel" key={5}>
                      <ul className='testings__list'>
                        {
                          testingsSecond.map(testing =>
                            <TestingItemForStudent key={testing.id} onClick={() => {setTestingId(testing.id); setModalStartActive(true)}} testing={testing} />
                          )
                        }
                      </ul>
                    </TabPanel>
                    <TabPanel className="tab-group__panel" key={6}>
                      <ul className='testings__list'>
                        {
                          testingsComission.map(testing =>
                            <TestingItemForStudent key={testing.id} onClick={() => {setTestingId(testing.id); setModalStartActive(true)}} testing={testing} />
                          )
                        }
                      </ul>
                    </TabPanel>
                  </TabPanels>
                </TabGroup>
            }
          </TabPanel>
          <TabPanel className="tab-group__panel" key="StartedAndFinished">
            {
              (isStartedAndFinishedLoading && currentTestingTabIndex == 1)
                ? <Loader text="Идет загрузка тестирований" />
                :
                <TabGroup className="testing__tab-group tab-group tab-group_sub">
                  <TabList className="tab-group__list">
                    <Tab className="tab-group__tab" key={7}>
                      Первая сдача
                    </Tab>
                    <Tab className="tab-group__tab" key={8}>
                      Пересдача
                    </Tab>
                    <Tab className="tab-group__tab" key={9}>
                      Комиссионная пересдача
                    </Tab>
                  </TabList>
                  <TabPanels className="tab-group__panels">
                    <TabPanel className="tab-group__panel" key={7}>
                      <ul className='testings__list'>
                        {
                          testingsFirst.map(testing =>
                            <TestingItemForStudent key={testing.id} onClick={() => {setTestingId(testing.id); setModalStartActive(true)}} testing={testing} />
                          )
                        }
                      </ul>
                    </TabPanel>
                    <TabPanel className="tab-group__panel" key={8}>
                      <ul className='testings__list'>
                        {
                          testingsSecond.map(testing =>
                            <TestingItemForStudent key={testing.id} onClick={() => {setTestingId(testing.id); setModalStartActive(true)}} testing={testing} />
                          )
                        }
                      </ul>
                    </TabPanel>
                    <TabPanel className="tab-group__panel" key={9}>
                      <ul className='testings__list'>
                        {
                          testingsComission.map(testing =>
                            <TestingItemForStudent key={testing.id} onClick={() => {setTestingId(testing.id); setModalStartActive(true)}} testing={testing} />
                          )
                        }
                      </ul>
                    </TabPanel>
                  </TabPanels>
                </TabGroup>
            }
          </TabPanel>
          <TabPanel className="tab-group__panel" key="Checked">
            {
              (isCheckedLoading && currentTestingTabIndex == 2)
                ? <Loader text="Идет загрузка тестирований" />
                :
                <TabGroup className="testing__tab-group tab-group tab-group_sub">
                  <TabList className="tab-group__list">
                    <Tab className="tab-group__tab" key={10}>
                      Первая сдача
                    </Tab>
                    <Tab className="tab-group__tab" key={11}>
                      Пересдача
                    </Tab>
                    <Tab className="tab-group__tab" key={12}>
                      Комиссионная пересдача
                    </Tab>
                  </TabList>
                  <TabPanels className="tab-group__panels">
                    <TabPanel className="tab-group__panel" key={10}>
                      <ul className='testings__list'>
                        {
                          testingsFirst.map(testing =>
                            <TestingItemForStudent key={testing.id} onClick={() => {setTestingId(testing.id); setModalStartActive(true)}} testing={testing} />
                          )
                        }
                      </ul>
                    </TabPanel>
                    <TabPanel className="tab-group__panel" key={11}>
                      <ul className='testings__list'>
                        {
                          testingsSecond.map(testing =>
                            <TestingItemForStudent key={testing.id} onClick={() => {setTestingId(testing.id); setModalStartActive(true)}} testing={testing} />
                          )
                        }
                      </ul>
                    </TabPanel>
                    <TabPanel className="tab-group__panel" key={12}>
                      <ul className='testings__list'>
                        {
                          testingsComission.map(testing =>
                            <TestingItemForStudent key={testing.id} onClick={() => {setTestingId(testing.id); setModalStartActive(true)}} testing={testing} />
                          )
                        }
                      </ul>
                    </TabPanel>
                  </TabPanels>
                </TabGroup>
            }
          </TabPanel>
        </TabPanels>
      </TabGroup>
      <Popup isOpen={modalStartActive} onClose={exitModalStart}>
        <h2 className="popup__title title">Вы действительно хотите начать тестирование?</h2>
        <div className="confirm-buttons">
          <Button disabled={isStartLoading} onClick={() => startTesting(studentId, testingId)}>
            {isStartLoading ? <Loader /> : "Да"}
          </Button>
          <Button onClick={exitModalStart}>Нет</Button>
        </div>
      </Popup>

    </>
  )
}

export default TestingListForStudent