import { useContext, useEffect, useState } from 'react'
import Button from '../../components/ui/Button/Button'
import AnswerList from '../../components/teacher/AnswerList'
import { Link, useNavigate, useLocation } from 'react-router-dom'
import { useFetching } from '../../hooks/useFetching'
import AnswerBlankService from '../../api/AnswerBlankService'
import Loader from '../../components/ui/Loader/Loader'
import ScoreCircle from '../../components/ui/ScoreCircle/ScoreCircle'
import { AppContext } from '../../context'
import Popup from '../../components/ui/Popup/Popup'
import Layout from '../../components/Layout'

const CheckingAnswers = () => {
  const location = useLocation()
  const { course, group, deptName, disciplineName, defaultScore, statusAnswerBlank, isAllAnswersChecked, scores, answerBlankId } = location.state
  const {showToast} = useContext(AppContext)

  const [modalActive, setModalActive] = useState(false)
  const redirect = useNavigate()

  const [answers, setAnswers] = useState([])
  const [getAnswersByAnswerBlankId, isAnswersLoading] = useFetching(async (answerBlankId) => {
    const response = await AnswerBlankService.getAnswerBlankByIdForChecking(answerBlankId)
    if (response.status == 200) {
      setAnswers(response.data.getAnswersForCheckingWithMaxPoint)
    }
  })

  const [listEvaluation, setListEvaluation] = useState([])

  const [saveAnswers, isSaveLoading] = useFetching(async (answerBlankId, listEvaluations, isEndChecking) => {
    const response = await AnswerBlankService.evaluationOfAnswers(answerBlankId, listEvaluations, isEndChecking)
    if (response.status == 200) {
      showToast("success", "Баллы успешно выставлены!")
      redirect(-1)
    } else {
      showToast("error", response.data)
    }
  })

  useEffect(() => {
    getAnswersByAnswerBlankId(answerBlankId)
  }, [])

  const handleUpdateListEvalutaion = (data) => {
    let res = [...listEvaluation]

    const ind = res.findIndex(x => x.answerId == data.answerId)
    if (ind != -1) {
      if (data.numberOfPoints == null) {
        res = res.filter(x => x.answerId != data.answerId)
      } else {
        res[ind].numberOfPoints = data.numberOfPoints
      }
    } else {
      res.push(data)
    }

    setListEvaluation(res)
  }

  const onEndChecking = () => {
    const isEndCheking = hasNumberOfPointsInAnswers()
    saveAnswers(answers[0].answer.answerBlankId, listEvaluation, isEndCheking)
  }

  const hasNumberOfPointsInAnswers = () => {
    let countAnswersWithNumberOfPoints = answers.reduce((acc, curr) => {
      if (curr.answer.numberOfPoints) {
        acc++    
      }
      return acc
    }, 0)
    
    if (countAnswersWithNumberOfPoints == answers.length) return true

    if (listEvaluation.length > 0) {
      for (let evaluation of listEvaluation) {
        const evaluationAnswer = answers.find(x => x.answer.id == evaluation.answerId)
        if (!evaluationAnswer.answer.numberOfPoints) {
          countAnswersWithNumberOfPoints++
        }
      }
    }
    
    return countAnswersWithNumberOfPoints == answers.length
  }

  return (
    <Layout>
    <section className='answers-check'>
      <div className="answers-check__container container container--smaller">
        <div className='answer-check__wrapper'>
          <Link className='btn btn_secondary' to={-1}>Назад</Link>
          <ScoreCircle
            isDisabled={true}
            defaultScore={defaultScore}
            isAllAnswersChecked={isAllAnswersChecked}
            scores={scores}
            statusAnswerBlank={statusAnswerBlank}
          />
        </div>
        <div className="answers-check__body">
          <h1 className='answers-check__title title'>{disciplineName}</h1>
          <div className="answers-check__data data">
            <span className='data__stage'>{`${course} курс ${group} группа`}</span>
            <span className='data__department'>{deptName}</span>
          </div>
          {
            isAnswersLoading
              ? <Loader text="Загрузка" />
              : <AnswerList answers={answers} onChange={handleUpdateListEvalutaion} />
          }
          {
            answers.length > 0 
            &&
              <div className='answers-check__bottom'>
                <Button className='answers-check__btn' onClick={() => setModalActive(true)}>Закончить проверку</Button>
              </div>
          }
        </div>
      </div>
      <Popup isOpen={modalActive} onClose={() => setModalActive(false)}>
        <h2 className="popup__title title">Вы действительно хотите закончить проверку?</h2>
        <div className="confirm-buttons">
          <Button disabled={isSaveLoading} onClick={onEndChecking}>
            {isSaveLoading ? <Loader /> : "Да"}
          </Button>
          <Button onClick={() => setModalActive(false)}>Нет</Button>
        </div>
      </Popup>
      </section>
    </Layout>
  )
}

export default CheckingAnswers