import axios from "axios"
import { API_URL, getToken } from "./config"

export default class UserService {
    static async getEmployees() {
        const response = await axios.get(`${API_URL}/User/GetEmployees`, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async getInspectors() {
        const response = await axios.get(`${API_URL}/User/GetInspectors`, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async createUser(createData) {
        const response = await axios.post(`${API_URL}/User/CreateEmployee`, createData, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async updateUser(updateData) {
        const response = await axios.post(`${API_URL}/User/UpdateEmployee`, updateData, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async removeUser(employeeId) {
        const response = await axios.post(`${API_URL}/User/RemoveEmployee`, {}, {
            params: { employeeId },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async recoveryUser(employeeId) {
        const response = await axios.post(`${API_URL}/User/CancelingRemovedEmployee`, {}, {
            params: { employeeId },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async signIn(login, password) {
        const response = await axios.post(`${API_URL}/User/EmployeeSignIn`, {
            login,
            password
        }).catch(e => {
            return e.response
        })
        return response;
    }
}