import ScoreCircle from "../ui/ScoreCircle/ScoreCircle"
import { getTotalScoreByAnswers } from "../../utils/blank"
import { getQuestionTypeNameById } from "../../utils/questionType"

const AnswerBlanksForChecking = ({ testingInfo, answerBlanks, questionTypes }) => {
  const isAllAnswersChecked = (studentAnswers) => {
    let resFlag = true

    const allowedQuestionTypesForChecking = ["№вопрос1", "№вопрос5", "№вопроскейс"]
    if (studentAnswers.length > 0) {
      for (let answer of studentAnswers) {
        const qTypeId = answer.question.questionTypeId
        const qTypeName = getQuestionTypeNameById(questionTypes, qTypeId)

        if (allowedQuestionTypesForChecking.includes(qTypeName) && !answer.numberOfPoints) {
          resFlag = false
        }
      }
    }

    return resFlag
  }

  return (
    <ul className='testing-teacher__list'>
      {
        answerBlanks?.map((blank, index) =>
          <li className="testing-teacher__item" key={index}>
            <ScoreCircle 
              key={index}
              defaultScore={index + 1}
              isAllAnswersChecked={isAllAnswersChecked(blank.answers)} 
              scores={getTotalScoreByAnswers(blank.answers)} 
              statusAnswerBlank={blank.statusAnswerBlank} 
              dataForState={{ ...testingInfo, answerBlankId: blank.id }}
            />
          </li>
        )
      }
    </ul>
  )
}

export default AnswerBlanksForChecking