import axios from "axios";
import { API_URL, getToken } from "./config";

export default class DekanatService {
    static async getEdukinds() {
        const response = await axios.get(`${API_URL}/Dekanat/GetEdukinds`)
        return response;
    }

    static async getFilials() {
        const response = await axios.get(`${API_URL}/Dekanat/GetFilials`)
        return response;
    }

    static async getFaculties() {
        const response = await axios.get(`${API_URL}/Dekanat/GetFaculties`)
        return response;
    }

    static async getDepartmentsByFacultyId(id) {
        const response = await axios.get(`${API_URL}/Dekanat/GetDepartmentByFacultyId`, {
            params: {
                facultyId: id
            }
        })
        return response;
    }

    static async getCoursesByDepartmentId(id, filialId) {
        const response = await axios.get(`${API_URL}/Dekanat/GetCoursesByDepartmentId`, {
            params: {
                departmentId: id,
                filialId
            }
        })
        return response;
    }
    
    static async getGroupsByDepartmentIdAndCourse(id, nCourse, filialId) {
        const response = await axios.get(`${API_URL}/Dekanat/GetGroupsByDepartmentId`, {
            params: {
                departmentId: id,
                course: nCourse,
                filialId
            }
        })
        return response;
    }

    static async getStudentsByCourseAndGroup(departmentId, nCourse, nGroup, filialId) {
        const response = await axios.get(`${API_URL}/Dekanat/GetStudentsByCourseAndGroup`, {
            params: {
                departmentId: departmentId,
                course: nCourse,
                ngroup: nGroup,
                filialId: filialId
            }
        })
        return response;
    }

    static async getUsers() {
        const response = await axios.get(`${API_URL}/Dekanat/GetUsers`, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async signInStudent(studentId, nzachkn) {
        const response = await axios.post(`${API_URL}/Dekanat/StudentSignIn`, {
            studentId: studentId,
            nzachkn: nzachkn
        }, {
            params: {
                studentId: studentId,
                nzachkn: nzachkn
            },
        }).catch((error) => {
            return error.response;
        })

        return response;
    }
}