import Select from "../ui/Select/Select";
import { Controller, useForm } from 'react-hook-form';
import DatePicker from '../../components/ui/DatePicker/DatePicker'
import DekanatService from '../../api/DekanatService'
import { useState, useEffect } from "react";
import { useFetching } from "../../hooks/useFetching";
import Button from "../ui/Button/Button";

const TestingsFilter = ({ hasFilial = false, onSubmit, onReset }) => {
    const { control, handleSubmit, resetField, reset } = useForm({
        mode: "onSubmit"
    })

    const [filials, setFilials] = useState([])
    const [getFilials, isFilialsLoading] = useFetching(async () => {
        const response = await DekanatService.getFilials()
        const dataArr = []
        response.data.forEach(dataItem => {
            dataArr.push({
                value: dataItem.filId,
                label: dataItem.filial
            })
        })

        setFilials(dataArr)
    })

    const [faculties, setFaculties] = useState([])
    const [getFaculties, isFacultiesLoading] = useFetching(async () => {
        const response = await DekanatService.getFaculties()
        const dataArr = []
        response.data.forEach(dataItem => {
            dataArr.push({
                value: dataItem.facId,
                label: dataItem.facName
            })
        })

        setFaculties(dataArr)
    })

    const [departments, setDepartments] = useState([])
    const [getDepartmentsByFacultyId, isDepartmentsLoading] = useFetching(async (facId) => {
        const response = await DekanatService.getDepartmentsByFacultyId(facId)
        const dataArr = []
        response.data.forEach(dataItem => {
            dataArr.push({
                value: dataItem.departmentId,
                label: dataItem.deptName
            })
        })

        setDepartments(dataArr)
    })

    const handleChangeFaculty = (onChange, newValue) => {
        onChange(newValue?.value);
        resetField("deptId");
        getDepartmentsByFacultyId(newValue?.value)
    }

    const handleReset = () => {
        reset()
        onReset()
    }

    useEffect(() => {
        hasFilial && getFilials()
        getFaculties()
    }, [])

    return (
        <div className='filter-testing'>
            <form className='filter-testing__form form' onSubmit={handleSubmit(onSubmit)}>
                {
                    hasFilial
                    &&
                        <label className='form__label'>
                            <span className='form__text'>Филиал</span>
                            <Controller
                                control={control}
                                name='filialId'
                                render={({ field: { value, onChange } }) => (
                                    <Select
                                        value={filials.find(x => x.value == value) || null}
                                        onChange={(newValue) => onChange(newValue?.value)}
                                        placeholder='Выберите филиал'
                                        options={filials}
                                        isLoading={isFilialsLoading}
                                        isDisabled={isFilialsLoading}
                                    />
                                )}
                            />
                        </label>
                }
                <label className='form__label'>
                    <span className='form__text'>Факультет</span>
                    <Controller
                        control={control}
                        name='facultyId'
                        render={({ field: { value, onChange } }) => (
                            <Select
                                value={faculties.find(x => x.value == value) || null}
                                onChange={(newValue) => handleChangeFaculty(onChange, newValue)}
                                placeholder='Выберите факультет'
                                options={faculties}
                                isLoading={isFacultiesLoading}
                                isDisabled={isFacultiesLoading}
                            />
                        )}
                    />
                </label>
                <label className='form__label'>
                    <span className='form__text'>Направление</span>
                    <Controller
                        control={control}
                        name='deptId'
                        render={({ field: { value, onChange } }) => (
                            <Select
                                value={departments.find(x => x.value == value) || null}
                                onChange={newValue => onChange(newValue?.value)}
                                placeholder='Выберите направление'
                                options={departments}
                                isLoading={isDepartmentsLoading}
                                isDisabled={isDepartmentsLoading}
                            />
                        )}
                    />
                </label>
                <label className='form__label'>
                    <span className='form__text'>От</span>
                    <Controller
                        control={control}
                        name='examDateBefore'
                        render={({ field: { value, onChange } }) => (
                            <DatePicker
                                value={value}
                                onChange={onChange}
                                showTimeSelect={false}
                            />
                        )}
                    />
                </label>
                <label className='form__label'>
                    <span className='form__text'>До</span>
                    <Controller
                        control={control}
                        name='examDateAfter'
                        render={({ field: { value, onChange } }) => (
                            <DatePicker
                                value={value}
                                onChange={onChange}
                                showTimeSelect={false}
                            />
                        )}
                    />
                </label>
                <div className='form__btns'>
                    <Button className='form__btn'>
                        Применить
                    </Button>
                    <Button type="button" className='form__btn btn_secondary' onClick={handleReset}>
                        Сбросить
                    </Button>
                </div>
            </form>
        </div>
    )
}

export default TestingsFilter