import { useState, useEffect } from 'react'
import { useLocation } from 'react-router-dom'
import Loader from '../../components/ui/Loader/Loader'
import QuestionsPreview from '../../components/uko/QuestionsPreview'
import QuestionService from '../../api/QuestionService'
import QuestionTypeService from '../../api/QuestionTypeService'
import { useFetching } from '../../hooks/useFetching'
import { Link } from 'react-router-dom'
import QuestionBlocksPreview from '../../components/uko/QuestionBlocksPreview'
import QuestionBlockForTestingService from '../../api/QuestionBlockForTestingService'
import Layout from '../../components/Layout'

const TestingPreview = () => {
    const location = useLocation()
    const {testingId, testingInspectors} = location.state

    const [isQuestionsPreviewLoading, setIsQuestionsPreviewLoading] = useState(true)

    const [questionTypes, setQuestionTypes] = useState([])
    const [getQuestionTypes] = useFetching(async () => {
        const response = await QuestionTypeService.getQuestionTypes()
        if (response.status == 200) {
            setQuestionTypes(response.data)
        }
    })

    const [questions, setQuestions] = useState([])
    const [getQuestions] = useFetching(async (testingId) => {
        const response = await QuestionService.getQuestionsByTestingId(testingId)
        if (response.status == 200) {
            setQuestions(response.data)  
        }
    })

    const [questionBlocksForTesting, setQuestionBlocksForTestiing] = useState([])
    const [getQuestionBlockForTestingByTestingId, isQuestionBlocksLoading] = useFetching(async (testingId) => {
        const response = await QuestionBlockForTestingService.getQuestionBlockForTestingByTestingId(testingId)
        if (response.status == 200) {
            setQuestionBlocksForTestiing(response.data)
        }
    })

    useEffect(() => {
        getQuestionBlockForTestingByTestingId(testingId)

        getQuestions(testingId)
            .then(_ => getQuestionTypes())
            .then(_ => setIsQuestionsPreviewLoading(false))
    }, [])

    return (
        <Layout>
            <div className='testing-preview'>
                <div className="container">
                    <Link className='testing-preview__back btn btn_secondary' to={-1}>Назад</Link>
                    <h1 className='testing-preview__title title'>Блок вопросов</h1>
                    {
                        isQuestionBlocksLoading
                            ? <Loader text="Идет загрузка блока вопросов" />
                            : <QuestionBlocksPreview inspectors={testingInspectors} questionBlocks={questionBlocksForTesting} />
                    }
                    <h2 className='testing-preview__title title'>Банк тестов</h2>
                    {
                        isQuestionsPreviewLoading
                            ? <Loader text="Идет загрузка банка тестов" />
                            : <QuestionsPreview questions={questions} questionTypes={questionTypes} />
                    }
                </div>
            </div>
        </Layout>
    )
}

export default TestingPreview