import { createContext, useContext, useMemo } from "react";
import { useSortable } from "@dnd-kit/sortable";
import { CSS } from "@dnd-kit/utilities";

import "./SortableItem.css";
import clsx from "clsx";

const SortableItemContext = createContext({
  attributes: {},
  listeners: undefined,
  ref() {}
});

export const SortableItem = ({ children, id, isTrueAnswer }) => {
  const {
    attributes,
    isDragging,
    listeners,
    setNodeRef,
    setActivatorNodeRef,
    transform,
    transition
  } = useSortable({ id });
  const context = useMemo(
    () => ({
      attributes,
      listeners,
      ref: setActivatorNodeRef
    }),
    [attributes, listeners, setActivatorNodeRef]
  );
  const style = {
    opacity: isDragging ? 0.4 : undefined,
    transform: CSS.Translate.toString(transform),
    transition
  };
  return (
    <SortableItemContext.Provider value={context}>
      <li className={clsx("sortable-list__item", "sortable-item", {
        _success: isTrueAnswer != undefined && isTrueAnswer == true,
        _failed: isTrueAnswer != undefined && isTrueAnswer == false
      })} ref={setNodeRef} style={style}>
        <div className="sortable-item__body">
          {children}
        </div>
        {
          isTrueAnswer != undefined && <div className="sortable-item__icon"></div>
        }
      </li>
    </SortableItemContext.Provider>
  );
}

export const DragHandle = () => {
  const { attributes, listeners, ref } = useContext(SortableItemContext);

  return (
    <button className="sortable-item__drag-handle" {...attributes} {...listeners} ref={ref}>
      <svg viewBox="0 0 20 20" width="20">
        <path d="M7 2a2 2 0 1 0 .001 4.001A2 2 0 0 0 7 2zm0 6a2 2 0 1 0 .001 4.001A2 2 0 0 0 7 8zm0 6a2 2 0 1 0 .001 4.001A2 2 0 0 0 7 14zm6-8a2 2 0 1 0-.001-4.001A2 2 0 0 0 13 6zm0 2a2 2 0 1 0 .001 4.001A2 2 0 0 0 13 8zm0 6a2 2 0 1 0 .001 4.001A2 2 0 0 0 13 14z"></path>
      </svg>
    </button>
  );
}
