import { useEffect, useContext, useState } from 'react';
import { AppContext } from "../context";
import Select from '../components/ui/Select/Select'
import Input from '../components/ui/Input/Input';
import Button from '../components/ui/Button/Button';
import DekanatService from '../api/DekanatService'
import { useFetching } from '../hooks/useFetching'
import { Controller, useForm } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import Loader from '../components/ui/Loader/Loader';
import Layout from '../components/Layout';

const LoginStudent = () => {
    const { setIsAuthStudent, setUserName, setStudentId, showToast, setAccessToken } = useContext(AppContext);

    const [filials, setFilials] = useState([])
    const [getFilials, isFilialsLoading] = useFetching(async () => {
        const response = await DekanatService.getFilials()
        const data = response.data.map(dataItem => ({
            value: dataItem.filId,
            label: dataItem.filial
        }))
        setFilials(data)
    })

    const [faculties, setFaculties] = useState([])
    const [getFaculties, isFacultiesLoading] = useFetching(async () => {
        const response = await DekanatService.getFaculties()
        const data = response.data.map(dataItem => ({
            value: dataItem.facId,
            label: dataItem.facName
        }))
        setFaculties(data)
    })

    const [departments, setDepartments] = useState([])
    const [getDepartments, isDepartmentsLoading] = useFetching(async (id) => {
        const response = await DekanatService.getDepartmentsByFacultyId(id)
        const data = response.data.map(dataItem => ({
            value: dataItem.departmentId,
            label: dataItem.deptName
        }))
        setDepartments(data)
    })

    const [courses, setCourses] = useState([])
    const [getCourses, isCoursesLoading] = useFetching(async (id, filialId) => {
        const response = await DekanatService.getCoursesByDepartmentId(id, filialId)
        const data = response.data.sort((a, b) => a - b).map(dataItem => ({
            value: dataItem,
            label: dataItem
        }))
        setCourses(data)
    })

    const [groups, setGroups] = useState([])
    const [getGroups, isGroupsLoading] = useFetching(async (id, nCourse, filialId) => {
        const response = await DekanatService.getGroupsByDepartmentIdAndCourse(id, nCourse, filialId)
        const data = response.data.map(dataItem => ({
            value: dataItem,
            label: dataItem
        }))

        setGroups(data)
    })

    const [students, setStudents] = useState([])
    const [getStudents, isStudentsLoading] = useFetching(async (id, nCourse, nGroup, filialId) => {
        const response = await DekanatService.getStudentsByCourseAndGroup(id, nCourse, nGroup, filialId)
        const data = response.data.map(dataItem => ({
            value: dataItem.id,
            label: `${dataItem.lastname} ${dataItem.firstname} ${dataItem.patr}`
        }))
        setStudents(data)
    })

    useEffect(() => {
        getFilials()
    }, [])

    const { control, handleSubmit, resetField, getValues, trigger, formState: { isSubmitted } } = useForm({
        mode: "onSubmit",
    })

    const handleChangeFilial = (onChange, newValue) => {
        onChange(newValue.value);
        resetField("facultyId");
        resetField("departmentId");
        resetField("course");
        resetField("group");
        resetField("studentId");
        isSubmitted && trigger(["facultyId", "departmentId", "course", "group", "studentId"])
        getFaculties()
    }

    const handleChangeFaculty = (onChange, newValue) => {
        onChange(newValue?.value);
        resetField("departmentId");
        resetField("course");
        resetField("group");
        resetField("studentId");
        isSubmitted && trigger(["departmentId", "course", "group", "studentId"])
        getDepartments(newValue?.value)
    }
    
      const handleChangeDepartment = (onChange, newValue) => {
        onChange(newValue?.value)
        resetField("course");
        resetField("group");
        resetField("studentId");
        isSubmitted && trigger(["course", "group", "studentId"])

        const filialId = getValues("filialId")
        getCourses(newValue?.value, filialId)
    }
    
    const handleChangeCourse = (onChange, newValue) => {
        onChange(newValue?.value)
        resetField("group");
        resetField("studentId");
        isSubmitted && trigger(["group", "studentId"])

        const filialId = getValues("filialId")
        const departmentId = getValues("departmentId")
        getGroups(departmentId, newValue?.value, filialId)
    }

    const handleChangeGroup = (onChange, newValue) => {
        onChange(newValue?.value)
        resetField("studentId");
        isSubmitted && trigger("studentId")

        const filialId = getValues("filialId")
        const departmentId = getValues("departmentId")
        const course = getValues("course")
        getStudents(departmentId, course, newValue?.value, filialId)
    }

    const redirect = useNavigate()

    const [loginStudent, isSignLoading] = useFetching(async (studentId, nzachkn) => {
        const response = await DekanatService.signInStudent(studentId, nzachkn)

        if (response.status == 200) {
            let userData = response.data

            setIsAuthStudent(true)
            localStorage.setItem("isAuthStudent", "true")

            let studentFio = students.find(s => s.value === studentId).label
            setUserName(studentFio)
            localStorage.setItem("userName", studentFio)

            setStudentId(studentId)
            localStorage.setItem("studentId", studentId)

            localStorage.setItem("access_token", userData.jwtToken)
            setAccessToken(userData.jwtToken)

            redirect(`/testings/${studentId}`)
        } else {
            showToast("error", "Неверные Ф.И.О. или № зач. книжки!")
        }
    })

    const isAccessAllowed = () => {
        return !(/Mac|laptop|Android|webOS|iPhone|iPad|iPod|BlackBerry|BB|PlayBook|IEMobile|Windows Phone|Kindle|Silk|Opera Mini|Linux/i.test(
            navigator.userAgent
        ));
    };

    const login = (data) => {
        isAccessAllowed() ? loginStudent(data.studentId, data.nzachkn) : showToast("warning", "Вход с данного устройства запрещен!")
    }

    return (
        <Layout headerNavLinks={[
            {
                href: "/",
                text: "Студент"
            },
            {
                href: "/login",
                text: "Администратор"
            }
        ]}>
            <section className='login'>
                <div className='container'>
                    <div className='login__inner'>
                        <h1 className='login__title title'>Введите ваши данные</h1>
                        <form className='form' onSubmit={handleSubmit(login)}>
                            <label className='form__label'>
                                <span className='form__text'>Филиал</span>
                                <Controller
                                    control={control}
                                    name='filialId'
                                    rules={{
                                        required: true
                                    }}
                                    render={({ field: { onChange }, fieldState: { error } }) => (
                                        <Select
                                            onChange={(newValue) => handleChangeFilial(onChange, newValue)}
                                            placeholder='Выберите филиал'
                                            options={filials}
                                            isLoading={isFilialsLoading}
                                            isDisabled={isFilialsLoading}
                                            isErrorStatus={!!error}
                                        />
                                    )}
                                />
                            </label>
                            <label className='form__label'>
                                <span className='form__text'>Факультет</span>
                                <Controller
                                    control={control}
                                    name='facultyId'
                                    rules={{
                                        required: true
                                    }}
                                    render={({ field: { value, onChange }, fieldState: { error } }) => (
                                        <Select
                                            value={faculties.find(x => x.value == value) || null}
                                            onChange={(newValue) => handleChangeFaculty(onChange, newValue)}
                                            placeholder='Выберите факультет'
                                            options={faculties}
                                            isLoading={isFacultiesLoading}
                                            isDisabled={isFacultiesLoading}
                                            isErrorStatus={!!error}
                                        />
                                    )}
                                />
                            </label>
                            <label className='form__label'>
                                <span className='form__text'>Направление</span>
                                <Controller
                                    control={control}
                                    name='departmentId'
                                    rules={{
                                        required: true
                                    }}
                                    render={({ field: { value, onChange }, fieldState: { error } }) => (
                                        <Select
                                            value={departments.find(x => x.value == value) || null}
                                            onChange={(newValue) => handleChangeDepartment(onChange, newValue)}
                                            placeholder='Выберите направление'
                                            options={departments}
                                            isLoading={isDepartmentsLoading}
                                            isDisabled={isDepartmentsLoading}
                                            isErrorStatus={!!error}
                                        />
                                    )}
                                />
                            </label>
                            <label className='form__label'>
                                <span className='form__text'>Курс</span>
                                <Controller
                                    control={control}
                                    name='course'
                                    rules={{
                                        required: true
                                    }}
                                    render={({ field: { value, onChange }, fieldState: { error } }) => (
                                        <Select
                                            value={courses.find(x => x.value == value) || null}
                                            onChange={(newValue) => handleChangeCourse(onChange, newValue)}
                                            placeholder="Выберите курс"
                                            options={courses}
                                            isLoading={isCoursesLoading}
                                            isDisabled={isCoursesLoading}
                                            isErrorStatus={!!error}
                                        />
                                    )}
                                />
                            </label>
                            <label className='form__label'>
                                <span className='form__text'>Группа</span>
                                <Controller
                                    control={control}
                                    name='group'
                                    rules={{
                                        required: true
                                    }}
                                    render={({ field: { value, onChange }, fieldState: { error } }) => (
                                        <Select
                                            value={groups.find(x => x.value == value) || null}
                                            onChange={(newValue) => handleChangeGroup(onChange, newValue)}
                                            placeholder="Выберите группу"
                                            options={groups}
                                            isLoading={isGroupsLoading}
                                            isDisabled={isGroupsLoading}
                                            isErrorStatus={!!error}
                                        />
                                    )}
                                />
                            </label>
                            <label className='form__label'>
                                <span className='form__text'>Номер зачетки</span>
                                <Controller
                                    control={control}
                                    name='nzachkn'
                                    rules={{
                                        required: true
                                    }}
                                    render={({ field: { onChange }, fieldState: { error } }) => (
                                        <Input
                                            isSmall={true}
                                            isErrorStatus={!!error}
                                            className="form__input"
                                            onChange={onChange}
                                        />
                                    )}
                                />
                            </label>
                            <label className='form__label'>
                                <span className='form__text'>ФИО</span>
                                <Controller
                                    control={control}
                                    name='studentId'
                                    rules={{
                                        required: true
                                    }}
                                    render={({ field: { value, onChange }, fieldState: { error } }) => (
                                        <Select
                                            value={students.find(x => x.value == value) || null}
                                            onChange={(newValue) => onChange(newValue?.value)}
                                            options={students}
                                            isLoading={isStudentsLoading}
                                            isDisabled={isStudentsLoading}
                                            placeholder='Выберите ФИО'
                                            isErrorStatus={!!error}
                                        />
                                    )}
                                />

                            </label>
                            <Button className="form__btn" disabled={isSignLoading}>
                                {isSignLoading ? <Loader text="Войти" /> : "Войти"}
                            </Button>
                        </form>
                    </div>
                </div>
            </section>
        </Layout>
    );
};

export default LoginStudent;