import Loader from "../Loader/Loader"
import clsx from "clsx"
import "./FileButton.css"

const FileButton = ({onSelected, className, isLoading, text, accept = null}) => {

  return (
    <label className={clsx(className, 'file-btn', {
      _loading: isLoading
    })}>
        {!isLoading &&  <input type="file" accept={accept} onChange={onSelected} />}
        <div className="file-btn__content">
          {isLoading ? <Loader text={text} /> : text}
        </div>
    </label>
  )
}

export default FileButton