import { createPortal } from 'react-dom'
import clsx from 'clsx'
import closePopup from '../../../assets/img/close.svg'
import { Transition, TransitionChild } from "@headlessui/react"
import "./Popup.css"

const popupRootElement = document.querySelector("#popup");

const Popup = ({ className = "", children, isOpen, onClose }) => {
  return createPortal(
    <Transition show={isOpen}>
      <div className={clsx(className, "popup")}>
        <TransitionChild
          enter="_backdrop-enter"
          enterFrom="_backdrop-enter-from"
          enterTo="_backdrop-enter-to"
          leave="_backdrop-leave"
          leaveFrom="_backdrop-leave-from"
          leaveTo="_backdrop-leave-to"
        >
          <div className="popup__backdrop"></div>
        </TransitionChild>
        <div className="popup__body">
          <TransitionChild
            enter="_popup-enter"
            enterFrom="_popup-enter-from"
            enterTo="_popup-enter-to"
            leave="_popup-leave"
            leaveFrom="_popup-leave-from"
            leaveTo="_popup-leave-to"
          >
            <div className="popup__content">
              <button type="button" className="popup__close" onClick={onClose}>
                <img src={closePopup} alt="" />
              </button>
              {children}
            </div>
          </TransitionChild>
        </div>
      </div>
    </Transition>
    ,
    popupRootElement
  )
}

export default Popup