import { forwardRef, useState } from 'react'
import clsx from 'clsx';
import ReactDatePicker from 'react-datepicker' 
import 'react-datepicker/dist/react-datepicker.css'
import ru from 'date-fns/locale/ru';
import "./DatePicker.css"

const DatePicker = forwardRef(({ onChange, value, showTimeSelect = true, isErrorStatus, ...props }, ref) => {
  const [isOpen, setIsOpen] = useState(false)
  const onChangeDate = (newDate) => {
    if (!newDate) {
      onChange(null)
      return
    }

    if (!showTimeSelect) {
      onChange(newDate.setHours(0, 0, 0))
      return
    }

    onChange(newDate)
  }

  return (
    <div className={clsx("datepicker-wrapper", { open: isOpen })}>
        <ReactDatePicker 
            locale={ru}
            showIcon
            ref={ref}
            showTimeSelect={showTimeSelect}
            selected={value}
            className={clsx("datepicker", { _error: isErrorStatus })}
            dateFormat={showTimeSelect ? "dd.MM.yyyy HH:mm" : "dd.MM.yyyy"}
            placeholderText={showTimeSelect ? "дд.мм.гггг чч:мм" : "дд.мм.гггг"}
            timeCaption='Время'
            timeIntervals={15}
            onChange={onChangeDate}      
            onCalendarOpen={() => setIsOpen(true)}
            onCalendarClose={() => setIsOpen(false)}
            {...props}
        />
    </div>
  )
})

export default DatePicker