import { Case, Switch } from '../SwitchCase'
import { getTestingFormatDate, getTestingStatusClassByStatus } from '../../utils/testing'
import clsx from 'clsx'
import { Link } from 'react-router-dom'

const TestingItemForStudent = ({ testing, onClick }) => {

    return (
        <li className={clsx("testings__item", "testings-item", getTestingStatusClassByStatus(testing.testingStatus))}>
            <span className='testings-item__date'>{getTestingFormatDate(testing.testingDate)}</span>
            <Switch param={getTestingStatusClassByStatus(testing.testingStatus)}>
                <Case value={["testings__item--disable", "testings__item--finished", "testings__item--passed"]}>
                    <div className='discipline-btn'>{testing.disciplineName}</div>
                </Case>
                <Case value=" ">
                    <button onClick={onClick} className='discipline-btn'>{testing.disciplineName}</button>
                </Case>
            </Switch>
            
        </li>
    )
}

export default TestingItemForStudent