import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useLocation } from 'react-router-dom'
import QuestionTypeService from "../../api/QuestionTypeService"
import QuestionService from '../../api/QuestionService'
import { useFetching } from '../../hooks/useFetching'
import Loader from "../../components/ui/Loader/Loader"
import UpdateQuestionsPreview from '../../components/uko/UpdateQuestionsPreview'
import Layout from '../../components/Layout'


const UpdateTestingFirstStep = () => {
    const location = useLocation()
    const {testing, successCreateRedirectUrl} = location.state

    const [questionsForUpdate, setQuestionsForUpdate] = useState([])
    const [getQuestionsBankByTestingId, isQuestionsLoading] = useFetching(async (testingId) => {
        const response = await QuestionService.getQuestionsByTestingId(testingId)
        if (response.status == 200) {
            setQuestionsForUpdate(response.data)  
        }
    })

    const [questionTypes, setQuestionTypes] = useState([])
    const [getQuestionTypes, isTypesLoading] = useFetching(async () => {
        const response = await QuestionTypeService.getQuestionTypes()
        if (response.status == 200) {
            setQuestionTypes(response.data)
        }
    })

    useEffect(() => {
        getQuestionsBankByTestingId(testing.testingId)
        getQuestionTypes()
    }, [])

    return (
        <Layout>
            <div className='update-testing'>
                <div className="container">
                    <Link className='update-testing__back btn btn_secondary' to={-1}>Назад</Link>
                    <h1 className='update-testing__title title'>Шаг 1. Проверьте правильность заполнения данных</h1>
                    { 
                        (isQuestionsLoading && isTypesLoading) 
                        ? <Loader text="Идет загрузка банка тестов" />
                        : <UpdateQuestionsPreview onChange={(data) => setQuestionsForUpdate(data)} questions={questionsForUpdate} questionTypes={questionTypes} /> 
                    }
                    <Link className="update-testing__next btn" to="/update/2" state={{ testing, questions: questionsForUpdate, successCreateRedirectUrl }}>Перейти на следующий шаг</Link>
                </div>
            </div>
        </Layout>
    )
}

export default UpdateTestingFirstStep