import axios from "axios";
import { API_URL, getToken } from "./config";

export default class QuestionBlockForTestingService {
    static async getQuestionBlockForTestingByTestingId(testingId) {
        const response = await axios.get(`${API_URL}/QuestionBlockForTesting/GetQuestionBlockForTestingsByTestingId`, {
            params: {
                testingId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }
}