import axios from "axios";
import { API_URL, getToken } from "./config";

export default class AnswerBlankService {
    static async studentStartTesting(studentId, testingId) {
        const response = await axios.post(`${API_URL}/AnswerBlank/StudentStartTesting`, {}, {
            params: {
                studentId,
                testingId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        }).catch((error) => {
            return error.response
        })
        return response;
    }

    static async getAnswerBlankByIdForStudent(answerBlankId) {
        const response = await axios.get(`${API_URL}/AnswerBlank/GetAnswerBlankByAnswerBlankIdForStudent`, {
            params: {
                answerBlankId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response
    }

    static async getAnswerBlanksByTestingId(testingId) {
        const response = await axios.get(`${API_URL}/AnswerBlank/GetAnswerBlanksByTestingId`, {
            params: {
                testingId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response
    }

    static async getAnswerBlankByIdForChecking(answerBlankId) {
        const response = await axios.get(`${API_URL}/AnswerBlank/GetAnswerBlankWithStudentByAnswerBlankIdForChecking`, {
            params: {
                answerBlankId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response
    }

    static async getCheckedAnswerBlanksByIdForStudent(answerBlankId) {
        const response = await axios.get(`${API_URL}/AnswerBlank/GetCheckedAnswerBlankByAnswerBlankIdForStudent`, {
            params: {
                answerBlankId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response
    }

    static async getListAnswerBlanksWithStudentByTestingIdForChecking(testingId) {
        const response = await axios.get(`${API_URL}/AnswerBlank/GetListAnswerBlanksWithStudentByTestingIdForChecking`, {
            params: {
                testingId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }

    static async resetAnswerBlank(resetData) {
        const response = await axios.post(`${API_URL}/AnswerBlank/ResetAnswerBlank`, {}, {
            params: resetData,
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response
    }

    static async evaluationOfAnswers(answerBlankId, evaluationOfAnswers, isEndCheking = false) {
        const response = await axios.post(`${API_URL}/AnswerBlank/EvaluationOfAnswers`, {
            answerBlankId,
            evaluationOfAnswers,
            isEndCheking
        }, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        }).catch((error) => {
            return error.response
        })
        return response;
    }

    static async updateAnswerBlank(answerBlankId, updateAnswers) {
        const response = await axios.post(`${API_URL}/AnswerBlank/UpdateAnswerBlank`, {
            answerBlankId,
            updateAnswers
        }, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        }).catch((error) => {
            return error.response
        })
        return response;
    }

    static async endTestingForStudent(answerBlankId, updateAnswers) {
        const response = await axios.post(`${API_URL}/AnswerBlank/EndTestForStudent`, {
            answerBlankId,
            updateAnswers
        }, {
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        }).catch((error) => {
            return error.response
        })
        return response;
    }
}