import { createContext, useContext, useMemo } from "react";
import { useDraggable } from "@dnd-kit/core";
import { CSS } from "@dnd-kit/utilities";
import "./DraggableItem.css";

const DraggableItemContext = createContext({
  attributes: {},
  listeners: undefined,
  ref() {},
});

export const DraggableItem = ({ id, children, data = null }) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    setActivatorNodeRef,
    transform,
    isDragging,
  } = useDraggable({ id, data });

  const context = useMemo(
    () => ({
      attributes,
      listeners,
      ref: setActivatorNodeRef,
    }),
    [attributes, listeners, setActivatorNodeRef]
  );

  const style = {
    opacity: isDragging ? 0 : undefined,
    transform: CSS.Translate.toString(transform),
  };

  return (
    <DraggableItemContext.Provider value={context}>
      <div ref={setNodeRef} className="draggable" id={id} style={style}>
        {children}
      </div>
    </DraggableItemContext.Provider>
  );
};

export const DragHandle = () => {
  const { attributes, listeners, ref } = useContext(DraggableItemContext);

  return (
    <button className="drag-handle" {...attributes} {...listeners} ref={ref}>
      <svg viewBox="0 0 20 20" width="20">
        <path d="M7 2a2 2 0 1 0 .001 4.001A2 2 0 0 0 7 2zm0 6a2 2 0 1 0 .001 4.001A2 2 0 0 0 7 8zm0 6a2 2 0 1 0 .001 4.001A2 2 0 0 0 7 14zm6-8a2 2 0 1 0-.001-4.001A2 2 0 0 0 13 6zm0 2a2 2 0 1 0 .001 4.001A2 2 0 0 0 13 8zm0 6a2 2 0 1 0 .001 4.001A2 2 0 0 0 13 14z"></path>
      </svg>
    </button>
  );
};
