import { Case, Switch } from './SwitchCase'
import TextArea from './ui/TextArea/TextArea'
import CheckBoxGroup from './ui/CheckboxGroup/CheckBoxGroup'
import { SortableList } from './ui/SortableList/SortableList'
import Accordance from './ui/Accordance/Accordance'
import Input from './ui/Input/Input'
import RadioGroup from './ui/RadioGroup/RadioGroup'

const StudentAnswers = ({ answers }) => {
    const getDefaultVariants = (qVariants, sVariants) => {
        const res = []

        if (sVariants) {
            for (let i = 0; i < sVariants.length; i++) {
                let qVar = { ...qVariants.find(x => x.id == sVariants[i].answerVariantId) }
                qVar.numberAnswerVariant = sVariants[i].numberAnswerVariant ? sVariants[i].numberAnswerVariant : 0
                res.push(qVar)
            }
        }

        return res
    }

    const getSortableListData = (initialData, choosenVariants) => {
        const res = []
    
        if (choosenVariants) {
          for (let i = 0; i < choosenVariants.length; i++) {
            const variantFromInitialData = initialData.find(x => x.id == choosenVariants[i].answerVariantId)
            res.push(variantFromInitialData)
          }
          return res
        } else {
          return initialData
        }
      }

    const getTrueAnswer = (questionAnswerVariants, questionTypeName) => {
        let trueAnswer = ""
        switch(questionTypeName) {
            case "№вопрос1":
            case "№вопрос2":
                trueAnswer = questionAnswerVariants.filter(x => x.isTrueVariant == true).map(x => x.textAnswerVariant).join(", ")
                break;
            case "№вопрос3":
                trueAnswer = questionAnswerVariants.sort((a, b) => a.numberAnswerVariant - b.numberAnswerVariant).map(x => x.textAnswerVariant).join(", ")
            case "№вопрос4":
                trueAnswer = questionAnswerVariants.sort((a, b) => a.numberAnswerVariant - b.numberAnswerVariant).filter(x => x.isFixedElement == false).map(x => x.textAnswerVariant).join(", ")
        }
        return trueAnswer
    }

    return (
        <div className="student-answers">
            <ul className='student-answers__list'>
                {
                    answers?.map((answerItem, index) => (
                        <li key={index} className='student-answers__item answers-item'>
                            <div className="answers-item__wrapper">
                                <span className='answers-item__number'>Вопрос №{index + 1}</span>
                                <div className="answers-item__text" dangerouslySetInnerHTML={{ __html: answerItem.question.questionText.replace("align=left", "") }}></div>
                            </div>
                            <div className="answers-item__wrapper">
                                <span className='answers-item__label'>Ответ</span>
                                <div className="answers-item__body">
                                    <Switch param={answerItem.question.questionType.questionTypeName}>
                                        <Case value="№вопрос1">
                                            <RadioGroup showIsTrueAnswer={true} isClearable={false} value={getDefaultVariants(answerItem.question.answerVariants, answerItem.studentAnswerVariants)[0]} name={`question-${index + 1}`} variants={answerItem.question.answerVariants} />
                                            <span className="answers-item__label">Обоснование</span>
                                            <TextArea heightByContent={true} value={answerItem?.studentAnswerJustification} onPaste={(evt) => evt.preventDefault()} className='answer-variants__justification' />
                                            {/* <span className="answers-item__label">Ключ</span>
                                            <TextArea heightByContent={true} value={answerItem?.question?.justification} onPaste={(evt) => evt.preventDefault()} className='answer-variants__justification' /> */}
                                        </Case>
                                        <Case value="№вопрос2">
                                            <CheckBoxGroup name={`question-${index + 1}`} showIsTrueAnswers={true} value={getDefaultVariants(answerItem.question.answerVariants, answerItem.studentAnswerVariants)} variants={answerItem.question.answerVariants} />
                                        </Case>
                                        <Case value="№вопрос3">
                                            <SortableList
                                                showIsTrueAnswers={true}
                                                items={getSortableListData(answerItem.question.answerVariants.sort((a, b) => a.numberAnswerVariant - b.numberAnswerVariant), answerItem.studentAnswerVariants)}
                                                renderItem={(variant, index) => (
                                                    <SortableList.Item isTrueAnswer={variant.numberAnswerVariant == (index + 1)} id={variant.id}>
                                                        <SortableList.DragHandle />
                                                        <div dangerouslySetInnerHTML={{ __html: variant.textAnswerVariant }}></div>
                                                    </SortableList.Item>
                                                )} />
                                        </Case>
                                        <Case value="№вопрос4">
                                            <Accordance showIsTrueAnswers={true} defaultValues={getDefaultVariants(answerItem.question.answerVariants, answerItem.studentAnswerVariants)} answers={answerItem.question.answerVariants} />
                                        </Case>
                                        <Case value="№вопрос5">
                                            <Input value={answerItem?.text} onPaste={(evt) => evt.preventDefault()} />
                                            <span className="answers-item__label">Обоснование</span>
                                            <TextArea heightByContent={true} value={answerItem?.studentAnswerJustification} onPaste={(evt) => evt.preventDefault()} className='answer-variants__justification' />
                                            {/* <span className="answers-item__label">Ключ</span>
                                            <TextArea heightByContent={true} value={answerItem?.question?.justification} onPaste={(evt) => evt.preventDefault()} className='answer-variants__justification' /> */}
                                        </Case>
                                        <Case value="№вопроскейс">
                                            <TextArea heightByContent={true} value={answerItem?.text} onPaste={(evt) => evt.preventDefault()} />
                                            {/* <span className="answers-item__label">Ключ</span>
                                            <TextArea heightByContent={true} value={answerItem?.question?.justification} onPaste={(evt) => evt.preventDefault()} className='answer-variants__justification' /> */}
                                        </Case>
                                    </Switch>
                                    {
                                        !["№вопрос5", "№вопроскейс"].includes(answerItem.question.questionType.questionTypeName)
                                        &&
                                        <div className="answer-true">
                                            Правильные ответ(ы): <div dangerouslySetInnerHTML={{ __html: getTrueAnswer(answerItem.question.answerVariants, answerItem.question.questionType.questionTypeName)}}></div>
                                        </div>
                                    }
                                    <div className="answers-item__scores">
                                        <div className="answers-item__score-input">
                                            Баллы: <Input disabled={true} isSmall={true} value={answerItem?.numberOfPoints} type="number" />
                                        </div>         
                                    </div>
                                </div>
                            </div>
                        </li>
                    ))
                }
            </ul>
        </div>
    )
}

export default StudentAnswers