import { useState, useEffect, useContext } from 'react'
import Button from '../../components/ui/Button/Button'
import { useFetching } from '../../hooks/useFetching'
import TestingService from '../../api/TestingService'
import TestingsListForUko from '../../components/uko/TestingsListForUko'
import { AppContext } from '../../context'
import { useNavigate } from 'react-router-dom'
import Loader from "../../components/ui/Loader/Loader"
import Popup from '../../components/ui/Popup/Popup'
import CreateTestingForm from '../../components/shared/CreateTestingForm'
import TestingsFilter from '../../components/shared/TestingsFilter'
import Layout from '../../components/Layout'

const UkoPage = () => {
  const { userFilialId, employeeId } = useContext(AppContext)

  const redirect = useNavigate()

  const [ttSessions, setTtSessions] = useState([])
  const [ttSessionsWithFilter, setTtSessionsWithFilter] = useState([])
  const [modalCreateActive, setModalCreateActive] = useState(false)

  const [testings, setTestings] = useState([])
  const [testingsWithFilter, setTestingsWithFilter] = useState([])

  const [getListTestsToCreate, isSessionLoading] = useFetching(async (filId) => {
    const response = await TestingService.getListTestsToCreate(filId)

    if (response.status == 200) {
      setTtSessions(response.data)
      setTtSessionsWithFilter(response.data)
    }
  })

  const [getTestingsByEmployeeId, isTestingsLoading] = useFetching(async (employeeId) => {
    const response = await TestingService.getTestingsByEmployeeId(employeeId)

    if (response.status == 200) {
      setTestings(response.data)
      setTestingsWithFilter(response.data)
    }
  })

  useEffect(() => {
    getListTestsToCreate(parseInt(userFilialId))
    getTestingsByEmployeeId(employeeId)
  }, [])

  const [loadTest, isParsingLoading] = useFetching(async (file, dataForCreate) => {
    const response = await TestingService.parseFile(file)
    if (response.status == 200) {
      redirect("/create/1", {
        state: {
          questions: response.data,
          dataForCreate,
          successCreateRedirectUrl: `/uko/${employeeId}`
        }
      })
    }
  })

  const onTestingAndSessionFilter = (data) => {
    let newTestingFiltersData = testings
    let newSessionFiltersData = ttSessions

    if (data.facultyId) {
      newTestingFiltersData = newTestingFiltersData.filter(t => t.facultyId == data.facultyId)
      newSessionFiltersData = newSessionFiltersData.filter(t => t.caseSFacultyId == data.facultyId)
    }

    if (data.deptId) {
      newTestingFiltersData = newTestingFiltersData.filter(t => t.departmentId == data.deptId)
      newSessionFiltersData = newSessionFiltersData.filter(t => t.deptId == data.deptId)
    }

    if (data.examDateBefore) {
      const beforeDateTime = new Date(data.examDateBefore)
      newTestingFiltersData = newTestingFiltersData.filter(t => new Date(t.testingDate).setHours(0, 0, 0) >= beforeDateTime)
      newSessionFiltersData = newSessionFiltersData.filter(t => new Date(t.startEvent).setHours(0, 0, 0) >= beforeDateTime)
    }

    if (data.examDateAfter) {
      const afterDateTime = new Date(data.examDateAfter)
      newTestingFiltersData = newTestingFiltersData.filter(t => new Date(t.testingDate).setHours(0, 0, 0) <= afterDateTime)
      newSessionFiltersData = newSessionFiltersData.filter(t => new Date(t.startEvent).setHours(0, 0, 0) <= afterDateTime)
    }

    setTestingsWithFilter(newTestingFiltersData)
    setTtSessionsWithFilter(newSessionFiltersData)
  }

  const onResetFilter = () => {
    setTestingsWithFilter(testings)
    setTtSessionsWithFilter(ttSessions)
  }

  const handleSubmitCreate = (data) => {
    data.filialId = parseInt(userFilialId)
    // data.startEvent = new Date(new Date(data.startEvent).setHours(new Date(data.startEvent).getHours() + 3))

    const file = data.questionsBankFile
    const formData = new FormData()
    formData.append("formFile", file)
    delete data["questionsBankFile"]

    let dataForCreate;
    if (data.group.length > 1) {
      dataForCreate = []
      for (let groupItem of data.group) {
        dataForCreate.push({
          ...data,
          group: groupItem
        })
      }
    } else {
      dataForCreate = {
        ...data,
        group: data.group[0]
      }
    }

    loadTest(formData, dataForCreate)
  }

  return (
    <Layout>
      <div className="container">
        <div className='testings'>
          <div className="testings__filter">
            <TestingsFilter onSubmit={onTestingAndSessionFilter} onReset={onResetFilter} />
          </div>
          <h1 className='testings__title title'>Тестирования</h1>
          <div className='testings__action'>
            <Button onClick={() => setModalCreateActive(true)}>Создать</Button>
          </div>
          {
            (isTestingsLoading || isSessionLoading)
              ? <Loader text="Идет загрузка тестирований" />
              : <TestingsListForUko
                onRemoveTesting={(testingId) => {
                  getListTestsToCreate(parseInt(userFilialId));
                  setTestings(testings.filter(x => x.testingId != testingId));
                  setTestingsWithFilter(testings.filter(x => x.testingId != testingId))
                }}
                ttSessions={ttSessionsWithFilter.sort((a, b) => new Date(b.startEvent) - new Date(a.startEvent))} testings={testingsWithFilter} />
          }
        </div>
      </div>
      <Popup isOpen={modalCreateActive} onClose={() => setModalCreateActive(false)}>
        <h2 className="popup__title title">Создание тестирования</h2>
        <CreateTestingForm isLoading={isParsingLoading} onSubmit={handleSubmitCreate} />
      </Popup>
    </Layout>
  )
}

export default UkoPage