import Select from "../ui/Select/Select"
import Checkbox from "../ui/Checkbox/Checkbox"
import Input from "../ui/Input/Input"
import Button from "../ui/Button/Button"
import Loader from "../ui/Loader/Loader"
import { useForm, Controller } from "react-hook-form"
import { useFetching } from "../../hooks/useFetching"
import AnswerBlankService from "../../api/AnswerBlankService"
import { useContext, useEffect, useState } from "react"
import { AdminContext } from "../../context"


const UnblockTestingForm = ({ onSubmit, isLoading }) => {
    const { selectedCurrentTestingId } = useContext(AdminContext)

    const { handleSubmit, control } = useForm({
        mode: "onSubmit"
    })

    const [answerBlanks, setAnswerBlanks] = useState([])
    const [getAnswerBlanks, isAnswerBlanksLoading] = useFetching(async (tesingId) => {
        const response = await AnswerBlankService.getListAnswerBlanksWithStudentByTestingIdForChecking(tesingId)
        if (response.status == 200) {
            const data = response.data
                .filter(x => x.answerBlank)
                .map(dataItem => ({
                    value: dataItem.answerBlank.id,
                    label: `${dataItem.lastname} ${dataItem.firstname} ${dataItem.patr}`
                }))
            setAnswerBlanks(data)
        }
    })

    useEffect(() => {
        getAnswerBlanks(selectedCurrentTestingId)
    }, [])

    return (
        <form onSubmit={handleSubmit(onSubmit)} className="form">
            <label className='form__label'>
                <span className='form__text'>Студент</span>
                <Controller
                    control={control}
                    name='answerBlankId'
                    rules={{
                        required: true
                    }}
                    render={({ field: { onChange }, fieldState: { error } }) => (
                        <Select
                            onChange={(newValue) => onChange(newValue?.value)}
                            placeholder='Выберите студента'
                            options={answerBlanks}
                            isLoading={isAnswerBlanksLoading}
                            isDisabled={isAnswerBlanksLoading}
                            isErrorStatus={!!error}
                        />
                    )}
                />
            </label>
            <label className='form__label'>
                <span className='form__text'>Дополнительное время в минутах</span>
                <Controller
                    control={control}
                    name='additionalTimeInMinutes'
                    render={({ field: { onChange }, fieldState: { error } }) => (
                        <Input
                            isSmall={true}
                            type="number"
                            onWheel={() => document.activeElement.blur()}
                            onInput={e => {
                                if (e.target.value.length > 3) { e.target.value = e.target.value.slice(0, 3) }
                            }}
                            className="form__input"
                            onChange={onChange}
                            isErrorStatus={!!error}
                        />
                    )}
                />
            </label>
            <label className='form__label form__label_checkbox'>
                <Controller
                    control={control}
                    name='isRemoveAnswerBlank'
                    render={({ field: { onChange } }) => (
                        <Checkbox
                            id="isRemoveAnswerBlank"
                            onChange={(evt) => onChange(evt.target.checked)}
                            label="Сбросить ответы?"
                        />
                    )}
                />
            </label>
            <Button className="form__btn" disabled={isLoading}>
                {isLoading ? <Loader text="Сброс" /> : "Сбросить"}
            </Button>
        </form>
    )
}

export default UnblockTestingForm