import { useEffect, useState } from "react";
import {
  DndContext,
  DragOverlay,
  defaultDropAnimationSideEffects,
} from "@dnd-kit/core";
import { DraggableItem, DragHandle } from "./DraggableItem/DraggableItem";
import DroppableContainer from "./DroppableContainer/DroppableContainer";
import "./Accordance.css";
import Loader from "../Loader/Loader";
import clsx from "clsx";

const dropAnimationConfig = {
  sideEffects: defaultDropAnimationSideEffects({
    styles: {
      active: {
        opacity: 0,
      },
    },
  }),
};

const initDraggableItems = (answerVariants) => {
  const res = [];

  for (let i = 0; i < answerVariants.length; i++) {
    res.push({
      id: `draggable-${i+1}`,
      answer: answerVariants[i],
    });
  }

  return res;
};

const initDropContainers = (fixedVariants, notFixedVariants) => {
  const res = {};

  for (let i = 0; i < fixedVariants.length; i++) {
    res[`droppable-${i + 1}`] = {
      answerNumber: i + 1,
      item: null,
    };
  }
  res["draggable-container"] = {
    items: initDraggableItems(notFixedVariants),
  };

  return res;
};

const getAccordanceResult = (dropContainerElements) => {
  const res = [];

  for (let containerName in dropContainerElements) {
    if (containerName == "draggable-container") continue;

    if (dropContainerElements[containerName].item) {
      const ansVar = dropContainerElements[containerName].item.answer
      ansVar.numberAnswerVariant = dropContainerElements[containerName].answerNumber
      res.push(ansVar);
    }
  }

  return res.length > 0 ? res : null;
};

const Accordance = ({ answers, showIsTrueAnswers = false, onChange, defaultValues }) => {
  const fixedVariants = answers.filter(x => x.isFixedElement == true).sort((a, b) => a.numberAnswerVariant - b.numberAnswerVariant)
  const notFixedVariants = answers.filter(x => x.isFixedElement == false)
  const [dropContainerElements, setDropContainerElements] = useState(
    initDropContainers(fixedVariants, notFixedVariants)
  );
  const [isInit, setIsInit] = useState(false)
  const [overId, setOverId] = useState(null);
  const [activeId, setActiveId] = useState(null);

  const findSourceContainerByElemId = (elemId) => {
    for (let key in dropContainerElements) {
      if (key == "draggable-container") {
        for (let item of dropContainerElements[key].items) {
          if (item.id == activeId) return key;
        }
      }
      if (dropContainerElements[key].item?.id == elemId) return key;
    }
    return null;
  };

  const findActiveElemHTML = (activeId) => {
    for (let key in dropContainerElements) {
      if (key == "draggable-container") {
        for (let item of dropContainerElements[key].items) {
          if (item.id == activeId) return item.answer.textAnswerVariant;
        }
      } else {
        if (dropContainerElements[key].item?.id == activeId)
          return dropContainerElements[key].item.answer.textAnswerVariant;
      }
    }
  };

  const move = (elem, sourceContainerId, destContainerId) => {
    const newDropContainerElements = dropContainerElements;

    if (
      sourceContainerId == "draggable-container" &&
      destContainerId.startsWith("droppable")
    ) {
      newDropContainerElements[sourceContainerId].items =
        newDropContainerElements[sourceContainerId].items.filter(
          (x) => x.id != elem.id
        );
      newDropContainerElements[destContainerId].item = elem;
    } else if (
      sourceContainerId.startsWith("droppable") &&
      destContainerId.startsWith("droppable")
    ) {
      newDropContainerElements[sourceContainerId].item = null;
      newDropContainerElements[destContainerId].item = elem;
    } else {
      newDropContainerElements[sourceContainerId].item = null;
      newDropContainerElements[destContainerId].items.push(elem);
    }
    setDropContainerElements(newDropContainerElements);
  };

  const isContainerEmpty = (dropContainerId) => {
    return dropContainerElements[dropContainerId].item == null;
  };

  const handleDragStart = (event) => {
    setActiveId(event.active.id);
  };

  const handleDragEnd = (event) => {
    if (event.over) {
      const srcContainerId = findSourceContainerByElemId(event.active.id);
      if (isContainerEmpty(event.over.id) && srcContainerId != event.over.id) {
        move(event.active.data.current, srcContainerId, event.over.id);
        onChange(getAccordanceResult(dropContainerElements));
      }
    }
    setActiveId(null);
    setOverId(null);
  };

  const handleDragOver = (event) => {
    const { over } = event;
    setOverId(over?.id);
  };

  const handleDragCancel = () => {
    setActiveId(null);
    setOverId(null);
  };

  const isTrueAnswer = (droppableContainerKey) => {
    const container = dropContainerElements[`droppable-${droppableContainerKey}`]
    if (container.item) {
      return container.answerNumber == container.item.answer.numberAnswerVariant
    }

    return false
  }

  useEffect(() => {
    if (defaultValues?.length > 0) {
      for (let value of defaultValues) {
        const srcContainerId = "draggable-container"
        const elem = dropContainerElements[srcContainerId].items.find(x => x.answer.id == value.id)

        const droppableContainersKeys = Object.keys(dropContainerElements).filter(x => x != "draggable-container")
        for (let containerKey of droppableContainersKeys) {
          if (dropContainerElements[containerKey].answerNumber == value.numberAnswerVariant) {
            move(elem, srcContainerId, containerKey)
          }
        }
      }
    }
    
    setIsInit(true)
  }, [])

  return (
    !isInit ? <Loader text="Загрзка" /> :
      <div className="accordance">
        <DndContext
          onDragEnd={handleDragEnd}
          onDragStart={handleDragStart}
          onDragOver={handleDragOver}
          onDragCancel={handleDragCancel}
        >
          <DragOverlay dropAnimation={dropAnimationConfig}>
            {activeId && (
              <DraggableItem id={activeId}>
                <DragHandle />
                <div dangerouslySetInnerHTML={{__html: findActiveElemHTML(activeId) }}></div>
              </DraggableItem>
            )}
          </DragOverlay>
          <ul className="accordance__list">
            {fixedVariants.map((varaint, index) => (
              <li key={index} className={clsx("accordance__item", "accordance-item", {
                _success: showIsTrueAnswers && isTrueAnswer(index + 1),
                _failed: showIsTrueAnswers && !isTrueAnswer(index + 1)
              })}>
                <div className="accordance-item__wrapper">
                  <span className="accordance-item__number">{index + 1}.</span>
                  <div className="accordance-item__box">{varaint.textAnswerVariant}</div>
                </div>
                <span className="accordance-item__arrow">→</span>
                <div className="accordance-item__wrapper">
                  <DroppableContainer
                    id={`droppable-${index + 1}`}
                    isOver={overId === `droppable-${index + 1}`}
                    onDragOver={handleDragOver}
                  >
                    {dropContainerElements[`droppable-${index + 1}`].item && (
                      <DraggableItem
                        key={
                          dropContainerElements[`droppable-${index + 1}`].item.id
                        }
                        id={dropContainerElements[`droppable-${index + 1}`].item.id}
                        data={dropContainerElements[`droppable-${index + 1}`].item}
                      >
                        <DragHandle />
                        <div dangerouslySetInnerHTML={{ __html: dropContainerElements[`droppable-${index + 1}`].item.answer.textAnswerVariant }}></div>
                      </DraggableItem>
                    )}
                  </DroppableContainer>
                </div>
                {showIsTrueAnswers && <div className="accordance-item__icon"></div>}
              </li>
            ))}
          </ul>
          <div className="accordance__answers">
            <DroppableContainer
              id="draggable-container"
              isOver={overId === "draggable-container"}
            >
              {dropContainerElements["draggable-container"].items.map((item) => (
                <DraggableItem key={item.id} id={item.id} data={item}>
                  <DragHandle />
                  <div dangerouslySetInnerHTML={{ __html: item.answer.textAnswerVariant }}></div>
                </DraggableItem>
              ))}
            </DroppableContainer>
          </div>
        </DndContext>
      </div>
  );
};

export default Accordance;
