import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useLocation } from 'react-router-dom'
import CreateQuestionsPreview from '../../components/uko/CreateQuestionsPreview'
import QuestionTypeService from "../../api/QuestionTypeService"
import { useFetching } from '../../hooks/useFetching'
import Loader from "../../components/ui/Loader/Loader"
import Layout from '../../components/Layout'


const CreateTestingFirstStep = () => {
    const location = useLocation()
    const {dataForCreate, questions, successCreateRedirectUrl} = location.state
    const [questionsForCreate, setQuestionsForCreate] = useState(questions)

    const [questionTypes, setQuestionTypes] = useState([])
    const [getQuestionTypes, isTypesLoading] = useFetching(async () => {
        const response = await QuestionTypeService.getQuestionTypes()
        if (response.status == 200) {
            setQuestionTypes(response.data)
        }
    })

    useEffect(() => {
        getQuestionTypes()
    }, [])

    return (
        <Layout>
            <div className='create-testing'>
                <div className="container">
                    <Link className='create-testing__back btn btn_secondary' to={-1}>Назад</Link>
                    <h1 className='create-testing__title title'>Шаг 1. Проверьте правильность загружаемого банка вопросов</h1>
                    { 
                        isTypesLoading 
                        ? <Loader text="Идет загрузка банка тестов" />
                        : <CreateQuestionsPreview onChange={setQuestionsForCreate} questions={questionsForCreate} questionTypes={questionTypes} /> 
                    }
                    <Link className="create-testing__next btn" to="/create/2" state={{ dataForCreate, questions: questionsForCreate, questionTypes, successCreateRedirectUrl }}>Перейти на следующий шаг</Link>
                </div>
            </div>
        </Layout>
    )
}

export default CreateTestingFirstStep