import { Switch, Case } from "../SwitchCase"
import RadioGroup from "../ui/RadioGroup/RadioGroup"
import Input from "../ui/Input/Input"
import TextArea from "../ui/TextArea/TextArea"

const AnswerList = ({ answers, onChange }) => {
  const getDefaultVariants = (qVariants, sVariants) => {
    const res = []

    if (sVariants) {
      for (let i = 0; i < sVariants.length; i++) {
        let qVar = { ...qVariants.find(x => x.id == sVariants[i].answerVariantId) }
        qVar.numberAnswerVariant = sVariants[i].numberAnswerVariant ? sVariants[i].numberAnswerVariant : 0
        res.push(qVar)
      }
    }

    return res
  }

  const handleSetScores = (scores, answerId) => {  
    onChange && onChange({
      answerId,
      numberOfPoints: scores
    })
  }

  return (
    <ul className='answers-check__list'>
      {
        answers.map((answerItem, index) => (
            <li key={answerItem.answer.id} className='answers-check__item answers-item'>
              <div className="answers-item__wrapper">
                <span className='answers-item__number'>Вопрос №{index + 1}</span>
                <div className="answers-item__text" dangerouslySetInnerHTML={{ __html: answerItem.answer.question.questionText.replace("align=left", "") }}></div>
              </div>
              <div className="answers-item__wrapper">
                <span className='answers-item__label'>Ответ</span>
                <div className="answers-item__body">
                  <Switch param={answerItem.answer.question.questionType.questionTypeName}>
                    <Case value="№вопрос1">
                      <RadioGroup showIsTrueAnswer={true} isClearable={false} value={getDefaultVariants(answerItem.answer.question.answerVariants, answerItem.answer.studentAnswerVariants)[0]} name={`question-${index + 1}`} variants={answerItem.answer.question.answerVariants} />
                      <span className="answers-item__label">Обоснование</span> 
                      <TextArea heightByContent={true} value={answerItem.answer.studentAnswerJustification} onPaste={(evt) => evt.preventDefault()} className='answer-variants__justification' />
                      <span className="answers-item__label">Ключ</span>  
                      <div dangerouslySetInnerHTML={{ __html: answerItem.answer.question.justification}} className='answer-variants__justification'></div>
                    </Case>
                    <Case value="№вопрос5">
                      <Input value={answerItem.answer.text} onPaste={(evt) => evt.preventDefault()} />
                      <span className="answers-item__label">Обоснование</span> 
                      <TextArea heightByContent={true} value={answerItem.answer.studentAnswerJustification} onPaste={(evt) => evt.preventDefault()} className='answer-variants__justification' />
                      <span className="answers-item__label">Ключ</span>  
                      <div dangerouslySetInnerHTML={{ __html: answerItem.answer.question.justification}} className='answer-variants__justification'></div>
                    </Case>
                    <Case value="№вопроскейс">
                      <TextArea heightByContent={true} value={answerItem.answer.text} onPaste={(evt) => evt.preventDefault()} />
                      <span className="answers-item__label">Ключ</span>  
                      <div dangerouslySetInnerHTML={{ __html: answerItem.answer.question.justification}} className='answer-variants__justification'></div>
                    </Case>
                  </Switch>
                  <div className="answers-item__scores">
                    <div className="answers-item__score-input">
                      Баллы: <Input 
                              min={0} 
                              maxLength={1}
                              isSmall={true} 
                              onInput={e => {
                                if (!/^[0-5]$/.test(e.target.value)) {
                                  e.target.value = ""
                                }
                                const score = e.target.value == "" ? null : Number(e.target.value) * answerItem.evaluationCoefficient * answerItem.maxNumberOfPoint
                                handleSetScores(score, answerItem.answer.id)
                              }} 
                            />
                    </div>
                    {
                      answerItem.answer.numberOfPoints && 
                        <div className="answers-item__score-text">
                          {`по 100-бальной системе выставлено: ${answerItem.answer.numberOfPoints}`}
                        </div> 
                    }  
                  </div>
                </div>
              </div>
            </li>
          )
        )
      }
    </ul>

  )
}

export default AnswerList