import { useContext, useEffect, useState } from 'react'
import AnswerBlanksForChecking from '../../components/teacher/AnswerBlanksForChecking'
import AnswerBlankService from '../../api/AnswerBlankService'
import Button from '../../components/ui/Button/Button'
import Popup from '../../components/ui/Popup/Popup'
import QuestionTypeService from '../../api/QuestionTypeService'
import { Link, useLocation, useParams } from 'react-router-dom'
import { useFetching } from '../../hooks/useFetching'
import { AppContext } from '../../context'
import Loader from '../../components/ui/Loader/Loader'
import { useNavigate } from 'react-router-dom'
import TestingService from '../../api/TestingService'
import Layout from '../../components/Layout'

const TestingForTeacher = () => {
    const { id: testingId } = useParams()
    const location = useLocation()
    const testingInfo = location.state

    const { showToast, employeeId } = useContext(AppContext)

    const [answerBlanks, setAnswerBlanks] = useState([])
    const [modalActive, setModalActive] = useState(false)

    const [getAnswerBlanksForChecking, isBlanksLoading] = useFetching(async (testingId) => {
        const response = await AnswerBlankService.getAnswerBlanksByTestingId(testingId)

        if (response.status == 200) {
            setAnswerBlanks(response.data)
        }
    })

    const [questionTypes, setQuestionTypes] = useState([])
    const [getQuestionTypes, isTypesLoading] = useFetching(async () => {
        const response = await QuestionTypeService.getQuestionTypes()
        if (response.status == 200) {
            setQuestionTypes(response.data)
        }
    })

    useEffect(() => {
        getAnswerBlanksForChecking(testingId)
        getQuestionTypes()
    }, [])

    const redirect = useNavigate()

    const [endTestingForEmployee, isEndLoading] = useFetching(async (testingId) => {
        const response = await TestingService.endTestingForEmployee(testingId)
        if (response.status == 200) {
            showToast("success", "Тестирование успешно завершено")
            redirect(`/testing-results/${testingId}`, {
                state: { course: testingInfo.course, group: testingInfo.group, deptName: testingInfo.deptName, disciplineName: testingInfo.disciplineName }
            })
        }
    })

    return (
        <Layout>
            <section className='testing-teacher'>
                <div className="testing-teacher__container container container--smaller">
                    <div className='testing-teacher__back'>
                        <Link className='btn btn_secondary' to={`/teacher/${employeeId}`}>Назад</Link>
                    </div>
                    <div className="testing-teacher__body">
                        <h1 className='testing-teacher__title title'>{testingInfo.disciplineName}</h1>
                        <div className="testing-teacher__data data">
                            <span className='data__stage'>{`${testingInfo.course} курс ${testingInfo.group} группа`}</span>
                            <span className='data__department'>{testingInfo.deptName}</span>
                        </div>
                        {
                            (isBlanksLoading && isTypesLoading)
                                ? <Loader text="Загрузка студентов" />
                                : <AnswerBlanksForChecking testingInfo={testingInfo} answerBlanks={answerBlanks} questionTypes={questionTypes} />
                        }
                        <Button onClick={() => setModalActive(true)}>Завершить тестирование</Button>
                        <Popup isOpen={modalActive} onClose={() => setModalActive(false)}>
                            <h2 className="popup__title title">Вы действительно хотите завершить тестирование?</h2>
                            <div className="confirm-buttons">
                                <Button disabled={isEndLoading} onClick={() => endTestingForEmployee(testingId)}>
                                    {isEndLoading ? <Loader /> : "Да"}
                                </Button>
                                <Button onClick={() => setModalActive(false)}>Нет</Button>
                            </div>
                        </Popup>
                    </div>
                </div>
            </section>
        </Layout>
    )
}

export default TestingForTeacher