import Layout from '../../components/Layout'
import TestingListForStudent from '../../components/student/TestingListForStudent'
import { useParams } from 'react-router-dom'

const Testings = () => {
  const {id: studentId} = useParams()

  return (
    <Layout>
      <section className='testings'>
          <div className='container'>
            <h1 className='testings__title title'>Тестирования</h1>
            <TestingListForStudent studentId={studentId} />
          </div>
      </section>
    </Layout>
  )
}

export default Testings