import { useParams } from 'react-router-dom'
import { useFetching } from '../../hooks/useFetching'
import TestingService from '../../api/TestingService'
import { useEffect, useState } from 'react'
import TestingListForTeacher from '../../components/teacher/TestingListForTeacher'
import Loader from '../../components/ui/Loader/Loader'
import Layout from '../../components/Layout'

const TestingsForTeacher = () => {
  const { id: employeeId } = useParams()

  const [testings, setTestings] = useState([])
  const [getTestingsByEmployeeId, isTestingsByEmployeeLoading] = useFetching(async (employeeId) => {
    const response = await TestingService.getTestingsByEmployeeId(employeeId)
    
    if (response.status == 200) {
      setTestings(response.data)
    }
  })

  const [getTestingsByPersonId, isTestingsByPersonLoading] = useFetching(async (personId) => {
    const response = await TestingService.getTestingsByPersonId(personId)

    if (response.status == 200) {
      setTestings(response.data)
    }
  }) 

  useEffect(() => {
    employeeId.includes("-") 
      ? getTestingsByEmployeeId(employeeId)
      : getTestingsByPersonId(employeeId)
  }, [])

  return (
    <Layout>
      <section className='testings'>
        <div className='container'>
          <h1 className='testings__title title'>Тестирования</h1>
            {
              (isTestingsByEmployeeLoading || isTestingsByPersonLoading)
                ? <Loader text="Идет загрузка тестирований" /> 
                : <TestingListForTeacher testings={testings} />
            } 
        </div>
      </section>
    </Layout>
  )
}

export default TestingsForTeacher