import { Link, useNavigate } from 'react-router-dom'
import { getTestingFormatDate, getTestingStatusClassByStatus } from '../../utils/testing'
import clsx from 'clsx'
import Button from '../ui/Button/Button'
import { useContext } from 'react'
import { AppContext, UkoContext } from '../../context'
import "../../assets/css/microtip.css"

const TestingsItemForUko = ({ testing }) => {
  const { employeeId } = useContext(AppContext)
  const { setSelectedCurrentTestingId, setModalDeleteActive } = useContext(UkoContext)
  const redirect = useNavigate()

  return (
    <li className={clsx("testings__item", "testings-item", getTestingStatusClassByStatus(testing.testingStatus))}>
      <span className='testings-item__date'>{getTestingFormatDate(testing.testingDate)}</span>
      <div className='testings-item__data data'>
        <span className='data__stage'>{`${testing.course} курс${testing.nGroup ? ` ${testing.nGroup} группа` : ""}`}</span>
        <span className='data__department'>{testing.department}</span>
      </div>
      <div className="testings-item__btns">
        {
          <>
            {
              testing.testingStatus == "Checked"
                ?
                <Link to={`/testing-results/${testing.testingId}`} state={{ course: testing.course, group: testing.nGroup, deptName: testing.department, disciplineName: testing.disciplineName }} className='discipline-btn'>{testing.disciplineName}</Link>
                : <div className='discipline-btn'>{testing.disciplineName}</div>
            }
            <div aria-label="Изменение тестирования" data-microtip-position="top" role="tooltip">
              <Button onClick={() => redirect(`/update/1`, { state: { testing, successCreateRedirectUrl: `/uko/${employeeId}` } })} className='btn_edit' />
            </div>
            <div aria-label="Просмотр данных тестирования" data-microtip-position="top" role="tooltip">
              <Button onClick={() => redirect(`/preview/`, { state: { testingId: testing.testingId, testingInspectors: testing.testingInspectorsForEmployee } })} className='btn_preview' />
            </div>
            <div aria-label="Удаление тестирования" data-microtip-position="top" role="tooltip">
              <Button className="btn_remove" onClick={() => {
                setSelectedCurrentTestingId(testing.testingId);
                setModalDeleteActive(true)
              }} />
            </div>
          </>
        }
      </div>
    </li>
  )
}

export default TestingsItemForUko