import axios from "axios";
import { API_URL, getToken } from "./config";

export default class QuestionService {
    static async getQuestionsByTestingId(testingId) {
        const response = await axios.get(`${API_URL}/Question/GetQuestionsByTestingId`, {
            params: {
                testingId
            },
            headers: {
                "Authorization": `Bearer ${getToken()}`
            }
        })
        return response;
    }
}